/* eslint-disable */
import * as Canvg from 'canvg';
import ffmpeg from 'ffmpeg.js/ffmpeg-mp4.js';
import dataUriToBuffer from "data-uri-to-buffer";

export function svgStringArrayToMP4(svgStringArray, audioBuffer, width, height, framerate) {
  const bufferList = svgStringArray.map(str => svgToPngBuffer(str, width, height));
  return pngListToMP4(bufferList, audioBuffer, width, height, framerate);
}

export function svgToPngBuffer(svgStr, width, height) {
  const canvas = document.createElement("canvas");
  canvas.setAttribute('width', width);
  canvas.setAttribute('height', height);

  const ctx = canvas.getContext('2d');
  const canvg = Canvg.Canvg.fromString(ctx, svgStr);
  canvg.start();

  const pngDataUrl = canvas.toDataURL("img/png");

  canvg.stop();

  return dataUriToBuffer(pngDataUrl);
}


export function pngListToMP4(pngList, audioBuffer, width, height, framerate) {
  const fileSys = pngList.map((png, i) => ({
    name: `image${i}.png`,
    data: new Uint8Array(png),
  }))
  const wavArray = getWavArray(audioBuffer);
  fileSys.push({
    name: 'music.wav',
    data: wavArray,
  })
  let stdout = "";
  let stderr = "";
  let exitCode = -1;
  const result = ffmpeg({
    MEMFS: fileSys,
    arguments: [
      "-i", "music.wav",
      "-ac", "1",
      "-framerate", "" + framerate,
      "-i", "image%d.png",
      "-r", "" + framerate,
      "-c:v", "libx264",
      "-s", `${width}x${height}`,
      "-pix_fmt", "yuv420p",
      "-ar", "16000",
      "output.mp4",
    ],
    print: function(data) { stdout += data + "\n"; },
    printErr: function(data) { stderr += data + "\n"; },
    onExit: function(code) {
      console.log("Video synthesis process exited with code " + code);
      if (code !== 0) {
        console.log(stdout);
        console.error(stderr);
      }
      exitCode = code;
    },
  });
  return {
    result: result.MEMFS[0]?.data ?? null,
    code: exitCode,
    message: stdout,
  }
}

function getWavArray(audioBuffer) {
  let numOfChan = audioBuffer.numberOfChannels,
      length = audioBuffer.length * numOfChan * 2 + 44,
      buffer = new ArrayBuffer(length),
      view = new DataView(buffer),
      channels = [], i, sample,
      offset = 0,
      pos = 0;

  function setUint16(data) {
    view.setUint16(pos, data, true);
    pos += 2;
  }

  function setUint32(data) {
    view.setUint32(pos, data, true);
    pos += 4;
  }

  // write WAVE header
  // "RIFF"
  setUint32(0x46464952);
  // file length - 8
  setUint32(length - 8);
  // "WAVE"
  setUint32(0x45564157);
  // "fmt " chunk
  setUint32(0x20746d66);
  // length = 16
  setUint32(16);
  // PCM (uncompressed)
  setUint16(1);
  setUint16(numOfChan);
  setUint32(audioBuffer.sampleRate);
  // avg. bytes/sec
  setUint32(audioBuffer.sampleRate * 2 * numOfChan);
  // block-align
  setUint16(numOfChan * 2);
  // 16-bit (hardcoded in this demo)
  setUint16(16);
  // "data" - chunk
  setUint32(0x61746164);
  // chunk length
  setUint32(length - pos - 4);

  // write interleaved data
  for (i = 0; i < audioBuffer.numberOfChannels; i++)
    channels.push(audioBuffer.getChannelData(i));

  while (pos < length) {
    // interleave channels
    for (i = 0; i < numOfChan; i++) {
      // clamp
      sample = Math.max(-1, Math.min(1, channels[i][offset]));
      // scale to 16-bit signed int
      sample = (0.5 + sample < 0 ? sample * 32768 : sample * 32767) | 0;
      // write 16-bit sample
      view.setInt16(pos, sample, true);
      pos += 2;
    }
    // next source sample
    offset++
  }

  return buffer;
}

export function download(buff, filename) {
  let url = window.URL.createObjectURL(new Blob( [buff], {type: "arraybuffer"}) )
  const link = document.createElement('a');
  link.style.display = 'none';
  link.href = url;
  link.setAttribute('download', filename);
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
}

