import {BaseTemplate} from "@/core/ast/template/BaseTemplate"
import {z} from "zod"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {LeafAniProps} from "@/core/ast/animation/logical/LeafAnimation"
import {AniMetaSchema} from "@/core/ast/animation/logical/LogicalAnimation"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"

export class EffectTemplate<PropType extends typeof LeafAniProps>
  extends BaseTemplate<PropType, EffectAnimation<PropType>> {

  protected constructor(meta: AniMetaSchema<PropType>) {
    super(meta)
  }

  create(cxt: ResolveContext<unknown, unknown>, props: z.output<PropType>): EffectAnimation<PropType> {
    const {name, packageName, targetType, propSchema} = this.meta
    return new EffectAnimation(name, packageName, targetType, propSchema, props)
  }
}
