export function getFormattedXmlString(element: Element): string {
  return getFormattedXmlStringHelper(element, 0)
}

function getFormattedXmlStringHelper(element: Element, indent = 0): string {
  const indentation = '  '.repeat(indent)
  const attributes = Array.from(element.attributes)
      .map(attr => `${attr.name}="${attr.value}"`)
      .join(' ')
  const openTag = `<${element.tagName.toLowerCase()}${(attributes === '') ? '' : ' '}${attributes}>`
  const closeTag = `</${element.tagName.toLowerCase()}>`

  const innerContent = Array.from(element.childNodes)
      .map(node => {
        if (node instanceof Element) {
          return getFormattedXmlStringHelper(node, indent + 1)
        } else {
          return `${'  '.repeat(indent + 1)}${node.textContent}`
        }
      })
      .join('\n')
  if (innerContent === '') {
    return `${indentation}${openTag}${closeTag}`
  }
  return `${indentation}${openTag}\n${innerContent}\n${indentation}${closeTag}`
}

export function domElToStr(el: Element): string {
  return `${el.tagName}#${el.id}`.trim()
}
