import {TargetType, TargetTypeComponent} from "@/core/ast/target/TargetType"
import {TreeLike} from "@/core/utils/TreeUtils"

export class TypeModelTreeNode implements TreeLike {
  readonly nodeId: string
  readonly targetType: TargetType
  readonly parent: TypeModelTreeNode
  readonly componentLink: TargetTypeComponent = null

  children: TypeModelTreeNode[] = []

  private _classList: string[] = null

  constructor(nodeId: string,
              targetType: TargetType,
              parent: TypeModelTreeNode = null,
              componentLink: TargetTypeComponent = null) {
    this.nodeId = nodeId
    this.targetType = targetType
    this.parent = parent
    this.componentLink = componentLink
  }

  visitChildren(): TreeLike[] {
    return this.children
  }

  toOneLineString(): string {
    return `${this.targetType.name} [(${this.nodeId}) class=[${this.classList.join(', ')}]`
  }

  isLeaf(): boolean {
    return this.targetType.isLeaf()
  }

  /**
   * Gets all class of this node. Class list include:
   * - TargetType classList (given when defining TargetType, empty for most of the time)
   * - TargetTypeComponent classList (given by its parent)
   */
  get classList(): string[] {
    if (this._classList === null) {
      this._classList = [this.targetType.name, ...this.targetType.classList]
      if (this.componentLink != null) {
        this._classList = this. _classList.concat(this.componentLink.classList)
      }
      this._classList.reverse()
    }
    return this._classList
  }

  get fullClassList(): string[] {
    return this.classList.concat(this.parent?.fullClassList ?? [])
  }
}
