import {
  AniMetaSchema,
  AniPropSchema,
  BaseAniPropSchema,
  LogicalAnimation
} from "@/core/ast/animation/logical/LogicalAnimation"
import {z} from "zod"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {CompositeTemplate} from "@/core/ast/template/CompositeTemplate"
import {AniUnitSpec} from "@/parser/json/animation/AniUnitSpec"
import {ParseContext} from "@/parser/json/ParseContext"
import {LoggerManager} from "@/core/utils/Logger"

export class JsonCompositeTemplate<PropType extends typeof AniPropSchema, LogicalAniType extends LogicalAnimation<BaseAniPropSchema>>
  extends CompositeTemplate<PropType, LogicalAniType> {

  private readonly spec: z.input<typeof AniUnitSpec>

  constructor(meta: AniMetaSchema<PropType>, spec: z.input<typeof AniUnitSpec>) {
    super(meta)
    this.spec = spec
  }

  instantiate<ObjType, AniType>(cxt: ResolveContext<ObjType, AniType>, props: z.output<PropType>): LogicalAniType {
    const parseContext = new ParseContext(props)
    LoggerManager.getLogger('JsonCompositeTemplate').debug(`Instantiate ${this.fullName} with props ${JSON.stringify(props)}`)
    const parser = cxt.compileExec.session.parser
    const animation = parser.parseAnimation(this.spec, parseContext) as unknown as LogicalAniType
    // merge outer target
    animation.target.concatAtFront(props.target)
    // merge timing
    animation.timing.rewriteBy(props.timing)
    return animation
  }
}
