import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {SyncAnimation} from "@/core/ast/animation/logical/impl/SyncAnimation"

export class SyncImpl extends GsapPhysicalAnimation<any, SyncAnimation> {
  doGenerate(cxt: SynthesisContext<any, GsapAnimation>): GsapAnimation {
    const offsets = this.logical.offsets
    const children = this.children

    const tl = gsap.timeline()
    children.forEach((child, i) => {
      const subTl = child.generate(cxt)
      if (subTl === null) {
        return
      }
      tl.add(subTl, offsets[i])
    })

    Object.assign(tl.vars, this.mergeVars())

    return tl
  }
}

