import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const ChangeColorPropSchema = EffectPropSchema.extend({
  type: z.enum(['to', 'from'])
      .default('to')
      .describe("Type of animation: to, from (default: to)"),
  fill: z.string()
      .default('#ff0000')
      .describe("Color to fill (default: #ff0000)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class ChangeColorTemplate extends EffectTemplate<typeof ChangeColorPropSchema> {
  constructor() {
    super({
      name: "ChangeColor",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: ChangeColorPropSchema,
    })
  }
}
