import {BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {Logger, LoggerManager} from "@/core/utils/Logger"
import {CompileExec} from "@/session/CompileExec"
import {PhysicalAnimation} from "@/core/ast/animation/physical/PhysicalAnimation"
import {SynthesisRule} from "@/core/exec/synthesis/SynthesisRule"
import {toTreeString} from "@/core/utils/TreeUtils"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"


export abstract class Synthesizer<ObjType, AniType> {
  private static readonly logger: Logger = LoggerManager.getLogger('Synthesizer')

  readonly compileExec: CompileExec<ObjType, AniType>
  readonly extraRules: SynthesisRule<ObjType, BaseLogicalAniType, AniType>[]

  protected constructor(compileExec: CompileExec<ObjType, AniType>,
                        extraRules: SynthesisRule<ObjType, BaseLogicalAniType, AniType>[] = []) {
    this.compileExec = compileExec
    this.extraRules = extraRules
  }

  getDefaultRules(): SynthesisRule<ObjType, BaseLogicalAniType, AniType>[] {
    return []
  }

  getRuleMap(): Map<string, SynthesisRule<ObjType, BaseLogicalAniType, AniType>> {
    const rules = this.getDefaultRules().concat(this.extraRules)
    const map = new Map()
    for (const rule of rules) {
      if (map.has(rule.targetName)) {
        throw new Error(`Rule target name conflict: ${rule.targetName}`)
      }
      map.set(rule.targetName, rule)
    }
    return map
  }

  plan(input: BaseLogicalAniType): PhysicalAnimation<ObjType, BaseLogicalAniType, AniType> {
    Synthesizer.logger.info('Start planning. Input logical animation:\n' + toTreeString(input))
    const ruleMap = this.getRuleMap()

    const cxt = new SynthesisContext(this.compileExec)

    const physicalAnimation = this.transformUp(input, (node) => {
      const rule = ruleMap.get(node.fullName)
      if (rule === undefined) {
        throw new Error(`Failed to find implementation of animation class ${node.fullName}. Reason: No rule found.`)
      }
      const newNode = rule.apply(cxt, node)
      Synthesizer.logger.debug(`Synthesized ${node.fullName} to ${newNode.constructor.name}`)
      return newNode
    })
    Synthesizer.logger.info('End planning. Final physical animation:\n' + toTreeString(physicalAnimation))

    return physicalAnimation
  }

  generate(physical: PhysicalAnimation<ObjType, BaseLogicalAniType, AniType>): AniType {
    const cxt = new SynthesisContext(this.compileExec)
    return physical.generate(cxt)
  }

  transformUp(root: BaseLogicalAniType, func: ((node: BaseLogicalAniType) => PhysicalAnimation<ObjType, BaseLogicalAniType, AniType>)): PhysicalAnimation<ObjType, BaseLogicalAniType, AniType> {
    return this.transformUpHelper(root, func)
  }

  transformUpHelper(node: BaseLogicalAniType, func: ((node: BaseLogicalAniType) => PhysicalAnimation<ObjType, BaseLogicalAniType, AniType>)): PhysicalAnimation<ObjType, BaseLogicalAniType, AniType> {
    const physicalAnimation = func(node)
    physicalAnimation.children = []
    for (const child of node.visitChildren()) {
      const newChild = this.transformUpHelper(child, func)
      physicalAnimation.children.push(newChild)
    }
    return physicalAnimation
  }
}
