import {TargetSelection} from "@/core/ast/target/TargetSelection"

export type TransformType = 'filter' | 'select' | 'selectAll' | 'filterExpr' | 'sort' | 'slice' | 'at' | 'group'

export abstract class Transform {
  abstract get type(): TransformType
  abstract execute(selection: TargetSelection): TargetSelection
  abstract clone(): Transform
}
