import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const SetPropSchema = EffectPropSchema.extend({
  attr: z.string()
      .describe("Attribute to set"),
  value: z.union([z.string(), z.number(), z.boolean()])
      .describe("Value of the attribute")
})

export class SetTemplate extends EffectTemplate<typeof SetPropSchema> {
  constructor() {
    super({
      name: "Set",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: SetPropSchema,
    })
  }
}
