export function getPosition(svg: SVGSVGElement, element: SVGGraphicsElement) {
  const rootPoint = svg.createSVGPoint()
  const ctm = element.getCTM()
  const elPoint = rootPoint.matrixTransform(ctm)
  return {x: elPoint.x, y: elPoint.y}
}

export function getSvgBox(svg: SVGSVGElement, element: SVGGraphicsElement) {
  const {x: posX, y: posY} = getPosition(svg, element)
  const {x: boxX, y: boxY, width, height} = element.getBBox()

  const x = posX + boxX
  const y = posY + boxY

  // we don't need to consider text-anchor because getBBox already considers it
  // if (element.nodeName === 'text') {
  //   switch (element.getAttribute("text-anchor")) {
  //     case "start":
  //       // start at left bottom corner
  //       y = y - height * 0.75
  //       break
  //     case "end":
  //       // start at right bottom corner
  //       x = x - width
  //       y = y - height * 0.75
  //       break
  //     case "middle":
  //       // start at middle bottom
  //       x = x - width / 2
  //       y = y - height * 0.75
  //       break
  //     default:
  //       break
  //   }
  // }

  return { x, y, width, height, }
}

export function createOrGetG(svg: SVGSVGElement, el: SVGGraphicsElement, id: string, classList: string[] = []) {
  if (svg.querySelector(`#${id}`)) {
    const g = svg.querySelector(`#${id}`) as SVGGElement
    classList.forEach(clazz => {
      if (!g.classList.contains(clazz)) {
        g.classList.add(clazz)
      }
    })
    return g
  }
  return createG(el, id, classList)
}

export function createG(el: SVGGraphicsElement, id: string, classList: string[] = []) {
  // create <g> element out of the given element
  const g = document.createElementNS('http://www.w3.org/2000/svg', 'g')
  g.setAttribute('id', id)
  g.setAttribute('class', classList.join(' '))
  el.parentNode.insertBefore(g, el)
  g.appendChild(el)
  return g
}

export function getRelativeBox(el: SVGGraphicsElement) {
  const {height, width} = el.getBBox()
  const strokeWidth = getStrokeWidth(el)
  return {
    height,
    width,
    strokeWidth,
    totalHeight: height + strokeWidth * 2,
    totalWidth: width + strokeWidth * 2,
    left: -strokeWidth,
    top: -strokeWidth,
    right: width + strokeWidth,
    bottom: height + strokeWidth,
  }
}

function getStrokeWidth(el: SVGGraphicsElement): number {
  let strokeWidth = parseInt(el.getAttribute('stroke-width') ?? '0')
  // recursively get max stroke-width from children
  if (el.children.length) {
    Array.from(el.children).forEach((child) => {
      const childStrokeWidth = getStrokeWidth(child as SVGGraphicsElement)
      if (childStrokeWidth > strokeWidth) {
        strokeWidth = childStrokeWidth
      }
    })
  }
  return strokeWidth
}

/* cite: https://greensock.com/forums/topic/17566-create-a-radial-wipe/?tab=comments#comment-79421 */
export function getArcPath(cx: number, cy: number, rx: number, ry: number, startAngle: number, sweepAngle: number) {
  // 0 degrees, no path
  if (!sweepAngle) return `M${cx} ${cy}`

  // mod angles to 360 degrees
  startAngle %= 360
  sweepAngle %= 360

  // If arc is 360 degrees, the sweep angle is 0 due to mod
  const isClosed = sweepAngle === 0

  // If closed, we'll need to use 2 arcs with an angle of 180 degrees
  if (isClosed) {
    sweepAngle = 180
  }

  // Make angle positive to simplify large arc flag
  if (sweepAngle < 0) {
    sweepAngle += 360
  }

  const largeArc = sweepAngle > 180 ? 1 : 0

  // Convert degrees to radians
  const a1 = (startAngle * Math.PI) / 180
  const a2 = ((startAngle + sweepAngle) * Math.PI) / 180

  // Calculate start and end coords for arc. Starts at 12 o'clock
  const x1 = cx + rx * Math.sin(a1)
  const y1 = cy - ry * Math.cos(a1)

  const x2 = cx + rx * Math.sin(a2)
  const y2 = cy - ry * Math.cos(a2)

  /*
  NOTE: To orient the starting point to 3 o'clock instead of 12 o'clock,
  add 90 degrees to the start angle, or change the code above to this...

  var x1 = cx + rx * Math.cos(a1);
  var y1 = cy + ry * Math.sin(a1);

  var x2 = cx + rx * Math.cos(a2);
  var y2 = cy + ry * Math.sin(a2);
  */

  // Related demo using arc path
  // https://codepen.io/osublake/pen/VbWKMj/
  if (isClosed) {
    return `
      M ${x1} ${y1}
      A ${rx} ${ry} 0 1 1 ${x2} ${y2}
      A ${rx} ${ry} 0 1 1 ${x1} ${y1}z`
  }

  return `
    M ${x1} ${y1}
    A ${rx} ${ry} 0 ${largeArc} 1 ${x2} ${y2}
    L ${cx} ${cy}z`
}
