import {Transform, TransformType} from "@/core/ast/target/Transform"
import {TargetSelection} from "@/core/ast/target/TargetSelection"

export class SliceTransform extends Transform {
  readonly start: number
  readonly end: number | null
  readonly step: number | null

  constructor(start: number, end: number | null = null, step: number | null = null) {
    super()
    this.start = start
    this.end = end
    this.step = step
  }

  get type(): TransformType {
    return "slice"
  }

  execute(selection: TargetSelection): TargetSelection {
    return selection.slice(this.start, this.end, this.step)
  }

  toString() {
    return `slice(${this.start}, ${this.end}, ${this.step})`
  }

  clone(): SliceTransform {
    return new SliceTransform(this.start, this.end, this.step)
  }
}
