import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {getArcPath, getRelativeBox} from "@/core/utils/SvgUtils"
import {SectorWheelPropSchema} from "@/preset/template/pie-chart/SectorWheelTemplate"
import {BaseSVGTargetModel} from "@/core/ast/target/impl/svg/BaseSVGTargetModel"


export class SectorWheelImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof SectorWheelPropSchema>> {
  private x: number
  private y: number
  private width: number
  private height: number

  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    const model = cxt.targetModel as BaseSVGTargetModel

    if (this.props().centerPos != '') {
      this.computeSizeWithPos(model)
    } else {
      this.computeSizeWithTarget(model)
    }

    const tl = gsap.timeline()
    const stagger: number = this.stagger
    this.elements.forEach((el, i) => {
      const tw = this.createTw(el, model)
      const position = i === 0 ? 0 : ('<' + stagger)
      tl.add(tw, position)
    })

    return tl
  }

  computeSizeWithTarget(model: BaseSVGTargetModel) {
    let centerElements
    if (this.props().centerTarget != '') {
      const filter = this.props().centerTarget
      const centerSel = model.rootSelection.filter(filter)
      centerElements = centerSel.realElements
    } else {
      centerElements = this.elements
    }

    let x = Infinity, y = Infinity, width = 0, height = 0

    centerElements.forEach(el => {
      const box = model.getElBox(el as SVGGraphicsElement)
      x = Math.min(x, box.x)
      y = Math.min(y ,box.y)
    })
    centerElements.forEach(el => {
      const box = model.getElBox(el as SVGGraphicsElement)
      const {totalWidth, totalHeight} = getRelativeBox(el as SVGGraphicsElement)
      width = Math.max(width, box.x + totalWidth - x)
      height = Math.max(height, box.y + totalHeight - y)
    })

    console.log('computeSizeWithTarget', x, y, width, height, centerElements)

    this.x = x
    this.y = y
    this.width = width
    this.height = height
  }

  computeSizeWithPos(model: BaseSVGTargetModel) {
    const svg = model.getOrigin()
    // hardcode
    // inner SVG
    const innerSvg = svg.querySelector('svg')
    const {width, height} = innerSvg.viewBox.baseVal
    const {centerPos} = this.props()    // e.g., '50% 50%', or '100 100'
    const [x, y] = centerPos.split(' ')
        .map((v, i) => {
          if (v.endsWith('%')) {
            return parseFloat(v) / 100 * (i === 0 ? width : height)
          } else {
            return parseFloat(v)
          }
        })
    console.log('computeSizeWithPos', x, y, width, height)
    this.x = x
    this.y = y
    this.width = width
    this.height = height
  }

  createTw(el: SVGElement, model: BaseSVGTargetModel): GsapAnimation {
    const {type, startAngle, sweepAngle, ease} = this.props()

    const {x, y} = model.getElBox(el as SVGGraphicsElement)
    // const svg = model.getOrigin()
    // const gClipPath = createOrGetG(svg, el as SVGGraphicsElement, `clip-path-${el.id}`, ['sector-wheel'])

    const targetX = this.x + this.width / 2
    const targetY = this.y + this.height / 2

    const arc = {
      startAngle,
      sweepAngle: 0,
      cx: targetX - x,
      cy: targetY - y,
      rx: this.width,
      ry: this.height,
    }
    const hiddenSweepAngle = 0
    const shownSweepAngle = sweepAngle

    const updatePath = function updatePath() {
      const path = getArcPath(
          arc.cx,
          arc.cy,
          arc.rx,
          arc.ry,
          arc.startAngle,
          arc.sweepAngle
      ).replaceAll('\n', ' ')
      el.setAttribute('clip-path', `path('${path}')`)
    }

    const vars = this.mergeVars({ease, onUpdate: updatePath})

    let tw: GsapAnimation

    switch (type) {
      case 'in':
        tw = gsap.fromTo(arc,
            { sweepAngle: hiddenSweepAngle },
            { sweepAngle: shownSweepAngle, ...vars })
        break
      case 'out':
        tw = gsap.fromTo(arc,
            { sweepAngle: shownSweepAngle },
            { sweepAngle: hiddenSweepAngle, ...vars })
        break
      default:
        // never goes here as type is checked by zod
        throw new Error(`Unknown type: ${type}`)
    }

    return tw
  }
}

