import assert from "assert"

export class Binder {
  readonly from: string
  readonly to: string
  readonly rules: BinderRule[]

  private constructor(from: string, to: string, rules: BinderRule[]) {
    this.from = from
    this.to = to
    this.rules = rules
  }

  static build(from: string, to: string, rules: BinderRule[] = []) {
    return new Binder(from, to, rules)
  }
}

export class BinderRule {
  readonly type: 'single' | 'batch'

  readonly from: string
  readonly to: string

  readonly id: string | null  // only for single
  readonly classList: string[]
  // data and dataList cannot be both non-null
  readonly data: Record<string, any> | null
  readonly dataList: Record<string, any>[] | null   // only for batch

  private constructor(type: 'single' | 'batch',
                      from: string, to: string,
                      id: string, classList: string[],
                      data: Record<string, any> | null, dataList: Record<string, any>[] | null) {
    this.type = type

    this.from = from
    this.to = to

    this.id = id
    this.classList = classList
    this.data = data
    this.dataList = dataList
  }

  static buildSingleBind({from, to, id, classList, data}:
                             { from: string, to: string, id?: string, classList?: string[], data?: Record<string, any> }) {
    return new BinderRule('single', from, to, id ?? null, classList ?? [], data ?? null, null)
  }

  static buildBatchBind({from, to, classList, data, dataList}:
                         { from: string, to: string, classList?: string[], data?: Record<string, any>, dataList?: Record<string, any>[] }) {
    assert(data == null || dataList == null, `Batch builder rule cannot have both data and dataList.`)
    return new BinderRule('batch', from, to, null, classList ?? [], data ?? null, dataList ?? null)
  }
}
