import {TargetType} from "@/core/ast/target/TargetType"
import {GaiaSession} from "@/session/GaiaSession"

import Any from "@/preset/target/common/Any.type.json5"
import Circle from "@/preset/target/common/Circle.type.json5"
import Image from "@/preset/target/common/Image.type.json5"
import Line from "@/preset/target/common/Line.type.json5"
import Path from "@/preset/target/common/Path.type.json5"
import Rect from "@/preset/target/common/Rect.type.json5"
import Symbol from "@/preset/target/common/Symbol.type.json5"
import Text from "@/preset/target/common/Text.type.json5"

import Background from "@/preset/target/chart/Background.type.json5"
import Axis from "@/preset/target/chart/Axis.type.json5"
import Annotation from "@/preset/target/chart/Annotation.type.json5"
import Legend from "@/preset/target/chart/Legend.type.json5"
import Grid from "@/preset/target/chart/Grid.type.json5"

import BarChart from "@/preset/target/chart/bar-chart/BarChart.type.json5"
import BarMark from "@/preset/target/chart/bar-chart/BarMark.type.json5"

import PieChart from "@/preset/target/chart/pie-chart/PieChart.type.json5"
import PieMark from "@/preset/target/chart/pie-chart/PieMark.type.json5"

import LineChart from "@/preset/target/chart/line-chart/LineChart.type.json5"
import LineMark from "@/preset/target/chart/line-chart/LineMark.type.json5"

import Map from "@/preset/target/chart/map/Map.type.json5"

import TornadoChart from "@/preset/target/chart/tornado/TornadoChart.type.json5"
import TornadoMark from "@/preset/target/chart/tornado/TornadoMark.type.json5"

import IsotypeChart from "@/preset/target/chart/isotype/IsotypeChart.type.json5"
import IsotypeMark from "@/preset/target/chart/isotype/IsotypeMark.type.json5"

import ScatterPlot from "@/preset/target/chart/scatter-plot/ScatterPlot.type.json5"
import ScatterPlotMark from "@/preset/target/chart/scatter-plot/ScatterPlotMark.type.json5"

import MixedChart from "@/preset/target/chart/mixed-chart/MixedChart.type.json5"

import TlBackground from "@/preset/target/timeline/TlBackground.type.json5"
import LinearTimeline from "@/preset/target/timeline/linear/LinearTimeline.type.json5"
import LinearTimelineUnit from "@/preset/target/timeline/linear/LinearTimelineUnit.type.json5"


export function getPresetTypes(session: GaiaSession): TargetType[] {
  const parser = session.parser
  return [
    /* Common */
    parser.parseTargetType(Any),
    parser.parseTargetType(Circle),
    parser.parseTargetType(Image),
    parser.parseTargetType(Line),
    parser.parseTargetType(Path),
    parser.parseTargetType(Rect),
    parser.parseTargetType(Symbol),
    parser.parseTargetType(Text),
    parser.parseTargetType(Background),

    /* Chart */
    parser.parseTargetType(Axis),
    parser.parseTargetType(Annotation),
    parser.parseTargetType(Legend),
    parser.parseTargetType(Grid),

    /* Bar Chart */
    parser.parseTargetType(BarChart),
    parser.parseTargetType(BarMark),

    /* Pie Chart */
    parser.parseTargetType(PieChart),
    parser.parseTargetType(PieMark),

    /* Line Chart */
    parser.parseTargetType(LineChart),
    parser.parseTargetType(LineMark),

    /* Map */
    parser.parseTargetType(Map),

    /* Tornado Chart */
    parser.parseTargetType(TornadoChart),
    parser.parseTargetType(TornadoMark),

    /* Isotype */
    parser.parseTargetType(IsotypeChart),
    parser.parseTargetType(IsotypeMark),

    /* Scatter Plot */
    parser.parseTargetType(ScatterPlot),
    parser.parseTargetType(ScatterPlotMark),

    /* Mixed Chart */
    parser.parseTargetType(MixedChart),

    /* Linear Timeline */
    parser.parseTargetType(TlBackground),
    parser.parseTargetType(LinearTimeline),
    parser.parseTargetType(LinearTimelineUnit),
  ]
}
