<template>
  <div class="test-view">
    <svg/>
  </div>
</template>

<script>
  import * as d3 from 'd3';
  import d3Lasso from '@/utils/lasso/lasso';

  export default {
    name: 'TestView',
    components: {
    },
    data() {
      return {
      }
    },
    methods: {
      initLasso() {
        let data = new Array(100).fill(null).map(()=>[Math.random(),Math.random()]);
        let w = 960;
        let h = 500;
        let r = 3.5;

        let svg = d3.select(this.$el).select('svg')
            .attr("width",w)
            .attr("height",h);

        let circles = svg.selectAll("circle")
            .data(data)
            .enter()
            .append("circle")
            .attr("cx",d=>d[0]*w)
            .attr("cy",d=>d[1]*h)
            .attr("r",r);

        let lasso = d3Lasso()
            .closePathSelect(true)
            .closePathDistance(100)
            .items(circles)
            .targetArea(svg);

        // Lasso functions
        let lassoStart = function() {
          lasso.items()
              .attr("r",3.5) // reset size
              .classed("not_possible",true)
              .classed("selected",false);
        };

        let lassoDraw = function() {

          // Style the possible dots
          lasso.possibleItems()
              .classed("not_possible",false)
              .classed("possible",true);

          // Style the not possible dot
          lasso.notPossibleItems()
              .classed("not_possible",true)
              .classed("possible",false);
        };

        let lassoEnd = function() {
          // Reset the color of all dots
          lasso.items()
              .classed("not_possible",false)
              .classed("possible",false);

          // Style the selected dots
          lasso.selectedItems()
              .classed("selected",true)
              .attr("r",7);

          // Reset the style of the not selected dots
          lasso.notSelectedItems()
              .attr("r",3.5);
        };

        lasso.on("start", lassoStart)
            .on("draw", lassoDraw)
            .on("end", lassoEnd);

        svg.call(lasso);
      },
    },
    mounted() {
      this.initLasso();
    },
    computed: {
    }
  }
</script>

<style scoped>
.test-view >>> circle {
  fill-opacity: 0.5;
}

.test-view >>> .lasso path {
  stroke: rgb(80,80,80);
  stroke-width:2px;
}

.test-view >>> .lasso .drawn {
  fill-opacity:.05 ;
}

.test-view >>> .lasso .loop_close {
  fill:none;
  stroke-dasharray: 4,4;
}

.test-view >>> .lasso .origin {
  fill:#3399FF;
  fill-opacity:.5;
}

.test-view >>> .not_possible {
  fill: rgb(200,200,200);
}

.test-view >>> .possible {
  fill: #EC888C;
}

.test-view >>> .selected {
  fill: steelblue;
}
</style>
