<script>
  import { mapState } from 'vuex';
  import JSON5 from 'json5';
  import { codemirror } from 'vue-codemirror';
  import {SelectionToolType} from "@/utils/common/SeletionToolType";
  import 'codemirror/lib/codemirror.css'
  import 'codemirror/theme/idea.css'
  import 'codemirror/mode/javascript/javascript'
  import 'codemirror/mode/xml/xml.js'
  import 'codemirror/addon/lint/lint'
  import 'codemirror/addon/lint/json-lint'

  import'codemirror/addon/selection/active-line.js'
  // autoCloseTags
  import'codemirror/addon/edit/closetag.js'

  import "codemirror/addon/fold/foldcode.js";
  import "codemirror/addon/fold/foldgutter.js";
  import "codemirror/addon/fold/foldgutter.css";
  import "codemirror/addon/fold/brace-fold.js";
  import 'codemirror/addon/fold/xml-fold.js'

  import 'codemirror/addon/edit/matchbrackets.js'
  import {getFormattedXmlString, getFormattedXmlStringHelper} from "@/core/utils/DomUtils"

  export default {
    name: 'DebugPanel',
    components: {
      codemirror,
    },
    props: ['paneHeight', 'paneWidth'],
    data() {
      return {
        activePaneName: 'animation',
        // activePaneName: 'specification',
        templateContentDialogVisible: true,
        cmOptions: {
          tabSize: 2,
          mode: 'application/json',
          theme: 'idea',
          lineNumbers: true,
          lineWrapping:true,
          matchBrackets:true,

          gutters: [
            "CodeMirror-lint-markers",
            "CodeMirror-linenumbers",
            "CodeMirror-foldgutter"
          ],
          foldGutter: true,

          line: true,
        },
        codeUpdateTimeoutId: null,

        cmHTMLOptions: {
          tabSize: 2,
          mode: 'text/html',
          htmlMode: true,
          theme: 'idea',
          lineNumbers: true,
          // lineWrapping:true,
          matchBrackets:true,
          readOnly: true,

          styleActiveLine: true,
          autoCloseTags: true,

          gutters: [
            "CodeMirror-lint-markers",
            "CodeMirror-linenumbers",
            "CodeMirror-foldgutter"
          ],
          foldGutter: true,

          line: true,
        },

        resourceText: '',
        templateText: '',
        animationText: '',
        binderText: '',
        virtualModelText: '',

        isHardChangeResource: false,
        isHardChangeTemplate: false,
        isHardChangeAnimation: false,

        updateMark: false,
      }
    },
    methods: {
      emptyAnimation() {
        this.$store.commit('editor/emptyAnimation')
      },
      currentChange(currentPage) {
        this.currentPage = currentPage
      },
      changeTab(tabObj) {
        this.activePaneName = tabObj.name
        setTimeout(() => {
          this.$refs.resourceTextEditor?.refresh();
          this.$refs.templateTextEditor?.refresh();
          this.$refs.animationTextEditor?.refresh();
          this.$refs.binderTextEditor?.refresh();
          this.$refs.virtualModelTextEditor?.refresh();
        }, 0);
      },
      // eslint-disable-next-line no-unused-vars
      resourceTextChange(codeEditor) {
        setTimeout(() => {
          codeEditor.refresh()
        }, 50);
      },
      templateTextChange(codeEditor) {
        if (this.isHardChangeTemplate) {
          this.isHardChangeTemplate = false;
          return;
        }
        clearTimeout(this.codeUpdateTimeoutId);
        this.codeUpdateTimeoutId = setTimeout(() => {
          try {
            const json = JSON5.parse(codeEditor.getValue());
            this.$store.commit('editor/updateEditorUsingTemplateJSON', json);
            this.updateMark = true;
            this.$store.commit('editor/emitAniChangeSign')
          } catch(e) {
            console.error(e);
            this.$store.commit('editor/changeSpecTextValidState',
                {isValid: false, errorMsg: e.message});
          }
        }, 1000);
      },
      animationTextChange(codeEditor) {
        if (this.isHardChangeAnimation) {
          this.isHardChangeAnimation = false;
          return;
        }
        clearTimeout(this.codeUpdateTimeoutId);
        this.codeUpdateTimeoutId = setTimeout(() => {
          try {
            const json = JSON5.parse(codeEditor.getValue());
            this.$store.commit('editor/updateEditorUsingAnimationJSON', json);
            this.updateMark = true;
            this.$store.commit('editor/emitAniChangeSign')
          } catch(e) {
            console.error(e);
            this.$store.commit('editor/changeSpecTextValidState',
                {isValid: false, errorMsg: e.message});
          }
        }, 1000);
      },
      clickFilter(filter) {
        if (this.selectionTool === SelectionToolType.NORMAL) {
          this.$notify({title: 'Enter selection mode first', type: 'warning'});
          return;
        }
        const relatedTargets = filter.relatedTargets(this.fullTargetList);
        this.$store.commit('editor/changeSelectionTool', SelectionToolType.COMP);
        this.$nextTick(() => {
          this.$store.commit('target/changeSelectedTargets', relatedTargets);
          this.$store.commit('target/changeSvgSelectionChangeSign');
        });
      },
      enableFilterChange(enableFilter) {
        this.$store.commit('editor/changeFilterSwitch', enableFilter);
      },
    },
    mounted() {
      // this.paneHeight = this.$el.getBoundingClientRect().height - 44;
    },
    watch: {
      resourceJSONRenderSign() {
        // const newStr = JSON5.stringify(this.resourceJSON, null, 2);
        // if (this.$refs.resourceTextEditor.codemirror === newStr) {
        //   return;
        // }
        // this.isHardChangeResource = true;
        // this.resourceText = newStr;
        // this.$refs.resourceTextEditor.codemirror.setValue(newStr);
      },
      templateJSONRenderSign() {
        const newStr = JSON5.stringify(this.templateJSON, null, 2);
        if (this.$refs.templateTextEditor.codemirror.getValue() === newStr) {
          return;
        }
        this.isHardChangeTemplate = true;
        this.templateText = newStr;
        this.$refs.templateTextEditor.codemirror.setValue(newStr);
      },
      animationJSONRenderSign() {
        const newStr = JSON5.stringify(this.animationJSON, null, 2);
        if (this.$refs.animationTextEditor.codemirror.getValue() === newStr) {
          return;
        }
        this.isHardChangeAnimation = true;
        this.animationText = newStr;
        this.$refs.animationTextEditor.codemirror.setValue(newStr);
      },
      binderJSON() {
        const newStr = this.binderJSON != null
            ? JSON5.stringify(this.binderJSON, null, 2)
            : '(No binder)';
        this.binderText = newStr;
        this.$refs.binderTextEditor.codemirror.setValue(newStr);
      },
      targetModel() {
        let newStr = '';
        if (this.targetModel == null) {
          newStr = '(No target model)';
        } else {
          const vm = this.targetModel.virtualModel;   // vm is a DOM object
          // convert vm dom to string. each element is a line. add indent.
          // we don't convert the most outer tag, which is the root
          const lines = ['<!-- Root selection is a list of these elements -->']
          Array.from(vm.children).forEach((child) => {
            lines.push(getFormattedXmlString(child));
          });
          newStr = lines.join('\n');
        }
        // newStr = "<html style=\"color: green\">\n" +
        //     "  <!-- this is a comment -->\n" +
        //     "  <head>\n" +
        //     "    <title>HTML Example</title>\n" +
        //     "  </head>\n" +
        //     "  <body>\n" +
        //     "    The indentation tries to be <em>somewhat &quot;do what\n" +
        //     "    I mean&quot;</em>... but might not match your style.\n" +
        //     "  </body>\n" +
        //     "</html>\n"
        this.virtualModelText = newStr;
        this.$refs.virtualModelTextEditor.codemirror.setValue(newStr);

        const cm = this.$refs.virtualModelTextEditor.codemirror;
        console.log('>>', cm)

        setTimeout(() => {
          // collapse all lines that contain g-v-el
          let i = 0;
          while (i < cm.doc.size) {
            // const lineHandle = cm.getLineHandle(i);
            const lineText = cm.getLine(i);
            const toFold = lineText.includes('g-v-el');
            if (toFold) {
              cm.foldCode(i);
            }
            // console.log(i, lineText, toFold)
            i++;
          }
        })
      },
    },
    computed: {
      ...mapState('editor', {
        isSpecTextValid: state => state.isSpecTextValid,
        specTextErrorMsg: state => state.specTextErrorMsg,
        resourceJSON: state => state.resourceJSON,
        templateJSON: state => state.templateJSON,
        animationJSON: state => state.animationJSON,
        resourceJSONRenderSign: state => state.resourceJSONRenderSign,
        templateJSONRenderSign: state => state.templateJSONRenderSign,
        animationJSONRenderSign: state => state.animationJSONRenderSign,
        selectionTool: state => state.selectionTool,
        enableFilter: state => state.enableFilter,
        binderJSON: state => state.binderJSON,
        targetModel: state => state.targetModel,
      }),
      ...mapState('target', {
        fullTargetList: state => state.fullTargetList,
        selectedTargets: state => state.selectedTargets,
        activeFilters: state => state.activeFilters,
      }),
      filterTable() {
        const filterTable = [];
        const filters = this.activeFilters;
        for (let i = 0; i < filters.length; i += this.numFilterPerRow) {
          filterTable.push(filters.slice(i, i + this.numFilterPerRow));
        }
        return filterTable;
      },
      numFilterPerRow() {
        //return Math.max(1, Math.round((this.paneWidth-20)/190));
        return Math.max(1, Math.round((this.paneWidth-20)/190));
      }
    },
  }
</script>

