import {BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {ResolveRule} from "@/core/exec/resolve/ResolveRule"
import {ResolveContext} from "../ResolveContext"
import {LogicalAnimationUtils} from "@/core/ast/animation/logical/utils/LogicalAnimationUtils"
import {LoggerManager} from "@/core/utils/Logger"
import {ForeachAnimation} from "@/core/ast/animation/logical/impl/ForeachAnimation"
import {Sync} from "@/core/ast/animation/logical/impl/SyncAnimation"
import {Concat} from "@/core/ast/animation/logical/impl/ConcatAnimation"

/**
 * Replace all ForeachAnimation to Sync or Concat.
 */
export class ResolveForeach extends ResolveRule {
  private static readonly logger = LoggerManager.getLogger("ResolveForeach")

  constructor() {
    super("ResolveForeach")
  }

  apply(cxt: ResolveContext<unknown, unknown>, input: BaseLogicalAniType): BaseLogicalAniType {
    return LogicalAnimationUtils.transformDown(input, node => {
      if (!(node instanceof ForeachAnimation)) return node

      const start = node.props().start

      // don't copy stagger
      const timing = node.timing.clone()
      timing.stagger = null

      let newNode
      const props = {
        id: node.id,
        target: node.target.clone(),
        timing: timing,
        animations: [node.child.clone()],
      }
      if (start === 'with prev') {
        newNode = Sync(props)
      } else if (start === 'after prev') {
        newNode = Concat(props)
      }
      newNode.setForeachSrc(node)
      newNode.setSrc(node.refSrc, node.templateSrc)

      ResolveForeach.logger.trace(`Transformed ${node} to ${newNode}`)

      return newNode
    })
  }
}
