import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"


export const LineFlowPropSchema = EffectPropSchema.extend({
  reverseDirection: z.boolean()
      .default(false)
      .describe("Reverse animation direction (default: false)"),
  dash: z.string()
      .default('80% 10%')
      .describe("Dash pattern of the line (default: '15% 70% 15%')"),
  times: z.number()
      .default(1)
      .describe("Number of times to play the animation (default: 1)"),
  ease: z.string()
      .default('linear')
      .describe("Easing function of animation (default: linear)"),
})

export class LineFlowTemplate extends EffectTemplate<typeof LineFlowPropSchema> {
  constructor() {
    super({
      name: "LineFlow",
      packageName: "gaia.path",
      imports: [],
      targetType: "Line",
      propSchema: LineFlowPropSchema,
    })
  }
}
