import {z} from "zod"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {CompositeTemplate} from "@/core/ast/template/CompositeTemplate"
import {Ref, RefAnimation} from "@/core/ast/animation/logical/impl/RefAnimation"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {ZoomPropSchema} from "@/preset/template/common/ZoomTemplate"

export const ZoomInPropSchema = EffectPropSchema.extend({
  ease: z.string()
      .default('back.out')
      .describe("Easing function of animation (default: back.out)"),
})

export class ZoomInTemplate extends CompositeTemplate<typeof ZoomInPropSchema, RefAnimation<typeof ZoomPropSchema>> {
  constructor() {
    super({
      name: "ZoomIn",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: ZoomInPropSchema,
    })
  }

  instantiate<ObjType, AniType>(cxt: ResolveContext<ObjType, AniType>,
                                props: z.output<typeof ZoomInPropSchema>): RefAnimation<typeof ZoomPropSchema> {
    const {id, target, timing, ease} = props
    return Ref.builder<typeof ZoomPropSchema>()
        .ref('Zoom')
        .props({id, target, timing, type: 'in', ease})
        .build()
  }
}
