import {BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {ResolveRule} from "@/core/exec/resolve/ResolveRule"
import {ResolveRef} from "@/core/exec/resolve/rules/ResolveRef"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {Logger, LoggerManager} from "@/core/utils/Logger"
import {ResolveTarget} from "@/core/exec/resolve/rules/ResolveTarget"
import {CompileExec} from "@/session/CompileExec"
import {ResolveTiming} from "@/core/exec/resolve/rules/ResolveTiming"
import {toTreeString} from "@/core/utils/TreeUtils"
import {ResolveForeach} from "@/core/exec/resolve/rules/ResolveForeach"

export class Resolver<ObjType, AniType> {
  private static readonly logger: Logger = LoggerManager.getLogger('Resolver')

  readonly compileExec: CompileExec<ObjType, AniType>
  readonly extraRules: ResolveRule[]

  constructor(compileExec: CompileExec<ObjType, AniType>, extraRules: ResolveRule[] = []) {
    this.compileExec = compileExec
    this.extraRules = extraRules
  }

  getDefaultBatch(): ResolveRule[] {
    return [
      new ResolveRef(),
      new ResolveForeach(),
      new ResolveTarget(),
      new ResolveTiming(),
    ]
  }

  getBatch(): ResolveRule[] {
    return this.getDefaultBatch().concat(this.extraRules)
  }

  execute(input: BaseLogicalAniType): BaseLogicalAniType {
    Resolver.logger.info('Start resolve. Input logical animation:\n' + toTreeString(input))

    const resolveContext = new ResolveContext(this.compileExec)
    let animation = input
    for (const rule of this.getBatch()) {
      animation = rule.apply(resolveContext, animation)
      Resolver.logger.trace(`Applying rule ${rule.ruleName}:\n${toTreeString(animation)}`)
    }

    Resolver.logger.info('End resolve. Final logical animation:\n' + toTreeString(animation))
    return animation
  }
}
