import {LogicalAnimation, AniPropSchema, BaseAniPropSchema} from "./LogicalAnimation"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"

export const LeafAniProps = AniPropSchema.extend({
})

export abstract class LeafAnimation<PropType extends typeof LeafAniProps> extends LogicalAnimation<PropType> {
  visitChildren(): LogicalAnimation<BaseAniPropSchema>[] {
    return []
  }

  computeDuration(cxt: ResolveContext<unknown, unknown>) {
    return this.timing.duration ?? cxt.config.defaultDuration
  }

  rescale(cxt: ResolveContext<unknown, unknown>, rate: number): void {
    this.computedDuration *= rate
    this.computedTotalDuration *= rate
  }
}

