import {z} from "zod"
import {ParamUsageSpec} from "@/parser/json/animation/ParamUsageSpec"

const FilterSpec = z.object({
  filter: z.string()
})

const FilterExprSpec = z.object({
  filterExpr: z.string()
})

const SelectSpec = z.object({
  select: z.string()
})

const SelectAllSpec = z.object({
  selectAll: z.string()
})

const SortSpec = z.object({
  sortBy: z.string(),
  order: z.union([
    z.literal('asc'),
    z.literal('desc'),
    z.array(z.union([z.number(), z.string()]))
  ]).default('asc')
})

const GroupSpec = z.object({
  groupBy: z.string()
})

const SliceSpec = z.object({
  slice: z.union([
    z.array(z.union([z.number(), ParamUsageSpec]))
        .min(1)
        .max(3),
    z.string(),
  ])
})

const AtSpec = z.object({
  at: z.union([z.number(), ParamUsageSpec])
})

export const TransformSpec = z.union([
  FilterSpec,
  FilterExprSpec,
  SelectSpec,
  SelectAllSpec,
  SortSpec,
  GroupSpec,
  SliceSpec,
  AtSpec,
])

export const TargetSpec = z.union([
  z.string(),
  TransformSpec,
  z.array(TransformSpec)
])

export type TransformSpec = z.infer<typeof TransformSpec>
export type TargetSpec = z.infer<typeof TargetSpec>


