import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"


export const WheelPropSchema = EffectPropSchema.extend({
  type: z.enum(['in', 'out'])
      .default('in')
      .describe("Type of animation: \"in\" or \"out\" (default: \"in\")"),
  // direction: z.enum(['clockwise', 'counterclockwise'])
  //     .default('clockwise')
  //     .describe("Direction of the circle path: \"clockwise\" or \"counterclockwise\" (default: \"clockwise\")"),
  // center: z.string()
  //     .default('50% 50%')
  //     .describe("Center of the circle path (default: \"50% 50%\")"),
  startAngle: z.number()
      .default(0)
      .describe("Start angle (in degree) of the circle path (default: 0)"),
  sweepAngle: z.number()
      .default(360)
      .describe("Sweep angle (in degree) of the circle path (default: 360)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class WheelTemplate extends EffectTemplate<typeof WheelPropSchema> {
  constructor() {
    super({
      name: "Wheel",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: WheelPropSchema,
    })
  }
}
