import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectAnimation, EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"


export const DisplayPathPropSchema = EffectPropSchema.extend({
  reverseDirection: z.boolean()
      .default(false)
      .describe("Reverse animation direction (default: false)"),
  aniTime: z.number()
      .default(1)
      .describe("Animation time for the path (default: 1)"),
  displayTime: z.number()
      .default(3)
      .describe("Display time for the path (default: 3)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class DisplayPathTemplate extends EffectTemplate<typeof DisplayPathPropSchema> {
  constructor() {
    super({
      name: "DisplayPath",
      packageName: "gaia.path",
      imports: [],
      targetType: "Line",
      propSchema: DisplayPathPropSchema,
    })
  }

  create(cxt: ResolveContext<unknown, unknown>, props: z.output<typeof DisplayPathPropSchema>): DisplayPathEffect {
    const {name, packageName, targetType, propSchema} = this.meta
    return new DisplayPathEffect(name, packageName, targetType, propSchema, props)
  }
}

export class DisplayPathEffect extends EffectAnimation<typeof DisplayPathPropSchema> {
  computeDuration(cxt: ResolveContext<unknown, unknown>): number {
    return this.timing.duration ?? this.getBlinkDuration()
  }

  getBlinkDuration(): number {
    return this.prop('aniTime') * 2 + this.prop('displayTime')
  }

  clone(): DisplayPathEffect {
    return new DisplayPathEffect(this.name, this.packageName, this.targetType, this.propSchema, {
      id: this.id,
      target: this.target.clone(),
      timing: this.timing.clone(),
      reverseDirection: this.prop('reverseDirection'),
      aniTime: this.prop('aniTime'),
      displayTime: this.prop('displayTime'),
      ease: this.prop('ease'),
    })
  }
}
