import {z} from "zod"

export const WildcardPattern = z.string().regex(/^([a-zA-Z_][a-zA-Z_0-9]*\.)+\*$/)
export const FullNamePattern = z.string().regex(/^([a-zA-Z_][a-zA-Z_0-9]*\.)+[a-zA-Z_][a-zA-Z_0-9]*$/)

export const ImportSpec = z.union([
  FullNamePattern,
  WildcardPattern,
  z.object({
    class: FullNamePattern,
    as: z.string().optional(),
  }),
])

export type ImportSpec = z.infer<typeof ImportSpec>


