import {ImportSpec} from "@/parser/json/animation/ImportSpec"
import {z} from "zod"

export const TargetTypeComponentSpec = z.object({
  ref: z.string(),
  class: z.array(z.string()).default([]),
  required: z.boolean().default(false),
}).strict()

export const TargetTypeSpec = z.object({
  type: z.string(),
  package: z.string().default(""),
  import: z.array(ImportSpec).default([]),
  class: z.array(z.string()).default([]),
  components: z.array(z.union([z.string(), TargetTypeComponentSpec])).default([]),
}).strict()

export type TargetTypeComponentSpec = z.infer<typeof TargetTypeComponentSpec>
export type TargetTypeSpec = z.infer<typeof TargetTypeSpec>
export type TargetTypeSpecInput = z.input<typeof TargetTypeSpec>


