import {CompileExec} from "@/session/CompileExec"
import {TemplateLibrary} from "@/core/library/TemplateLibrary"
import {Logger, LoggerManager, LogRecord} from "@/core/utils/Logger"
import {BaseSpecParser} from "@/parser/BaseSpecParser"
import {TargetModel} from "@/core/ast/target/TargetModel"
import {JsonSpecParser} from "@/parser/json/JsonSpecParser"
import {Resolver} from "@/core/exec/resolve/Resolver"
import {Synthesizer} from "@/core/exec/synthesis/Synthesizer"
import {GsapSynthesizer} from "@/preset/gsap/GsapSynthesizer"
import {BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {TypeLibrary} from "@/core/library/TypeLibrary"
import {AniClassSpecInput} from "@/parser/json/animation/AniClassSpec"
import {getPresetTypes} from "@/preset/target"
import {getPresetTemplates} from "@/preset/template"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {BinderSpecInput} from "@/parser/json/target/BinderSpec"
import {BSVGTargetModel} from "@/core/ast/target/impl/svg/BSVGTargetModel"
import {SSVGTargetModel} from "@/core/ast/target/impl/svg/SSVGTargetModel"
import {TargetTypeSpecInput} from "@/parser/json/target/TargetTypeSpec"
import {BaseTemplate, BaseTemplateType} from "@/core/ast/template/BaseTemplate"
import {TargetType} from "@/core/ast/target/TargetType"
import {cloneDeep} from "lodash"


export class GaiaSession {
  private static readonly logger: Logger = LoggerManager.getLogger('GaiaSession')

  parser: BaseSpecParser<unknown>
  targetModelFactory: <ObjType>(session: GaiaSession, target: ObjType, ...targetArgs: unknown[]) => TargetModel<ObjType>
  resolverFactory: <ObjType, AniType>(session: GaiaSession, compileExec: CompileExec<ObjType, AniType>) => Resolver<ObjType, AniType>
  synthesizerFactory: <ObjType, AniType>(session: GaiaSession, compileExec: CompileExec<ObjType, AniType>) => Synthesizer<ObjType, AniType>

  document: Document

  templateLibrary: TemplateLibrary
  typeLibrary: TypeLibrary

  constructor() {
    const parser = this.parser = new JsonSpecParser()

    // SSVG factory
    // this.targetModelFactory = ((target: SVGElement) => new SSVGTargetModel(target)) as (<ObjType> (target: ObjType) => TargetModel<ObjType>)
    // SVG with binder factory
    // this.targetModelFactory = ((target: SVGElement, binder: BinderSpecInput) => new SVGTargetModel(target, this.parser.parseBinder(binder))) as (<ObjType> (target: ObjType, ...targetArgs: unknown[]) => TargetModel<ObjType>)
    // auto detect factory
    this.targetModelFactory = function(session: GaiaSession, target: SVGElement, binder?: BinderSpecInput): TargetModel<SVGElement> {
      // assert(target instanceof SVGSVGElement, `Target must be an SVGSVGElement. Got ${target.constructor.name}`)
      if (binder === undefined) {
        return new SSVGTargetModel(target as SVGSVGElement) as unknown as TargetModel<SVGElement>
      } else {
        return new BSVGTargetModel(target as SVGSVGElement, parser.parseBinder(binder)) as unknown as TargetModel<SVGElement>
      }
    } as <ObjType>(session: GaiaSession, target: ObjType, ...targetArgs: unknown[]) => TargetModel<ObjType>

    this.resolverFactory = function(session: GaiaSession, compileExec) {
      return new Resolver(compileExec)
    }

    this.synthesizerFactory = function(session: GaiaSession, compileExec: CompileExec<SVGElement, gsap.core.Animation>) {
      return new GsapSynthesizer(compileExec)
    } as <ObjType, AniType>(session: GaiaSession, compileExec: CompileExec<ObjType, AniType>) => Synthesizer<ObjType, AniType>

    this.document = document
  }

  init() {
    // create library with presets
    GaiaSession.logger.debug("Creating preset template library")
    this.templateLibrary = new TemplateLibrary()
    this.templateLibrary.register(getPresetTemplates(this))

    GaiaSession.logger.debug("Creating preset type library")
    this.typeLibrary = new TypeLibrary()
    this.typeLibrary.register(getPresetTypes(this))

    GaiaSession.logger.info(`GaiaSession created. Preset: ${this.templateLibrary.size} templates, ${this.typeLibrary.size} types`)
    GaiaSession.logger.trace(`Template library: ${this.templateLibrary.getAll().map(it => it.name).join(', ')}`)
    GaiaSession.logger.trace(`Type library: ${this.typeLibrary.getAll().map(it => it.name).join(', ')}`)
  }

  get logs(): LogRecord[] {
    return LoggerManager.records
  }

  registerOrUpdateTemplate(spec: AniClassSpecInput): void
  registerOrUpdateTemplate(template: BaseTemplateType): void
  registerOrUpdateTemplate(specOrTemplate: AniClassSpecInput | BaseTemplateType): void {
    if (specOrTemplate == undefined) return
    if (specOrTemplate instanceof BaseTemplate) {
      this.templateLibrary.registerOrUpdate(specOrTemplate)
    } else {
      const spec = cloneDeep(specOrTemplate)
      const template = this.parser.parseTemplate(spec)
      this.templateLibrary.registerOrUpdate(template)
    }
  }

  registerOrUpdateTemplates(specList: (AniClassSpecInput | BaseTemplateType)[]): void {
    if (specList == undefined) return
    for (const specOrTemplate of specList) {
      this.registerOrUpdateTemplate(specOrTemplate)
    }
  }

  unregisterTemplate(templateName: string): boolean {
    const template = this.templateLibrary.get(templateName)
    if (template == undefined) {
      return false
    }
    return this.templateLibrary.unregister(template.fullName)
  }

  registerOrUpdateType(spec: TargetTypeSpecInput): void
  registerOrUpdateType(type: TargetType): void
  registerOrUpdateType(specOrType: TargetTypeSpecInput | TargetType): void {
    if (specOrType == undefined) return
    if (specOrType instanceof TargetType) {
      this.typeLibrary.registerOrUpdate(specOrType)
    } else {
      const spec = cloneDeep(specOrType)
      const type = this.parser.parseTargetType(spec)
      this.typeLibrary.registerOrUpdate(type)
    }
  }

  registerOrUpdateTypes(specList: (TargetTypeSpecInput | TargetType)[]): void {
    if (specList == undefined) return
    for (const specOrType of specList) {
      // this.registerOrUpdateType(specOrType)  // this hints an error
      if (specOrType instanceof TargetType) {
        this.registerOrUpdateType(specOrType)
      } else {
        this.registerOrUpdateType(specOrType)
      }
    }
  }

  unregisterType(typeName: string): boolean {
    const type = this.typeLibrary.get(typeName)
    if (type == undefined) {
      return false
    }
    return this.typeLibrary.unregister(type.fullName)
  }

  compileJson<ObjType, AniType>(spec: AniClassSpecInput, target: ObjType, ...targetArgs: unknown[]): AniType {
    spec = cloneDeep(spec)
    let unresolved
    try {
      const template = this.parser.parseTemplate(spec)
      const compileExec = CompileExec.quickExec(this)
      const cxt = new ResolveContext(compileExec)
      unresolved = template.create(cxt, {})
    } catch (e) {
      GaiaSession.logger.error('Compile JSON failed. Caused by:\n' + e)
      throw e
    }
    return this.compile(unresolved, target, ...targetArgs)
  }

  compile<ObjType, AniType>(animation: BaseLogicalAniType, target: ObjType, ...targetArgs: unknown[]): AniType {
    try {
      const targetModel = this.targetModelFactory(this, target, ...targetArgs)
      targetModel.init(this)
      GaiaSession.logger.debug('Target model created')

      const compileExec = CompileExec.fullyExec<ObjType, AniType>(this, animation, targetModel)
      compileExec.setResolver(this.resolverFactory(this, compileExec))
          .setSynthesizer(this.synthesizerFactory(this, compileExec))
      GaiaSession.logger.debug('Compile exec created')

      return compileExec.generated

    } catch (e) {
      GaiaSession.logger.error('Compile failed. Caused by:\n' + e)
      throw e
    }
  }

  resolveJson<ObjType>(spec: AniClassSpecInput, target: ObjType, ...targetArgs: unknown[]): BaseLogicalAniType {
    spec = cloneDeep(spec)
    let unresolved
    try {
      const template = this.parser.parseTemplate(spec)
      const compileExec = CompileExec.quickExec(this)
      const cxt = new ResolveContext(compileExec)
      unresolved = template.create(cxt, {})
    } catch (e) {
      GaiaSession.logger.error('Resolve JSON failed. Caused by:\n' + e)
      throw e
    }
    return this.resolve(unresolved, target, ...targetArgs)
  }

  resolve<ObjType>(unresolved: BaseLogicalAniType, target: ObjType, ...targetArgs: unknown[]): BaseLogicalAniType {
    try {
      const targetModel = this.targetModelFactory(this, target, ...targetArgs)
      targetModel.init(this)

      const compileExec = CompileExec.fullyExec<ObjType, unknown>(this, unresolved, targetModel)
      compileExec.setResolver(this.resolverFactory(this, compileExec))

      return compileExec.resolved
    } catch (e) {
      GaiaSession.logger.error('Resolve failed. Caused by:\n' + e)
      throw e
    }
  }

  getTargetModel<ObjType>(target: ObjType, ...targetArgs: unknown[]) {
    try {
      const targetModel = this.targetModelFactory(this, target, ...targetArgs)
      targetModel.init(this)
      return targetModel
    } catch (e) {
      GaiaSession.logger.error('Get target model failed. Caused by:\n' + e)
      throw e
    }
  }

  static builder(): GaiaSessionBuilder {
    return new GaiaSessionBuilder()
  }
}

export class GaiaSessionBuilder {
  private readonly _session: GaiaSession

  constructor() {
    this._session = new GaiaSession()
  }

  parser<SpecType>(parser: BaseSpecParser<SpecType>): GaiaSessionBuilder {
    this._session.parser = parser
    return this
  }

  targetModelFactory(factory: <ObjType>(session: GaiaSession, target: ObjType, ...targetArgs: unknown[]) => TargetModel<ObjType>): GaiaSessionBuilder {
    this._session.targetModelFactory = factory
    return this
  }

  resolverFactory(factory: <ObjType, AniType>(session: GaiaSession, compileExec: CompileExec<ObjType, AniType>) => Resolver<ObjType, AniType>): GaiaSessionBuilder {
    this._session.resolverFactory = factory
    return this
  }

  synthesizerFactory(factory: <ObjType, AniType>(session: GaiaSession, compileExec: CompileExec<ObjType, AniType>) => Synthesizer<ObjType, AniType>): GaiaSessionBuilder {
    this._session.synthesizerFactory = factory
    return this
  }

  templateLibrary(templateLibrary: TemplateLibrary): GaiaSessionBuilder {
    this._session.templateLibrary = templateLibrary
    return this
  }

  typeLibrary(typeLibrary: TypeLibrary): GaiaSessionBuilder {
    this._session.typeLibrary = typeLibrary
    return this
  }

  document(document: Document): GaiaSessionBuilder {
    this._session.document = document
    return this
  }

  build(): GaiaSession {
    this._session.init()
    return this._session
  }
}

export const DefaultSession = GaiaSession.builder().build()
