import {TargetModel, VirtualElementType} from "@/core/ast/target/TargetModel"
import {Binder} from "@/core/ast/target/Binder"
import assert from "assert"
import {BaseSVGTargetModel} from "@/core/ast/target/impl/svg/BaseSVGTargetModel"
import {TargetSelection} from "@/core/ast/target/TargetSelection"

/**
 * Target model that uses SVG and binder as input.
 */
export class BSVGTargetModel extends BaseSVGTargetModel {
  private binder: Binder

  protected vNodeId2RealId: Map<string, string> = new Map()

  constructor(origin: SVGSVGElement, binder: Binder) {
    super(origin)
    this.binder = binder
    assert(binder.from == 'svg', 'Attribute "from" of BSVGTargetModel binder must be "svg".')
  }

  initRootType() {
    this.rootTargetType = this.session.typeLibrary.get(this.binder.to)
    if (this.rootTargetType == null) {
      throw new Error(`Target type ${this.binder.to} not found when processing root target type of BSVGTargetModel.`)
    }
  }

  completeVirtualModel() {
    let idCnt = 0
    const getNextId = () => {
      idCnt++
      let _id = `el-${idCnt}`
      while (this.realElementIdSet.has(_id)) {
        idCnt++
        _id = `el-${idCnt}`
      }
      return _id
    }

    if (this.rootTargetType.name === 'Any') {
      if (this.binder.rules.length > 0) {
        TargetModel.logger.warn(`Root target type is "Any" but binder has rules. Rules will be ignored.`)
      }
      // add id in origin obj
      if (this.origin.id == null || this.origin.id == '') {
        this.origin.id = getNextId()
      }
      Array.from(this.origin.querySelectorAll('*')).forEach(el => {
        if (el.id == null || el.id == '') {
          el.id = getNextId()
        }
        this.vNodeId2RealId.set(el.id, el.id)
      })

      const vm = this.virtualModel
      const vEl = this.origin.cloneNode(true) as VirtualElementType
      vm.appendChild(vEl)

      // create selection
      this.rootSelection = new TargetSelection([vEl], this, null)
      return
    }

    for (const rule of this.binder.rules) {
      const elements = this.origin.querySelectorAll<SVGElement>(rule.from)
      if (elements.length == 0) {
        TargetModel.logger.warn(`Rule ${JSON.stringify(rule)} matched 0 elements. Skip.`)
        continue
      }

      if (rule.type === 'single') {
        assert(elements.length == 1,
            `A single builder rule ${JSON.stringify(rule)} can only match 1 element, but got ${elements.length}.`)
        assert(rule.dataList == null, `A single builder rule ${JSON.stringify(rule)} cannot have dataList.`)
        const el = elements[0]

        // process id
        const isElIdValid = el.id != null && el.id != ''
        const isRuleIdValid = rule.id != null && rule.id != ''

        let finalId

        if (isElIdValid && isRuleIdValid) {
          // replace id with rule.id
          finalId = rule.id
        } else if (isElIdValid) {
          // use el.id
          finalId = el.id
        } else if (isRuleIdValid) {
          // use rule.id, but set it to el.id
          el.id = rule.id
          finalId = rule.id
        } else {
          // both are invalid, generate a new id
          finalId = getNextId()
          el.id = finalId
        }
        this.vNodeId2RealId.set(finalId, el.id)

        // process classList

        const typeNode = this.queryOneTypeNode(rule.to)
        const classList = typeNode.fullClassList
        rule.classList.forEach(clazz => {
          if (!classList.includes(clazz)) {
            classList.push(clazz)
          }
        })

        const vEl = this.createVirtualElement(el, finalId, classList, rule.data ?? {})
        this.virtualModel.appendChild(vEl)

      } else if (rule.type === 'batch') {
        assert(rule.data == null || rule.dataList == null,
            `Batch builder rule ${JSON.stringify(rule)} cannot have both data and dataList.`)
        if (rule.dataList != null) {
          assert(elements.length == rule.dataList.length,
              `Batch builder rule ${JSON.stringify(rule)} matched ${elements.length} elements, but got ${rule.dataList.length} data.`)
        }

        // process classList
        const typeNode = this.queryOneTypeNode(rule.to)
        const classList = typeNode.fullClassList
        rule.classList.forEach(clazz => {
          if (!classList.includes(clazz)) {
            classList.push(clazz)
          }
        })

        elements.forEach((el, idx) => {
          // process id
          let finalId
          const isElIdValid = el.id != null && el.id != ''
          if (!isElIdValid) {
            finalId = getNextId()
            el.id = finalId
          } else {
            finalId = el.id
          }
          this.vNodeId2RealId.set(finalId, el.id)

          // process data
          let data
          if (rule.dataList != null) {
            // each element has its own data
            data = rule.dataList[idx]
          } else if (rule.data != null) {
            // all elements share the same data
            data = rule.data
          } else {
            data = {}
          }

          const vEl = this.createVirtualElement(el, finalId, classList, data ?? {})
          this.virtualModel.appendChild(vEl)
        })

      } else {
        throw new Error(`Unknown rule type: ${rule.type}`)
      }
    }

    const virtualElements = Array.from(this.virtualModel.children) as VirtualElementType[]
    this.rootSelection = new TargetSelection(virtualElements, this, null)
  }

  getRealElementById(id: string): SVGElement {
    const realId = this.vNodeId2RealId.get(id)
    assert(realId != null, `Cannot find real id for virtual id ${id}.`)
    return this.origin.querySelector(`#${realId}`)
  }
}
