<template>
  <div class="export-svg-container-master">
    <svg ref="svg" :viewBox="`0 0 ${width} ${height}`">
      <rect x="0" y="0" :width="width" :height="height" :fill="'#ffffff'" stroke="none"></rect>
      <g class="scene"/>
    </svg>
  </div>
</template>

<script>
  import {mapState} from "vuex";
  import * as ExportUtils from "@/utils/output/ExportUtils";
  import {aniChart} from "@/core";

  export default {
    name: 'ExportSVGContainer',
    data() {
      return {
        svg: null,
        scene: null,

        // constance
        framerate: 24,  // frame every sec

        // temp var
        width: 0,
        height: 0,
        timeline: null,
        totalTime: 0,
        curTime: 0,
        svgList: [],
      }
    },
    methods: {
      exportVideo() {
        this.loadSVG();
        this.$nextTick(() => {
          this.collectFrames();
        })
      },
      loadSVG() {
        // reset the attr changed by Camera-zoom effect
        this.scene.innerHTML = this.svgStr;
        this.scene._gsap = null;      // remove the gsap cache
        this.scene.removeAttribute('transform')

        // update size
        const chart = this.scene.querySelector('svg');
        this.width = chart.getAttribute('width');
        this.height = chart.getAttribute('height');
      },
      collectFrames() {
        this.timeline = aniChart.render(this.scene, this.animationJSON, {});

        this.$nextTick(() => {
          this.startCollect()
        })
      },
      startCollect() {
        console.log('start collect')
        this.totalTime = this.timeline.totalDuration();
        console.log('total duration', this.totalTime)
        this.timeline.progress(0)
        this.curTime = 0;
        this.svgList = [];
        this.$nextTick(this.collectCurrentFrame);
      },
      collectCurrentFrame() {
        // console.log('collect')
        const svg = this.$el.innerHTML;
        this.svgList.push(svg);
        this.$nextTick(this.moveToNext);
      },
      moveToNext() {
        // console.log('seek time ptr')
        if (this.curTime > this.totalTime) {
          this.$nextTick(this.convertFramesToVideo);
          return;
        }
        this.timeline.progress(this.curTime / this.totalTime)
        // console.log('time:', this.curTime)
        this.curTime += 1 / this.framerate;
        this.$nextTick(this.collectCurrentFrame);
      },
      convertFramesToVideo() {
        const innerSvg = this.scene.querySelector('svg');
        let width = parseInt(innerSvg.getAttribute("width"))
        let height = parseInt(innerSvg.getAttribute("height"))

        // make the size smaller. Otherwise, we will get an OOM
        const maxWidth = 540, maxHeight = 360;
        const wFactor = width > maxWidth ? maxWidth / width : 1.0;
        const hFactor = height > maxHeight ? maxHeight / height : 1.0;
        const factor = Math.min(wFactor, hFactor);
        width = Math.floor(width * factor);
        height = Math.floor(height * factor);
        // make them be even number (required by ffmpeg)
        width += width & 1;
        height += height & 1;

        console.log('export', {svgList: this.svgList, audioBuffer: this.audioBuffer, width, height})

        this.$store.commit('editor/setIsExportingFiles', true);

        const generateAndDownload = () => {
          let mp4, code, message;
          try {
            let ret = ExportUtils.svgStringArrayToMP4(this.svgList, this.audioBuffer, width, height, this.framerate)
            mp4 = ret.result;
            code = ret.code;
            message = ret.message;
          } catch (e) {
            console.error(e);
          }

          console.log('create finished')
          if (code === 0) {
            this.$notify({
              type: 'success',
              title: 'Generate MP4 file successfully',
            });
            ExportUtils.download(mp4, 'output.mp4')
          } else {
            this.$notify({
              type: 'error',
              title: 'Fail to generate MP4 file',
              message: 'Message returned: ' + message
            });
          }

          this.$store.commit('editor/setIsExportingFiles', false);
        }

        // wait 1 sec to play the loading animation
        setTimeout(generateAndDownload, 1000);
      },
    },
    mounted() {
      this.svg = this.$refs.svg;
      this.scene = this.svg.querySelector('.scene');
    },
    watch: {
      exportSign() {
        this.exportVideo();
      }
    },
    computed: {
      ...mapState('editor', {
        exportSign: state => state.exportSign,
        svgStr: state => state.scene?.svgStr ?? '',
        animationJSON: state => state.animationJSON,
        audioBuffer: state => state.audioBuffer,
      }),
    }
  }
</script>

<style scoped>
.export-svg-container-master {
  user-select: none;
}
</style>
