import {z} from "zod"

export const BinderRuleSpec = z.object({
  from: z.string(),
  to: z.string(),
  id: z.string()
      .nonempty("Id cannot be empty if specified")
      .nullable()
      .default(null),
  classList: z.array(z.string())
      .default([]),
  // data and dataList cannot be both specified
  data: z.record(z.string(), z.any())
      .nullable()
      .default(null),
  dataList: z.array(z.record(z.string(), z.any()))
      .nullable()
      .default(null),
}).strict()

export const BinderSpec = z.object({
  from: z.string(),
  to: z.string(),
  rules: z.array(BinderRuleSpec).default([]),
}).strict()

export type BinderSpec = z.infer<typeof BinderSpec>
export type BinderSpecInput = z.input<typeof BinderSpec>


