import {BaseAniPropSchema, LogicalAnimation} from "@/core/ast/animation/logical/LogicalAnimation"
import {UnaryAnimation} from "@/core/ast/animation/logical/UnaryAnimation"
import {AryAnimation} from "@/core/ast/animation/logical/AryAnimation"
import {LeafAnimation} from "@/core/ast/animation/logical/LeafAnimation"
import {ResolveError} from "@/core/utils/error/ResolveError"

function preorderTraversal(animation: LogicalAnimation<BaseAniPropSchema>,
                           func: ((node: LogicalAnimation<BaseAniPropSchema>) => boolean)) {
  preorderTraversalHelper(animation, func)
}

function preorderTraversalHelper(node: LogicalAnimation<BaseAniPropSchema>,
                                 func: ((node: LogicalAnimation<BaseAniPropSchema>) => boolean)): boolean {
  const fastStop = func(node)
  if (fastStop) {
    return true
  }
  if (node instanceof UnaryAnimation) {
    return preorderTraversalHelper(node.child, func)
  } else if (node instanceof AryAnimation) {
    for (const child of node.children) {
      const childFastStop = preorderTraversalHelper(child, func)
      if (childFastStop) {
        return true
      }
    }
    return false
  } else if (node instanceof LeafAnimation) {
    return false
  }
  throw ResolveError.build("Fail to call traversal function on an unknown animation.")
}


function postorderTraversal(animation: LogicalAnimation<BaseAniPropSchema>,
                            func: ((node: LogicalAnimation<BaseAniPropSchema>) => boolean)) {
  postorderTraversalHelper(animation, func)
}

function postorderTraversalHelper(node: LogicalAnimation<BaseAniPropSchema>,
                                  func: ((node: LogicalAnimation<BaseAniPropSchema>) => boolean)): boolean {
  let fastStop = false

  if (node instanceof UnaryAnimation) {
    fastStop = postorderTraversalHelper(node.child, func)
  } else if (node instanceof AryAnimation) {
    for (const child of node.children) {
      const childFastStop = postorderTraversalHelper(child, func)
      if (childFastStop) {
        fastStop = true
        break
      }
    }
  } else if (node instanceof LeafAnimation) {
    // do nothing
  } else {
    throw ResolveError.build("Fail to call traversal function on an unknown animation.")
  }

  if (fastStop) {
    return true
  }
  return func(node)
}


function transformDown(animation: LogicalAnimation<BaseAniPropSchema>,
                       func: ((node: LogicalAnimation<BaseAniPropSchema>) => LogicalAnimation<BaseAniPropSchema>)): LogicalAnimation<BaseAniPropSchema> {
  return transformDownHelper(null, animation, func)
}

function transformDownHelper(parent: LogicalAnimation<BaseAniPropSchema>,
                             node: LogicalAnimation<BaseAniPropSchema>,
                             func: ((node: LogicalAnimation<BaseAniPropSchema>) => LogicalAnimation<BaseAniPropSchema>)): LogicalAnimation<BaseAniPropSchema> {
  const newNode = func(node)

  // transform down if it has children / child
  if (newNode instanceof UnaryAnimation) {
    const newChild = transformDownHelper(newNode, newNode.child, func)
    if (newChild !== newNode.child) {
      newNode.replace(newChild)
    }
  } else if (newNode instanceof AryAnimation) {
    for (const child of newNode.children) {
      const newChild = transformDownHelper(newNode, child, func)
      if (newChild !== child) {
        newNode.replace(child, newChild)
      }
    }
  }

  return newNode
}


export const LogicalAnimationUtils = {
  preorderTraversal,
  postorderTraversal,
  transformDown,
}
