import {BaseError} from "@/core/utils/error/BaseError"

export class UnresolvedError extends BaseError {
  static buildByProto(protoName: string, field: string): UnresolvedError {
    const message = `UnresolvedError: Visit field ${field} before analysis.\n` +
        `In proto ${protoName}.`
    return new UnresolvedError(message)
  }

  static build(msg: string): UnresolvedError {
    return new UnresolvedError('UnresolvedError: ' + msg)
  }
}
