import {LogicalAnimation, AniPropSchema, BaseLogicalAniType} from "./LogicalAnimation"
import {z} from "zod"
import {ForeachAnimation} from "@/core/ast/animation/logical/impl/ForeachAnimation"

export const AryPropSchema = AniPropSchema.extend({
  animations: z.instanceof(LogicalAnimation).array()
      .describe('Child animations')
})

export abstract class AryAnimation<PropType extends typeof AryPropSchema> extends LogicalAnimation<PropType> {
  private _foreachSrc: ForeachAnimation = null

  setForeachSrc(src: ForeachAnimation): void {
    this._foreachSrc = src
  }

  get foreachSrc(): ForeachAnimation {
    return this._foreachSrc
  }

  get children(): BaseLogicalAniType[] {
    return this.prop("animations")
  }

  setChildren(children: BaseLogicalAniType[]): void {
    this._props.animations = children
  }

  visitChildren(): BaseLogicalAniType[] {
    return this.prop("animations")
  }

  get animations(): BaseLogicalAniType[] {
    return this.prop("animations")
  }

  abstract replace(oldAnimation: BaseLogicalAniType, animation: BaseLogicalAniType): AryAnimation<PropType>
}

