<template>
  <div class="footer">
    <div class="operation-group">
<!--      <el-button-group style="margin-left: 10px;">-->
<!--        &lt;!&ndash; <el-button :type="timelineMode === 'text' ? 'primary' : ''" size="mini"-->
<!--                   @click="changeTimelineMode('text')">Narration</el-button> &ndash;&gt;-->
<!--        &lt;!&ndash; <el-button :type="timelineMode === 'time' ? 'primary' : ''" size="mini"-->
<!--                   @click="changeTimelineMode('time')">Timeline</el-button> &ndash;&gt;-->
<!--        <el-tooltip placement="top" style="font-size: 0.9rem;"-->
<!--          :content="showTimeline ? 'Hide Timeline' : 'Show Timeline'">-->
<!--          <el-button icon="el-icon-time" :type="showTimeline ? 'primary' : ''" size="mini" @click="changeTimelineDisplay"-->
<!--            style="font-size: 0.9rem;">Timeline</el-button>-->
<!--        </el-tooltip>-->
<!--      </el-button-group>-->
<!--      <el-divider direction="vertical"></el-divider>-->

<!--      <el-button-group style="">-->
<!--          <el-button icon="el-icon-data-board" size="mini" @click="loadExample('animation')" style="font-size: 0.9rem;">Load Animations</el-button>-->
<!--          <el-button icon="el-icon-refresh" size="mini" @click="resetScene" style="font-size: 0.9rem;">Reset Scene</el-button>-->
<!--      </el-button-group>-->

      <el-button @click="switchRunningState" :icon="runIcon"
                 type="primary" plain
                 style="width: 100px; font-size: 0.9rem;"
                 size="mini">{{ this.running ? "Pause" : "Play" }}
      </el-button>

<!--      <el-divider direction="vertical"></el-divider>-->

      <el-button @click="stopAndSeekToZero"
                 icon="el-icon-refresh"
                 type="warning" plain
                 size="mini"
                 style="width: 100px; margin-left: 20px; font-size: 0.9rem;">Reset</el-button>
    </div>

    <div class="timeline-group" v-loading="audioCompiling" element-loading-text="Updating Audio"
      element-loading-spinner="el-icon-loading">
<!--      TextPanel should be removed, but a bug will occur if remove it. So hide it for now-->
      <TextPanel v-show="timelineMode === 'text' && false" v-on:aniChange="handleAniChange"
        :panelHeight="showTimeline ? emptyHeight * 0.4 : emptyHeight" :audioRunning="running" :compile="compile"
        :style="{ width: tlContainerWidth + 'px' }" />
      <el-slider v-show="timelineMode === 'time'" class="slider" v-model="time" :style="{ width: timelineWidth + 'px' }"
        :max="duration" @input="changeTime" :marks="marks" :disabled="disabled" :format-tooltip="formatTooltip"/>
      <TimelinePanel :duration="duration" v-show="showTimeline" v-on:aniChange="handleAniChange"
        v-on:timeChange="handleTimeChange" :panelHeight="emptyHeight" />
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import TimelinePanel from "@/components/editor/ControlPanel/TimelinePanel";
import TextPanel from "@/components/editor/ControlPanel/TextPanel";
import { SelectionToolType } from "@/utils/common/SeletionToolType";

export default {
  name: "ControlPanel",
  components: { TimelinePanel, TextPanel },
  props: ["footerHeight"],
  data() {
    return {
      animationRate: 1,
      duration: 1,
      running: false,
      disabled: true,

      needAudio: false,
      needCompile: true,

      audioReadyCallback: null,

      marks: {},

      timelineMode: 'text', // time or text
      audioNotification: 'Audio', // Generating Audio... or Audio Updated
      updateAudioNotificationTimeout: null,

      showTimeline: true,
      showAnimationPane: false,
      emptyHeight: 0,
    }
  },
  methods: {
    init() {
      this.time = 0;
      this.running = false;
      this.disabled = true;
      this.needCompile = true;
      this.needAudio = false;
      this.$store.commit('editor/changeAniStatus', 'stop');
    },
    compile(notification = true, callback = null) {
      this.$store.commit('editor/changeAniStatus', 'pause');
      this.$store.commit('editor/changeAniSvgRenderSign');
      let onUpdateFunc = function () {
        this.time = this.timeline.progress() * this.duration
      }.bind(this)
      this.$store.commit('editor/changeSelectionTool', SelectionToolType.NORMAL);
      this.$store.commit('target/changeSelectedTargets', []);

      const bigCallback = () => {
        this.$nextTick(() => {
          this.$store.commit('editor/compile', { onUpdateFunc, notification });
          if (!this.compileSuccess) return;
          this.disabled = false;
          this.needCompile = false;
          this.stopAndSeekToZero();
          if (callback) callback();
        })
      }

      if (this.needAudio) {
        this.getAudio();
        this.audioReadyCallback = bigCallback;
      }
      else bigCallback();

    },
    handleTextChange() {
      this.needAudio = true;
    },
    handleAniChange() {
      this.needCompile = true;
    },
    handleTimeChange() {
      if (this.audioReady && this.useAudio)
        this.audioEl.currentTime = this.time / 1000;
    },
    changeEditMode() {
      if (this.currentMode === 'normal') {
        this.$store.commit('editor/changeMode', 'text');
      }
      else {
        this.$store.commit('editor/changeMode', 'normal');
      }
    },
    resetScene() {
      this.$store.commit('editor/initWorkspace');
      // TODO: seperate animation from the scene
      this.$store.commit('editor/resetEditor');
    },
    loadExample(mode = 'animation') {
      if (mode === 'animation')
        this.$store.commit('editor/initWorkspace', true);
      else
        this.$store.commit('editor/initLinking');
    },
    deleteAniItem(event) {
      this.$store.commit('editor/deleteItem', this.itemForSettings);

      // quit selection mode
      if (this.selectionTool !== SelectionToolType.NORMAL) {
        this.$store.commit('editor/clearSelectionLeave', true);
        if (!this.stopLeave) {
          this.$store.commit('editor/changeSelectionTool', SelectionToolType.NORMAL);
          this.$store.commit('editor/clearSelectionConfirm', false);
        }
      }

      // refresh button after click
      let target = event.target;
      if(target.nodeName === "I" || target.nodeName === "SPAN"){
        target = event.target.parentNode;
      }
      target.blur();
    },
    getWordLinking() {
      this.$store.dispatch('editor/queryNLPParseResult');
    },
    getNLPResult() {
      this.$store.dispatch('editor/queryNLPGenerateResult');
      this.$store.commit('editor/changeSelectionTool', SelectionToolType.NORMAL);
      this.needCompile = true;
    },
    getAudio() {
      const transcript = this.textPanelText;
      this.$store.commit('editor/updateAudio', transcript);
      this.needAudio = false;
      this.restart();
    },
    changeTimelineDisplay() {
      this.showTimeline = !this.showTimeline;
    },
    // changeAnimationPaneDisplay() {
    //   this.showAnimationPane = !this.showAnimationPane;
    //   // close timeline and text
    //   if (this.timelineMode !== "none") {
    //     this.changeTimelineMode("none");
    //   } else {
    //     this.changeTimelineMode("text");
    //   }
    //   this.showTimeline && this.changeTimelineDisplay();
    // },
    updateTimelineMarks() {
      const tickFactor = 10;
      const tickNum = tickFactor * this.timelineWidth / this.tlContainerWidth;
      const ticks = this.timelineScale.ticks(tickNum);
      const tickFormat = this.timelineScale.tickFormat(tickNum, ".2");
      const timeTicks = ticks.map(tickFormat);
      const marks = {}
      timeTicks.forEach(t => marks[parseInt(t) * 1000] = t);
      this.marks = marks
    },
    switchRunningState() {
      this.$store.commit('editor/changeSelectionTool', SelectionToolType.NORMAL);
      this.$store.commit('target/changeSelectedTargets', []);

      if (this.needCompile || this.needAudio) {
        this.compile(true, () => {
          if (this.running) {
            this.pause();
          } else {
            this.stopAndSeekToZero();
            this.resume();
          }
        });
      } else {
        if (this.running) {
          this.pause();
        } else {
          this.resume();
        }
      }

    },
    pause() {
      this.running = false
      this.timeline.pause()
      if (this.audioReady && this.useAudio) {
        this.audioEl.pause()
      }
      this.$store.commit('editor/changeAniStatus', 'pause');
    },
    stopAndSeekToZero() {
      this.$store.commit('editor/changeSelectionTool', SelectionToolType.NORMAL);
      this.$store.commit('target/changeSelectedTargets', []);

      this.running = false;
      if (this.timeline) {
        this.timeline.time(0);
        this.timeline.pause();
      }


      if (this.audioReady && this.useAudio) {
        this.audioEl.pause()
        this.audioEl.currentTime = 0
      }

      this.$store.commit('editor/changeAniStatus', 'stop');
    },
    resume() {
      this.running = true
      this.timeline.play()
      if (this.audioReady && this.useAudio) {
        this.audioEl.play()
      }
      this.$store.commit('editor/changeAniStatus', 'play');
    },
    formatTooltip(val) {
      val = val / 1000
      return val.toFixed(2);
    },
    changeTime(time) {
      if (!this.timeline) return
      let p = time / this.duration
      this.timeline.progress(p)
    },
    changeRate(rate) {
      try {
        const r = parseFloat(rate)
        if (r <= 0) return
        if (this.timeline)
          this.timeline.timeScale(r);
        if (this.audioReady) {
          this.audioEl.playbackRate = r
        }
      } catch (exception) {
        console.error(exception)
      }
    },
    changeTimelineMode(mode) {
      this.timelineMode = mode;
    },
    recomputeHeight() {
      const optionEl = document.getElementsByClassName('operation-group')[0];
      this.emptyHeight = this.footerHeight - optionEl.clientHeight;
    },
  },
  mounted() {
    this.init();
    this.$store.commit('editor/changeTlContainerWidth', 80);
  },
  watch: {
    footerHeight() {
      this.recomputeHeight();
    },
    controllerInitSign() {
      this.init();
    },
    audioReady() {
      if (this.audioReady && this.audioReadyCallback) {
        this.audioReadyCallback();
        this.audioReadyCallback = null;
      }
      if (this.audioReady) {
        const audioDuration = this.audioEl.duration * 1000 + 2000
        if (this.duration < audioDuration) {
          this.duration = audioDuration
          this.$store.commit('editor/updateTimelineScaleByAudio', this.duration);
          this.updateTimelineMarks();
        }
      }
    },
    timeline() {
      // this.duration = this.timelineScale.invert(this.timelineWidth) * 1000;
      //
      // const onCompleteFunc = function () {
      //   this.running = false
      //   this.timeline.pause()
      // }.bind(this)
      // if (this.timeline)
      //   this.timeline.eventCallback('onComplete', onCompleteFunc)
      //
      // this.changeRate(this.animationRate)
      // this.updateTimelineMarks()
    },
    timelineWidth() {
      this.duration = this.timelineScale.invert(this.timelineWidth) * 1000;
      this.updateTimelineMarks();
    },
    audioSrc() {
      // this.duration = Math.ceil(this.transcriptSrc.words.slice(-1)[0].endTime + 2) * 1000;
      // this.$store.commit('editor/updateTimelineScaleByAudio', this.duration);
      // this.updateTimelineMarks();
    },
    audioCompiling(newV) {
      this.audioNotification = newV ? 'Generating audio...' : 'Audio updated'
      if (newV) {
        clearTimeout(this.updateAudioNotificationTimeout);
      } else {
        this.updateAudioNotificationTimeout = setTimeout(() => {
          this.audioNotification = 'Audio';
        }, 2000);
      }
    },
    aniStatus() {
      if (this.aniStatus === 'preview') {
        this.needCompile = true;
      }
    },
    aniChangeSign() {
      this.needCompile = true;
    },
  },
  computed: {
    ...mapState('resource', {
    }),
    ...mapState('editor', {
      controllerInitSign: state => state.controllerInitSign,
      timeline: state => state.timeline,
      timelineScale: state => state.timelineScale,
      timelineWidth: state => state.timelineWidth,
      tlContainerWidth: state => state.tlContainerWidth,

      nlpLoading: state => state.nlpLoading,
      nlpLinking: state => state.nlpLinking,

      audioEl: state => state.audioEl,
      audioReady: state => state.audioReady,
      audioSrc: state => state.audioSrc,
      transcriptSrc: state => state.transcriptSrc,
      textPanelText: state => state.textPanelText,
      audioCompiling: state => state.audioCompiling,
      refresh: state => state.refresh,
      compileSuccess: state => state.compileSuccess,
      wordTargetIconPos: state => state.wordTargetIconPos,

      selectionTool: state => state.selectionTool,
      currentMode: state => state.currentMode,
      aniStatus: state => state.aniStatus,
      itemForSettings: state => state.itemForSettings,

      aniChangeSign: state => state.aniChangeSign,
    }),
    ...mapGetters('editor', [
      'svgPanelInSelecting',
    ]),
    useAudio: {
      get() {
        return this.$store.state.editor.useAudio
      },
      set(useAudio) {
        this.$store.commit('editor/changeUseAudio', useAudio)
      }
    },
    time: {
      get() {
        return this.$store.state.editor.time
      },
      set(time) {
        this.$store.commit('editor/changeTime', time)
      }
    },
    showPotential: {
      get() {
        return this.$store.state.editor.showPotential
      },
      set(showPotential) {
        this.$store.commit('editor/changeShowPotential', showPotential)
      }
    },
    runIcon: function () {
      return !this.running ? "el-icon-video-play" : "el-icon-video-pause"
    },
    linkButtonIcon() {
      return this.nlpLinking ? 'el-icon-loading' : 'el-icon-link';
    },
    nlpButtonIcon() {
      return this.nlpLoading ? 'el-icon-loading' : 'el-icon-magic-stick';
    },
    linked() {
      return this.wordTargetIconPos.length > 0;
    },
    editModeDisabled() {
      return this.selectionTool !== SelectionToolType.NORMAL;
    },
    inEditMode() {
      return this.currentMode === 'text';
    },
    // inSettingMode() {
    //   return this.
    // }
    showAniItemButton() {
      // animation buttons are available when an animation is selected
      return this.itemForSettings != null;
    }
  },
}
</script>

