import {Transform, TransformType} from "@/core/ast/target/Transform"
import {TargetSelection} from "@/core/ast/target/TargetSelection"
import {LoggerManager} from "@/core/utils/Logger"
import {TargetModel, VirtualElementType} from "@/core/ast/target/TargetModel"

/**
 * Group can only be used for tree-like virtual model
 */
export class GroupTransform extends Transform {
  private static readonly logger = LoggerManager.getLogger('GroupTransform')

  readonly groupBy: string

  constructor(groupBy: string) {
    super()
    this.groupBy = groupBy
  }

  get type(): TransformType {
    return "group"
  }

  execute(selection: TargetSelection): TargetSelection {
    const groupBy = this.groupBy
        .replaceAll('@id', '__el__.id')
        .replaceAll('@class', 'Array.from(__el__.classList)')
        .replaceAll('@index', '__idx__')
        .replaceAll('@data', '(__el__.data ?? {})')
        .replaceAll('@el', '__el__')
        .replaceAll('@coll', '__collection__')
    GroupTransform.logger.debug(`Group ${selection} with groupBy: ${groupBy}.`)
    const groups = selection.elements
    const keyMap = new Map<VirtualElementType, KeyType>()
    for (let idx = 0; idx < groups.length; idx++) {
      const group = groups[idx]
      let value
      try {
        value = mapperEval(groupBy, group, idx, groups, selection.targetModel)
      } catch (e) {
        GroupTransform.logger.error(`Error while evaluating groupBy: ${groupBy} on group ${group}.`, e)
        throw e
      }
      keyMap.set(group, value)
    }
    return selection.group(a => keyMap.get(a))
  }

  toString() {
    return `group(${this.groupBy})`
  }

  clone(): GroupTransform {
    return new GroupTransform(this.groupBy)
  }
}

/* eslint-disable @typescript-eslint/no-unused-vars */
function mapperEval(__groupBy__: string,
                    __el__: VirtualElementType,
                    __idx__: number,
                    __collection__: VirtualElementType[],
                    __model__: TargetModel<unknown>): KeyType {
  return eval(__groupBy__)
}
/* eslint-enable @typescript-eslint/no-unused-vars */
