<template>
  <div
    class="timeline-panel"
    draggable="false"
    @wheel="wheelHandler"
    @onmousewheel="onmousewheel"
    :style="{ height: panelHeight + 'px' }"
  >
    <!-- <svg width="100%" height="200" style="background: none">
     <g transform="translate(0,2)">
       <g>
         <g v-for="(visRow, rowIdx) in visRows" :key="`row-${rowIdx}`">
           <rect x="0" :y="rowIdx * rowHeight" :width="timelineWidth" :height="rowHeight"
                 fill="none"
                 @drop="drop($event, visRow, rowIdx)"
                 @dragover="dragover($event, visRow, rowIdx)"/>
         </g>
         <line v-for="(lineId) in Array.from({length: visRows.length+1},(o,i)=>i)"
               :key="'separate-line' + lineId"
               x1="0" :y1="lineId * rowHeight" :x2="timelineWidth" :y2="lineId * rowHeight"
               stroke="gray" stroke-width="2"></line>
       </g>
     </g>
   </svg> -->
    <!-- <el-tooltip placement="top" :value="timelineMode==='time'" :content="getTimeString()"> -->
    <div
      class="timeline-cursor-tooltip noSelection"
      :class="{ 'hided-div': !cursorInRange()}"
      :style="{ 'margin-left': cursorLeft - documentScrollLeft - 22 + 'px' }"
      @mousedown="moveTimeCursor()"
    >
      {{ getTimeString() }}
    </div>

    <div
      class="timeline-scroller"
      :style="{
        width: tlContainerWidth + 'px',
        cursor: 'auto',
        'overflow-x': 'auto',
        'overflow-y': 'hidden',
      }"
      @scroll="updateDocumentScrollLeft($event)"
      @mousedown="mousedownTimeline($event)"
      @mouseup="mouseupTimeline()"
      @mousemove="mousemoveTimeline($event)"
    >
      <div :style="{ width: timelineWidth + 'px', 'padding-bottom': '20px' }">
        <!-- el-slider__button -->
        <!-- </el-tooltip> -->
        <div
          class="timerow-scroller"
          style="overflow-y: hidden"
          :style="{
            height: (panelHeight - 55) + 'px',
          }"
        >

          <div
            class="timeline-row"
            v-for="(rowMap, rowIdx) in rowMapList"
            :key="`row-${rowIdx}`"
            :style="{
              height: rowHeight + 'px',
              width: '100%',
            }"
          >
            <div
                v-for="(vItem, itemIdx) in rowMap.get(ItemType.HIDDEN)"
                :key="'rect-' + vItem.uuid"
                class="timeline-item"
                :id="'rect-' + vItem.uuid"
                :style="{
                height: rectHeight + 'px',
                width: vItem.width + 'px',
                left: vItem.startX + 'px',
                top: '1px',
                background: getColor(vItem),
              }"
                style="cursor: pointer;"
                :item-idx="itemIdx"
                :title="`${getVItemTitle(vItem)} (${getDurationString(vItem)})`"
            >
              <!-- an animation block -->
              <div class="timeline-item-container"
                  @click="clickVItem(vItem)">
                <font-awesome-icon
                    id="animationIcon"
                    :icon="getAnimationIcon(vItem)"
                    style="margin-right: 2px"
                    :style="{
                    'background-color': 'white',
                    color: getColor(vItem),
                    'border-color': getColor(vItem),
                  }"
                />
                <div>
                  {{ getVItemTitle(vItem) }} ({{ getDurationString(vItem) }})
                </div>
              </div>

              <div class="timeline-item-cover"/>
            </div>

            <div
              v-for="(vItem, itemIdx) in rowMap.get(ItemType.SHOWN)"
              :key="'rect-' + vItem.uuid"
              class="timeline-item"
              :id="'rect-' + vItem.uuid"
              :style="{
                height: rectHeight + 'px',
                width: vItem.width + 'px',
                left: vItem.startX + 'px',
                top: '1px',
                background: getColor(vItem),
              }"
              :item-idx="itemIdx"
              :title="`${getVItemTitle(vItem)} (${getDurationString(vItem)})`"
            >
              <!-- an animation block -->
              <div
                class="timeline-item-container"
                @mouseenter="mouseenter(vItem)"
                @mouseleave="mouseleave(vItem)"
                @mousedown="mousedown($event, vItem, itemIdx)"
              >
<!--                <i class="el-icon-close" @click="deleteVItem(vItem)"></i>-->
                <font-awesome-icon
                  id="animationIcon"
                  :icon="getAnimationIcon(vItem)"
                  style="margin-right: 2px"
                  :style="{
                    'background-color': 'white',
                    color: getColor(vItem),
                    'border-color': getColor(vItem),
                  }"
                />
                <div>
                  {{ getVItemTitle(vItem) }} ({{ getDurationString(vItem) }})
                </div>

              </div>
            </div>

            <div
              v-for="(vItem, itemIdx) in rowMap.get(ItemType.SHOWN)"
              :key="'tooltip-' + vItem.uuid"
              class="timeline-item-tooltip"
              :id="'tooltip-' + vItem.uuid"
              :item-idx="itemIdx"
              :style="{
                height: rectHeight + 'px',
                width: vItem.width + 'px',
                left: vItem.startX + 'px',
                top: '2px',
              }"
              @mouseenter="mouseenter(vItem)"
              @mouseleave="mouseleave(vItem)"
              @mousedown="mousedown($event, vItem, itemIdx)"
            >
              <div
                class="button-container"
                :style="{
                  left: `-${dragButtonWidth}px`,
                  width: dragButtonWidth + 'px',
                  clipPath: `url(#rect-${vItem.uuid})`,
                  cursor: isFadeColor(vItem.originItem) ? 'auto' : 'e-resize'
                }"
                style="
                  position: absolute;
                  top: 0;
                  height: 100%;
                  border-radius: 5px 0 0 5px;
                "
                @mousedown="dragEdge($event, vItem, 'left')"
              >
                <el-icon class="el-icon-arrow-left my-icon" v-show="false" />
              </div>
              <div
                class="button-container"
                :style="{
                  right: `-${dragButtonWidth + 2}px`,
                  width: dragButtonWidth + 'px',
                  cursor: isFadeColor(vItem.originItem) ? 'auto' : 'e-resize'
                }"
                style="
                  position: absolute;
                  top: 0;
                  height: 100%;
                  border-radius: 0 5px 5px 0;
                "
                @mousedown="dragEdge($event, vItem, 'right')"
              >
                <el-icon class="el-icon-arrow-right my-icon" v-show="false" />
              </div>

              <!-- delete icon -->
              <!-- v-show="vItem.mouseOn || vItem === movingVItem" -->
              <!-- <div
                class="button-container head-button-container"
                :style="{ top: `-26px`, height: '25px' }"
                style="
                  position: absolute;
                  width: 100%;
                  left: 0;
                  min-width: 50px;
                  justify-content: start;
                  border-radius: 4px 4px 0 0;
                  z-index: 10;
                "
              >
                <el-icon
                  class="el-icon-delete delete-icon"
                  style="
                    margin-left: auto;
                    margin-right: 4px;
                    cursor: pointer;
                    border-radius: 4px;
                    padding: 2px;
                  "
                  @click.native="deleteVItem(vItem)"
                />
              </div> -->
              <div
                class="button-container foot-button-container"
                v-show="vItem.mouseOn || vItem === movingVItem"
                :style="{ marginTop: `${rectHeight}px`, height: '25px' }"
                style="
                  position: absolute;
                  width: 100%;
                  left: 0;
                  min-width: 50px;
                  justify-content: start;
                  border-radius: 0 0 4px 4px;
                "
              >
              </div>
            </div>

            <div
              v-if="newItem"
              class="timeline-item"
              :style="{
                height: '16px',
                width: newItem.width + 'px',
                left: newItem.startX + 'px',
                top: '1px',
              }"
            >
              <span class="timeline-item-text">{{ newItem.use }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div id="timeline-cursor-wrapper"
         style="position: relative; pointer-events: none;"
         :style="{
               'margin-top': (-(panelHeight - 55) - rowHeight) + 'px',
               // 'margin-top': -(rowHeight + 1) * visRows.length + 'px',
             }">
      <div
          class="timeline-marks noSelection"
          style="
              pointer-events: all;
              position: absolute;
              margin-top: -25px;
              height: 25px;
              z-index: -10;
              overflow: hidden;
            "
          :style="{}"
      >
        <svg
            id="timeline-marks-svg"
            style="height: 100%;"
            :style="{
              'margin-left':  -documentScrollLeft + 'px',
              width: timelineWidth + 'px'
            }"
        ></svg>
      </div>

      <div
          class="timeline-cursor noSelection"
          :class="{ 'red-bg': cursorInRange()}"
          :style="{
              'margin-top': 24 + 'px',
              'margin-left': (cursorLeft - documentScrollLeft) + 'px',
              height: (rowHeight + 1) * visRows.length + 'px',
            }"
      ></div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
// import {aniChart} from "@/core";
import { computeUUID } from "@/utils/common/uuid";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import {faFlag, faStar} from "@fortawesome/free-solid-svg-icons";
import * as d3 from "d3";
/* eslint-disable */
export default {
  name: "TimelinePanel",
  components: {
    FontAwesomeIcon,
  },
  props: ["duration", "panelHeight"],
  data() {
    return {
      rowHeight: 44,
      rectHeight: 40,
      tipHeight: 20,
      dragButtonWidth: 20,
      newItem: null,
      inMoving: false,
      movingVItem: null,

      visRows: [],
      shownVisRows: [],
      hiddenVisRows: [],

      cursorLeft: 0,
      iconLeft: [],
      faFlag: faFlag,
      documentScrollLeft: 0,
      moveTimeline: false,
      moveTimelineX: 0,
      moveTimelineY: 0,

      ItemType: {
        SHOWN: 'shown',
        HIDDEN: 'hidden',
      },
    };
  },
  methods: {
    onmousewheel() {
      return false
    },
    cursorInRange() {
      return this.cursorLeft - this.documentScrollLeft > -1
    },
    wheelHandler(event) {
      this.$store.commit(
        "editor/updateTimelineScale",
        event.deltaY > 0 ? 1.1 : 0.9
      );
      this.$store.commit("editor/fixScrollPos", event);
    },
    moveTimeCursor() {
      if (this.aniStatus === 'stop') {
        this.$notify({
          type: 'warning',
          title: 'Click play button first'
        })
        return
      }
      const left = document
        .getElementById("timeline-cursor-wrapper")
        .getBoundingClientRect().left;
      document.onmousemove = (e) => {
        this.cursorLeft = Math.max(e.clientX - left, 0);
        this.cursorLeft = Math.min(this.cursorLeft, this.timelineWidth);
        this.$store.commit(
          "editor/changeTime",
          (this.cursorLeft * this.duration) / this.timelineWidth
        );
      };
      document.onmouseup = () => {
        document.onmousemove = null;
        document.onmouseup = null;
        this.$emit("timeChange");
      };
    },
    mousedown(event, vItem, itemIdx) {
      event.stopPropagation();

      if (this.isFadeColor(vItem.originItem)) {
        return;
      }

      const element = document.getElementById("rect-" + vItem.uuid);
      const disX = event.clientX - element.offsetLeft;
      let disY = element.getBoundingClientRect().top;
      let rowIdx = this.visRows.findIndex((row) => row.includes(vItem));

      this.inMoving = false;
      this.movingVItem = null;
      this.changeIconLeft(itemIdx, 0);

      let moveCnt = 5;
      // drag mouse
      document.onmousemove = (e) => {
        if (moveCnt-- > 0) {
          return;
        }
        this.inMoving = true;
        this.movingVItem = vItem;
        const left = e.clientX - disX;
        const start = Math.round(this.timelineScale.invert(left) * 100) / 100;
        const castedLeft = this.timelineScale(start);
        const top = e.clientY - disY;
        // the distance towards timeline right destination
        const right = castedLeft + vItem.width;

        const rowCnt = this.timelineRows.length;
        const rowOffset = Math.floor(top / this.rowHeight);
        let curRow = rowIdx + rowOffset;
        curRow = Math.max(0, curRow);
        curRow = Math.min(rowCnt - 1, curRow);

        // console.log('move', castedLeft, right, top, curRow, rowCnt, rowOffset);
        // have changed row
        if (rowOffset !== 0) {
          // check conflict
          this.moveVItemBetweenRow(vItem, rowIdx, curRow);
          disY += this.rowHeight * rowOffset;
          rowIdx = curRow;
        }
        if (castedLeft < 0 || right > this.timelineWidth) {
          return;
        }
        // console.log(top);
        this.$store.commit("editor/updateItemTiming", {
          itemId: vItem.originItem.id,
          start,
        });
        // vItem.originItem will be update in last commit function
        // this.$store.commit('editor/changeItemUpdateTuple', {
        //   itemId: vItem.originItem.id,
        //   newItemInfo: {},
        // })
        vItem.startX = castedLeft;
      };
      // release mouse
      document.onmouseup = (e) => {

        this.movingVItem = null;
        document.onmousemove = null;
        document.onmouseup = null;

        if (!this.inMoving) {
          // just click
          this.clickVItem(vItem);
          return;
        }

        this.inMoving = false;

        const left = e.clientX - disX;
        const right = left + vItem.width;
        if (right > this.timelineWidth) {
          this.extend();
        }

        this.$store.commit("editor/updateAniJSONUsingTimelineRows");
        this.$store.commit("editor/updateItemRow", {
          item: vItem.originItem,
          rowIdx,
        });
        // vItem.originItem will be update in last commit function
        // this.$store.commit('editor/changeItemUpdateTuple', {
        //   item: vItem.originItem,
        //   newItemInfo: {},
        // })

        this.$emit("aniChange");
      };
    },
    // dblclickVItem(event, vItem) {
    //   // console.log('dblclick', event, vItem);
    //   if (this.inMoving) {
    //     // in moving
    //     return;
    //   }
    //   this.$store.commit('editor/showSettingPanel', vItem.originItem);
    // },
    clickVItem(vItem) {
      // console.log('>>> click', vItem)
      // this.$store.commit("editor/changeClickIconSignal", vItem.originItem);
    },
    deleteVItem(vItem) {
      this.$store.commit("editor/deleteItem", vItem.originItem);
      this.$emit("aniChange");
    },
    mouseenter(vItem) {
      if (this.inMoving && this.movingVItem !== vItem) {
        return;
      }
      vItem.mouseOn = true;

      this.$store.commit('editor/changeHighlightItem', vItem.originItem)
    },
    mouseleave(vItem) {
      if (this.inMoving && this.movingVItem !== vItem) {
        return;
      }
      vItem.mouseOn = false;

      if (this.itemForSettings !== this.highlightItem) {
        this.$store.commit('editor/changeHighlightItem', null)
      }
    },
    ifOverlap(vItem) {
      const overlapItems = this.overlapVisRows.get(vItem.startX);
      return (
        overlapItems.length !== 1 &&
        overlapItems.indexOf(vItem) === overlapItems.length - 1
      );
    },
    flatIcons(vItem, init = false) {
      if (!init) {
        vItem.clicked = !vItem.clicked;
      }
      let left = 0;
      this.visRows.flat().forEach((item, itemIdx) => {
        if (item.startX === vItem.startX) {
          this.changeIconLeft(itemIdx, left);
          left += vItem.clicked ? 25 : 5;
        }
      });
    },
    dragEdge(event, vItem, direction) {
      event.stopPropagation();
      if (this.isFadeColor(vItem.originItem)) {
        return;
      }

      console.log("mousedown", event, vItem, direction);
      const obj = event.target;
      console.log("target", obj);

      this.inMoving = false;
      this.movingVItem = null;

      if (direction === "left") {
        const element = document.getElementById("rect-" + vItem.uuid);
        const disX = event.clientX - element.offsetLeft;
        document.onmousemove = (e) => {
          this.inMoving = true;
          this.movingVItem = vItem;
          const left = e.clientX - disX;
          const start = Math.round(this.timelineScale.invert(left) * 100) / 100;
          const castedLeft = this.timelineScale(start);

          // console.log(disX, castedLeft)
          if (castedLeft < 0 || castedLeft > vItem.endX) {
            return;
          }
          const width = vItem.width + vItem.startX - castedLeft;
          const duration =
            this.timelineScale.invert(width) - this.timelineScale.invert(0);
          const castedDuration = Math.round(duration * 100) / 100;
          this.$store.commit("editor/updateItemTiming", {
            itemId: vItem.originItem.id,
            start,
            duration: castedDuration,
          });
          // vItem.originItem will be update in last commit function
          // this.$store.commit('editor/changeItemUpdateTuple', {
          //   itemId: vItem.originItem.id,
          //   newItemInfo: {},
          // })
          vItem.width =
            this.timelineScale(castedDuration) - this.timelineScale(0);
          vItem.startX = castedLeft;
        };
        document.onmouseup = (e) => {
          const left = e.clientX - disX;
          const right = left + vItem.width;
          if (right > this.timelineWidth) {
            this.extend();
          }
          this.inMoving = false;
          this.movingVItem = null;
          document.onmousemove = null;
          document.onmouseup = null;

          this.$store.commit("editor/updateAniJSONUsingTimelineRows");
        };
      } else if (direction === "right") {
        const element = document.getElementById("rect-" + vItem.uuid);
        const disX = event.clientX - element.offsetLeft - vItem.width;
        document.onmousemove = (e) => {
          this.inMoving = true;
          const left = e.clientX - disX;
          const start = Math.round(this.timelineScale.invert(left) * 100) / 100;
          const castedLeft = this.timelineScale(start);

          // console.log(disX, castedLeft)
          if (castedLeft <= vItem.startX || castedLeft > this.timelineWidth) {
            return;
          }
          const width = castedLeft - vItem.startX;
          const duration =
            this.timelineScale.invert(width) - this.timelineScale.invert(0);
          const castedDuration = Math.round(duration * 100) / 100;
          this.$store.commit("editor/updateItemTiming", {
            itemId: vItem.originItem.id,
            duration: castedDuration,
          });
          // vItem.originItem will be update in last commit function
          // this.$store.commit('editor/changeItemUpdateTuple', {
          //   itemId: vItem.originItem.id,
          //   newItemInfo: {},
          // })
          vItem.width =
            this.timelineScale(castedDuration) - this.timelineScale(0);
          vItem.endX = castedLeft;
        };
        document.onmouseup = (e) => {
          const left = e.clientX - disX;
          const right = left + vItem.width;
          if (right > this.timelineWidth) {
            this.extend();
          }
          this.inMoving = false;
          document.onmousemove = null;
          document.onmouseup = null;

          this.$store.commit("editor/updateAniJSONUsingTimelineRows");
          this.$emit("aniChange");
        };
      }
    },

    moveVItemBetweenRow(vItem, oldRowIdx, newRowIdx) {
      const oldRow = this.visRows[oldRowIdx];
      const newRow = this.visRows[newRowIdx];
      oldRow.splice(oldRow.indexOf(vItem), 1);
      newRow.push(vItem);
    },

    extend() {
      console.log("extend");
      this.$store.commit(
        "editor/changeTimelineWidth",
        this.timelineWidth * 1.5
      );
    },
    compress() {
      console.log("compress");
    },

    dragover(event) {
      event.preventDefault();
    },
    drop(event, tlRow, rowIdx) {
      const templateName = event.dataTransfer.getData("templateName");
      console.log("drop", templateName);
      if (!templateName) {
        return;
      }

      const startX =
        event.clientX -
        document.querySelector(".timeline-row").getBoundingClientRect().left;
      // console.log(event.clientX, this.$el.clientLeft)
      this.$store.commit("editor/appendTimelineItem", {
        rowIdx,
        templateName,
        startX,
      });
    },
    computeVisRows() {
      this.visRows = this.timelineRows.map((tlRow) => {
        return tlRow.map((item) => {
          const duration = item.computedTotalDuration
          const visItem = {
            ...item,
            originItem: item,
            uuid: computeUUID(),
            mouseOn: false,
          };
          visItem.startX = this.timelineScale(item.timing.offset ?? 0);
          visItem.width = this.timelineScale(duration) - this.timelineScale(0);
          visItem.endX = visItem.startX + visItem.width;
          return visItem;
        });
      });
    },
    getColor(vItem) {
      // return '#5288e8'
      return '#3692d1'
      // const title = this.getVItemTitle(vItem);
      // const clazz = this.useToAniMap[title] ?? vItem.ani_type ?? "Other";
      // return this.animationStyle[clazz]
      //   ? this.animationStyle[clazz].color
      //   : "#b5b5d8";
    },
    getAnimationIcon(vItem) {
      return faStar
      // const title = this.getVItemTitle(vItem);
      // const clazz = this.useToAniMap[title] ?? vItem.ani_type ?? "Other";
      // return this.animationStyle[clazz]
      //   ? this.animationStyle[clazz].icon
      //   : faFlag;
    },
    getVItemTitle(vItem) {
      const item = vItem.originItem
      if (item.initialRefSrc) {
        return item.initialRefSrc.ref
      }
      return item.name
      // if (vItem.originItem.sync || vItem.originItem.concat) {
      //   this.storeVItemTitle(
      //     vItem.originItem.sync ? "sync" : "concat"
      //   );
      //   return vItem.originItem.sync ? "sync" : "concat";
      // }
      // this.storeVItemTitle(
      //   vItem.ref
      //   // this.useToAniMap[vItem.use] ?? vItem.ani_type ?? "Other"
      // );
      // // return this.useToAniMap[vItem.use] ?? vItem.ani_type ?? "Other";
      // return vItem.ref;
    },
    storeVItemTitle(vItemTitle) {
      this.$store.commit("editor/storeVItemTitle", vItemTitle);
    },
    getDurationString(vItem) {
      let duration = vItem.originItem.computedTotalDuration
      duration = (Math.round(duration * 100) / 100).toFixed(2);
      return duration + "s";
    },
    getTimeString() {
      return (this.time / 1000).toFixed(2) + "s";
    },
    updateCursorPositionByTime() {
      this.cursorLeft = Math.max(
        (this.time / this.duration) * this.timelineWidth,
        0
      );
    },
    changeIconLeft(index, left) {
      this.iconLeft[index] = left;
    },
    updateDocumentScrollLeft(event) {
      this.documentScrollLeft = event.target.scrollLeft;
    },
    mousedownTimeline(event) {
      this.moveTimeline = true;
      this.moveTimelineX = event.x;
      this.moveTimelineY = event.y;
    },
    mousemoveTimeline(event) {
      if (this.moveTimeline) {
        const changeLeft = this.moveTimelineX - event.x;
        const changeTop = this.moveTimelineY - event.y;
        this.moveTimelineX = event.x;
        this.moveTimelineY = event.y;
        this.timelineScroller.scrollLeft += changeLeft;
        this.timerowScroller.scrollTop += changeTop;
      }
    },
    mouseupTimeline() {
      this.moveTimeline = false;
      this.moveTimelineX = 0;
      this.moveTimelineY = 0;
    },
    getCoverItems(visRow) {
      if (this.highlightItem === null) {
        return [];
      } else {
        return visRow.filter(o => o.originItem !== this.highlightItem);
      }
    },
    isFadeColor(item) {
      return this.highlightItem !== null && this.highlightItem !== item;
    },
    hasHighlightItem() {
      return this.highlightItem !== null;
    }
  },
  mounted() {
    this.$store.commit("editor/mountTimelinePanel", this.$el);
  },
  watch: {
    timelineRenderSign() {
      this.computeVisRows();
    },
    time() {
      this.updateCursorPositionByTime();
    },
    timelineWidth() {
      this.updateCursorPositionByTime();
      this.$store.commit("editor/updateTimelineSvg");
    },
    overlapVisRows(newV) {
      this.iconLeft = Array.apply(0, Array(newV.length)).map(function () {});
      this.visRows.flat().forEach((vItem) => {
        this.flatIcons(vItem, true);
      });
    },
    scrollLeft(newV) {
      this.timelineScroller.scrollLeft = newV;
    },
  },
  computed: {
    ...mapState("editor", {
      sceneEl: (state) => state.sceneEl,
      time: (state) => state.time,
      timelineRows: (state) => state.timelineRows,
      timelineScale: (state) => state.timelineScale,
      tlContainerWidth: (state) => state.tlContainerWidth,
      timelineWidth: (state) => state.timelineWidth,
      timelineRenderSign: (state) => state.timelineRenderSign,
      durationMap: (state) => state.durationMap,
      animationStyle: (state) => state.animationStyle,
      useToAniMap: (state) => state.useToAniMap,
      scrollLeft: (state) => state.scrollLeft,
      highlightItem: state => state.highlightItem,
      itemForSettings: state => state.itemForSettings,
      aniStatus: state => state.aniStatus,
    }),
    overlapVisRows() {
      return d3.group(this.visRows.flat(), (d) => d.startX);
    },
    timelineScroller() {
      return document.querySelector(".timeline-scroller");
    },
    timerowScroller() {
      return document.querySelector(".timerow-scroller");
    },
    rowMapList() {
      return this.visRows.map(row => {
        const rowMap = new Map();
        if (this.highlightItem !== null) {
          const shown = row.filter(vItem => vItem.originItem === this.highlightItem);
          const hidden = row.filter(vItem => vItem.originItem !== this.highlightItem)
          rowMap.set(this.ItemType.SHOWN, shown);
          rowMap.set(this.ItemType.HIDDEN, hidden);
        } else {
          rowMap.set(this.ItemType.SHOWN, row);
          rowMap.set(this.ItemType.HIDDEN, []);
        }
        return rowMap;
      });
    },
  },
};
</script>

