import {BaseTemplate} from "@/core/ast/template/BaseTemplate"
import {BaseAniPropSchema, LogicalAnimation} from "@/core/ast/animation/logical/LogicalAnimation"
import {z} from "zod"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"

export abstract class CompositeTemplate<PropType extends BaseAniPropSchema, LogicalAniType extends LogicalAnimation<BaseAniPropSchema>>
  extends BaseTemplate<PropType, LogicalAniType> {

  create<ObjType, AniType>(cxt: ResolveContext<ObjType, AniType>, props: z.input<PropType>): LogicalAniType {
    const parsedProps = this.propSchema.parse(props)
    return this.instantiate(cxt, parsedProps)
  }

  abstract instantiate<ObjType, AniType>(cxt: ResolveContext<ObjType, AniType>, props: z.output<PropType>): LogicalAniType
}
