import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectAnimation, EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"

export const CameraPropSchema = EffectPropSchema.extend({
  from: z.object({
    scale: z.number().optional(),
    x: z.number().or(z.string()).optional(),
    y: z.number().or(z.string()).optional(),
  }).optional()
      .describe("Transform from position (scale, x, y). Example: {scale: 0.5, x: 100, y: 100}, {x: '-=10', y: '+=10px'}"),
  to: z.object({
    scale: z.number().optional(),
    x: z.number().or(z.string()).optional(),
    y: z.number().or(z.string()).optional(),
  }).optional()
      .describe("Move to position (x, y). Example: {scale: 0.5, x: 100, y: 100}, {x: '-=10', y: '+=10px'}"),
  origin: z.string()
      .default('50% 50%')
      .describe("Origin of transformation (default: \"50% 50%\")"),
  reset: z.boolean()
      .default(false)
      .describe("Reset the camera position (default: false)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class CameraTemplate extends EffectTemplate<typeof CameraPropSchema> {
  constructor() {
    super({
      name: "Camera",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: CameraPropSchema,
    })
  }

  create(cxt: ResolveContext<unknown, unknown>, props: z.output<typeof CameraPropSchema>): CameraEffect {
    const {name, packageName, targetType, propSchema} = this.meta
    return new CameraEffect(name, packageName, targetType, propSchema, props)
  }
}

export class CameraEffect extends EffectAnimation<typeof CameraPropSchema> {
  computeTotalDuration(cxt: ResolveContext<unknown, unknown>): number {
    return this.computeDuration(cxt)
  }

  clone(): CameraEffect {
    throw new Error("CameraEffect can't be cloned")
  }
}
