    </div>

    <el-row class="content-container"
            v-loading="isExportingFiles"
            element-loading-text="Exporting...It will take a few minutes.">

      <div class="left-aside-container"
           style="margin-right: 4px;"
           :style="{width: asideWidth + 'px'}">
        <DebugPanel
          :pane-height="debugPanelHeight"
          :pane-width="asideWidth"
          :style="{height: '100%'}"
          style="width: calc(100% - 3px);"/>
      </div>

      <div id="resize-left" class="v-resizer" v-show="asideWidthCache === null"></div>

      <div class="middle-container" style="margin-left: -2px;">
        <div class="main-container" @click="clearSelection"
        :style="{height: svgHeight + 'px'}"
        ref="mainContainer">
          <ExportSVGContainer style="position: absolute; z-index: 992;"/>
          <SVGContainer style="position: absolute; z-index: 993;" v-show="aniStatus === 'stop'"/>
          <div style="width: 90%; height: 90%; background: white; position: absolute; z-index: 995;"
               :style="{ visibility: aniStatus === 'playing' ? 'visible' : 'hidden' }"/>
          <AniSVGContainer style="position: absolute; z-index: 995;"
                           v-show="aniStatus !== 'stop'"/>
        </div>

        <div class="footer-container" :style="{height: footerHeight + 'px'}">
          <div class="h-resizer" id="hResizer"
          :style="{
            width: controlPanelWidth + 'px',
            bottom: (footerHeight-6) + 'px',
            }"
          @mousedown="bottomResizerMousedown">
          </div>
          <ControlPanel
          :footerHeight="footerHeight"/>
        </div>
      </div>

    </el-row>

    <el-dialog
        title="Load"
        :visible.sync="projSelPanelVisible"
        lock-scroll
        fullscreen
        width="800px">
      <ProjSelectionPanel @closeProjSelPanel="closeProjSelPanel"></ProjSelectionPanel>
    </el-dialog>

