<script>
  import {mapGetters, mapState} from 'vuex'
  import * as d3 from 'd3'
  import ControlPanel from "@/components/editor/ControlPanel/ContrlPanel";
  import SVGContainer from "@/components/editor/SVGContainer";
  import AniSVGContainer from "@/components/editor/AniSVGContainer";
  import DebugPanel from "@/components/editor/AsidePanel/DebugPanel";
  import ProjSelectionPanel from "@/components/editor/ProjSelPanel/ProjSelectionPanel";
  import ExportSVGContainer from "@/components/editor/ExportSVGContainer";


  export default {
    name: 'EditorView',
    components: {
      ExportSVGContainer,
      DebugPanel,
      SVGContainer,
      ControlPanel,
      AniSVGContainer,
      ProjSelectionPanel,
    },
    data() {
      return {
        asideWidth: 590,
        asideWidthArray: [210, 400, 590, 780, 970],
        asideWidthScale: null,
        asideWidthCache: null,

        showCode: true,
        debugPanelHeight: 0,

        activeSidePanel: '',

        headerHeight: 50,
        footerRatio: 0.25,
        footerHeight: 0,

        svgUrl: '',
        targetDisabled: false,

        projSelPanelVisible: false,
      }
    },
    mounted() {
      // if (!this.scene) {
      //   // this.$router.push('system');
      // }
      let index = 0;
      try {
        index = parseInt(this.$route.query['example-index'] ?? ('' + index))
      } catch (e) {
        console.error('Failed to parse example index')
        console.log(e)
      }
      this.$store.commit('resource/changeProjectId', this.projectList[index].id)

      this.$store.commit('editor/mountAudioElement', this.$refs.audioEl);

      this.initAsideWidthScale()
      this.setupLeftResizer();
      this.setupLeftMiddleResizer();

      // initWorkspace will be called by project store
      // this.$store.commit('editor/initWorkspace');

      const el = this.$el.querySelector('.left-aside-container');
      this.debugPanelHeight = el.getBoundingClientRect().height - 44;

      this.$store.commit('editor/changeWindowSize');

      this.$store.commit('editor/mountMainContainer', this.$refs.mainContainer);

      let lastCall = Date.now();

      // Register the window size listener
      window.onresize = () => {
        if (Date.now() - lastCall > 100) {
          this.$store.commit('editor/changeWindowSize');
          lastCall = Date.now();
        }
      }

      this.footerHeight = this.footerRatio * this.windowHeight;

      this.$store.commit('editor/changeHeaderHeight', this.headerHeight);
      this.$store.commit('editor/changeFooterHeight', this.footerHeight);

      this.$store.commit('editor/changeLeftPanelWidth', this.asideWidth);
    },
    methods: {
      saveSpec() {
        this.$store.commit('editor/saveSpec');
      },
      clickHeaderText() {
        this.$router.push('homepage');
      },
      openProjSelPanel() {
        this.projSelPanelVisible = true;
      },
      closeProjSelPanel() {
        this.projSelPanelVisible = false;
      },
      /* eslint-disable */
      exportClick() {
        this.$store.commit('editor/changeExportSign');
      },
      isRenewedOption(id) {
        return this.renewedOptions.has(id);
      },
      disableTargetButton() {
        this.targetDisabled = true;
      },
      enableTargetButton() {
        this.targetDisabled = false;
      },
      initAsideWidthScale() {
        let domain = []
        for (let i = 0; i < this.asideWidthArray.length - 1; ++i) {
          domain.push((this.asideWidthArray[i] + this.asideWidthArray[i + 1]) / 2)
        }
        this.asideWidthScale = d3.scaleThreshold()
            .domain(domain)
            .range(this.asideWidthArray)
      },
      getTTSResult() {
        this.$store.dispatch('resource/queryTTSAudioGoogle', {
          el: this,
        })
      },
      setupLeftResizer() {
        const that = this;
        const resize = document.getElementById("resize-left");
        let inEl = false
        let moving = false
        resize.onmouseenter = function() {
          inEl = true
          resize.style.background = "rgba(33,150,243,0.67)";
        }
        resize.onmouseleave = function() {
          inEl = false
          if (moving) return
          resize.style.background = ""
        }

        resize.onmousedown = function() {
          moving = true
          document.onmousemove = function(e) {
            const curX = e.clientX;
            that.asideWidth = that.asideWidthScale(curX)
            that.$store.commit('editor/changeLeftPanelWidth', that.asideWidth);
            // console.log(that.asideWidth)
          };
          document.onmouseup = function() {
            moving = false
            document.onmousemove = null;
            document.onmouseup = null;
            if (!inEl) {
              resize.style.background = ""
            }
          };
          return false;
        };
      },
      setupLeftMiddleResizer() {
        const that = this;
        const resize = document.getElementById("resize-left-middle");
        if (!resize) {
          return;
        }
        let inEl = false
        let moving = false
        resize.onmouseenter = function() {
          inEl = true
          resize.style.background = "rgba(33,150,243,0.67)";
        }
        resize.onmouseleave = function() {
          inEl = false
          if (moving) return
          resize.style.background = ""
        }

        resize.onmousedown = function() {
          moving = true
          const rect = document.getElementsByClassName('content-container')[0].getBoundingClientRect();
          document.onmousemove = function(e) {
            that.topPercentage = (e.clientY - rect.top) / rect.height * 100;
            console.log(rect, that.topPercentage);
          };
          document.onmouseup = function() {
            moving = false
            document.onmousemove = null;
            document.onmouseup = null;
            if (!inEl) {
              resize.style.background = ""
            }
          };
          return false;
        };
      },
      bottomResizerMousedown() {
        const that = this;
        let lastUpdate = Date.now() - 100;

        document.onmousemove = function(event) {
          if (Date.now() - lastUpdate < 100) return;
          lastUpdate = Date.now();

          const nowY = event.clientY;
          let nowRatio = (that.windowHeight-nowY) / that.windowHeight;
          nowRatio = Math.max(0.1, nowRatio);
          nowRatio = Math.min(0.8, nowRatio);
          that.footerRatio = nowRatio;

          document.onmouseup = function() {
            document.onmousemove = null;
            document.onmouseup = null;
          }
        }
      },
      changeDebugPanelStatus() {
        this.showCode = !this.showCode;
        if (this.showCode) {
          const el = this.$el.querySelector('.left-aside-container');
          this.debugPanelHeight = el.getBoundingClientRect().height - 44;
        }
      },
      changeRightPanel(target) {
        if (this.activeSidePanel === target)
          this.activeSidePanel = '';
        else {
          this.activeSidePanel = target;
        }
      },
      undo() {

      },
      redo() {

      },
      exitEditor() {
        this.$store.commit('editor/stashScene');
        this.$router.push('system');
      },
      clearSelection(/*event*/) {
        // const {currentTarget, target} = event;
        // if (currentTarget !== target) {
        //   return;
        // }
        // if (this.svgPanelInSelecting) {
        //   this.$store.commit('target/changeSelectedTargets', []);
        // }
      },
      clickLeftCollapseButton() {
        this.changeDebugPanelStatus();
        if (this.asideWidthCache === null) {
          this.asideWidthCache = this.asideWidth;
          this.asideWidth = 0;
        } else {
          this.asideWidth = this.asideWidthCache;
          this.asideWidthCache = null;
        }
        this.$store.commit('editor/changeLeftPanelWidth', this.asideWidth);
      },
    },
    watch: {
      projectId() {
        if (!this.projectId) {    // first time
          return;
        }
        this.$store.dispatch('resource/queryProjectSpec2', this.projectId);
      },
      windowWidth() {
        this.$store.commit('editor/updateChartSize');
      },
      windowHeight() {
        this.footerHeight = this.windowHeight * this.footerRatio;
        this.$store.commit('editor/changeFooterHeight', this.footerHeight);
        this.$store.commit('editor/updateChartSize');
      },
      leftPanelWidth() {
        this.$store.commit('editor/updateChartSize');
      },
      targetPanelWidth() {
        this.$store.commit('editor/updateChartSize');
      },
      footerRatio() {
        this.footerHeight = this.windowHeight * this.footerRatio;
        this.$store.commit('editor/changeFooterHeight', this.footerHeight);
        this.$store.commit('editor/updateChartSize');
      },
      currentMode() {
        if (this.currentMode === 'animation') {
          this.activeSidePanel = 'effect';
        }
      },
    },
    computed: {
      ...mapState('resource', {
        projectList: state => state.projectList,
        examples: () => [],
      }),
      ...mapState('editor', {
        scene: state => state.scene,
        audioSrc: state => state.audioSrc,
        nlpLoading: state => state.nlpLoading,
        windowWidth: state => state.windowWidth,
        windowHeight: state => state.windowHeight,
        aniStatus: state => state.aniStatus,
        leftPanelWidth: state => state.leftPanelWidth,
        targetPanelWidth: state => state.targetPanelWidth,
        currentMode: state => state.currentMode,

        timeline: state => state.timeline,
        isExportingFiles: state => state.isExportingFiles,
      }),
      ...mapGetters('editor', [
          'svgPanelInSelecting',
      ]),
      sampleFileOptions() {
        return this.examples.map(e => ({label: e.label, value: e}))
      },
      projectId: {
        set(value) {
          this.$store.commit('resource/changeProjectId', value);
        },
        get() {
          return this.$store.state.resource.projectId;
        }
      },
      optionGroups() {
        const groupMap = new Map();
        this.projectList.forEach(project => {
          const group = project.group;
          if (group === 'Single chart' || group === 'Custom chart' || group.includes('Vega')) {
            if (groupMap.has(group)) {
              groupMap.get(group).push(project);
            } else {
              groupMap.set(group, [project]);
            }
          }
        });
        const groups = [];
        groupMap.forEach((projects, group) => {
          groups.push({
            label: group ?? 'Other',
            options: projects.map(p => ({label: p.label, value: p})),
          });
        });

        return groups;
      },
      nlpButtonIcon() {
        return this.nlpLoading ? 'el-icon-loading' : 'el-icon-magic-stick';
      },
      controlPanelWidth() {
        return this.windowWidth - this.targetPanelWidth - this.leftPanelWidth;
      },
      svgHeight() {
        return this.windowHeight - this.headerHeight - this.footerHeight;
      },
      renewedOptions() {
        const newOptions = [
          'vega-lite1',
          'vega-lite2',
          'vega-lite4',
          'vega-lite5',
          'vega-lite6',
          'vega-lite7',
          'vega-lite9',
          'vega-lite21',
          'vega-lite22',
        ];
        return new Set(newOptions);
      },
    }
  }
</script>

