import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectAnimation, EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"

export const CirclePropSchema = EffectPropSchema.extend({
  direction: z.enum(['clockwise', 'counterclockwise'])
      .default('clockwise')
      .describe("Direction of the circle path: \"clockwise\" or \"counterclockwise\" (default: \"clockwise\")"),
  aniTime: z.number()
      .default(1)
      .describe("Animation time for the circle path (default: 1)"),
  displayTime: z.number()
      .default(3)
      .describe("Display time for the circle path (default: 3)"),
  stroke: z.string()
      .default('#ff6868')
      .describe("Stroke color of the circle path (default: #ff6868)"),
  strokeWidth: z.number()
      .default(3)
      .describe("Stroke width of the circle path (default: 3)"),
  // dx: z.number()
  //     .default(0)
  //     .describe("X offset of the circle path (default: 0)"),
  // dy: z.number()
  //     .default(0)
  //     .describe("Y offset of the circle path (default: 0)"),
  // width: z.number().or(z.string())
  //     .default('100%')
  //     .describe('Width of the circle path, can be a pixel value (e.g., "100px"), a percentage value (e.g., 110%) (default: 100%)'),
  // height: z.number().or(z.string())
  //     .default('100%')
  //     .describe('Height of the circle path, can be a pixel value (e.g., "100px"), a percentage value (e.g., 110%) (default: 100%)'),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class CircleTemplate extends EffectTemplate<typeof CirclePropSchema> {
  constructor() {
    super({
      name: "Circle",
      packageName: "gaia.annotation",
      imports: [],
      targetType: "Any",
      propSchema: CirclePropSchema,
    })
  }

  create(cxt: ResolveContext<unknown, unknown>, props: z.output<typeof CirclePropSchema>): CircleEffect {
    const {name, packageName, targetType, propSchema} = this.meta
    return new CircleEffect(name, packageName, targetType, propSchema, props)
  }
}

export class CircleEffect extends EffectAnimation<typeof CirclePropSchema> {
  computeDuration(cxt: ResolveContext<unknown, unknown>): number {
    return this.timing.duration ?? this.getBlinkDuration()
  }

  getBlinkDuration(): number {
    return this.prop('aniTime') * 2 + this.prop('displayTime')
  }

  clone(): CircleEffect {
    return new CircleEffect(this.name, this.packageName, this.targetType, this.propSchema, {
      id: this.id,
      target: this.target.clone(),
      timing: this.timing.clone(),
      direction: this.props().direction,
      aniTime: this.props().aniTime,
      displayTime: this.props().displayTime,
      stroke: this.props().stroke,
      strokeWidth: this.props().strokeWidth,
    })
  }
}
