import assert from "assert"
import {BaseAniPropSchema} from "@/core/ast/animation/logical/LogicalAnimation"
import {z} from "zod"
import {cloneDeep} from "lodash"

export class ParseContext {
  private readonly _props: z.output<BaseAniPropSchema> & {[key: string]: any} = {}

  processStack: (string | number)[] = []

  constructor(props: z.output<BaseAniPropSchema> & {[key: string]: any} = {}) {
    this._props = props
  }

  basicProp<K extends keyof z.output<BaseAniPropSchema>>(key: K): z.output<BaseAniPropSchema>[K] {
    return this._props[key]
  }

  prop(keyWithDollar: string): any {
    assert(keyWithDollar.startsWith('$'), `A parameter usage call must start with "$", got ${keyWithDollar}`)
    const key = keyWithDollar.substring(1)
    if (!(key in this._props)) {
      throw new Error(`Cannot find parameter '${key}' when parsing ${this.getProcessPath()}`)
    }
    return cloneDeep(this._props[key])
  }

  withProcessPath<T, U>(fn: (params: T) => U,
                        attribute: string,
                        withIndex: number | undefined = undefined,
                        params: T = undefined): U {
    const pathItem = attribute + (typeof withIndex === "number" ? `[${withIndex}]` : '')
    this.processStack.push(pathItem)
    const ret = fn(params)
    this.processStack.pop()
    return ret
  }

  getProcessPath(): string {
    return this.processStack.join('.')
  }
}
