import {BaseAniPropSchema, LogicalAnimation} from "@/core/ast/animation/logical/LogicalAnimation"
import {PhysicalAnimation} from "@/core/ast/animation/physical/PhysicalAnimation"
import {LeafAnimation} from "@/core/ast/animation/logical/LeafAnimation"
import {LoggerManager} from "@/core/utils/Logger"

export type GsapAnimation = gsap.core.Animation

export abstract class GsapPhysicalAnimation<ObjType, LogicalAniType extends LogicalAnimation<BaseAniPropSchema>>
  extends PhysicalAnimation<ObjType, LogicalAniType, GsapAnimation> {

  private readonly logger

  constructor(logical: LogicalAniType) {
    super(logical)
    this.logger = LoggerManager.getLogger("GsapPhysicalAnimation")
  }

  protected mergeDuration(vars: gsap.TweenVars | gsap.TimelineVars = {}): gsap.TweenVars | gsap.TimelineVars {
    return this.mergeVars(vars, {duration: true, yoyo: false, reverse: false})
  }

  protected mergeYoyo(vars: gsap.TweenVars | gsap.TimelineVars = {}): gsap.TweenVars | gsap.TimelineVars {
    return this.mergeVars(vars, {duration: false, yoyo: true, reverse: false})
  }

  /**
   * Merge the timing settings with the given vars
   *
   * @param vars
   * @param options
   * @protected
   */
  protected mergeVars(vars: gsap.TweenVars | gsap.TimelineVars = {},
                      options: {duration: boolean, yoyo: boolean, reverse: boolean} = {duration: true, yoyo: true, reverse: true}
  ): gsap.TweenVars | gsap.TimelineVars {
    const {duration, yoyo, reverse} = options

    const _vars: gsap.TweenVars = { }

    if (duration) {
      _vars.duration = this.duration
    }

    // if (this.stagger != null) {
    //   _vars.stagger = this.stagger
    //   if (!(this.logical instanceof LeafAnimation)) {
    //     this.logger.warn(`Stagger is not supported for non-leaf animation ${this.logical} in gsap implementation.`)
    //   }
    // }

    if (yoyo && this.yoyo != null) {
      _vars.yoyo = true
      _vars.repeat = this.yoyo.repeat
      _vars.repeatDelay = this.yoyo.delay
      if (!(this.logical instanceof LeafAnimation)) {
        this.logger.warn(`Yoyo is not supported for non-leaf animation ${this.logical} in gsap implementation.`)
      }
    }

    if (reverse && this.reverse) {
      _vars.runBackwards = true
      this.logger.warn(`Reverse is not supported in gsap implementation.`)
    }

    return Object.assign(_vars, vars)
  }
}

