import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import assert from "assert"
import {DynamicNumberPropSchema} from "@/preset/template/text/DynamicNumberTemplate"


export class DynamicNumberImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof DynamicNumberPropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // target
    const elements = this.elements
    // timing
    const stagger = this.stagger

    const tl = gsap.timeline()

    elements.forEach((el, i) => {
      assert(el instanceof SVGTextElement, `DynamicNumberImpl: only support SVGTextElement, but got: ${el}`)
      const tw = this.createTw(el, cxt)
      tl.add(tw, i === 0 ? 0 : ('<' + stagger))
    })

    return tl
  }

  createTw(el: SVGTextElement, cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // timing
    const duration = this.duration
    // effect
    const {numStart, numEnd, toFixed, ease} = this.props()

    let formatter: string
    const group = el.innerHTML.match(/([^\d]*)(\d+)(.*)/)
    if (group) {
      formatter = group[1] + '@num' + group[3]
    } else {
      formatter = '@num'
    }

    let computedStart = numStart, computedEnd = numStart
    if (numStart == null || numEnd == null) {
      const group = el.innerHTML.match(formatter.replace('@num', '([\\d.]+)'))
      if (numStart == null) {
        computedStart = parseFloat(group[1])
      }
      if (numEnd == null) {
        computedEnd = parseFloat(group[1])
      }
    }

    const tempNumObj = {tempNum: computedStart}
    const onUpdate = () => {
      const num = tempNumObj.tempNum.toFixed(toFixed)
      el.innerHTML = formatter.replaceAll('@num', num)
    }
    onUpdate()    // call it firstly

    return gsap.to(tempNumObj, {tempNum: computedEnd, ease, duration, onUpdate})
  }
}

