import {CompileExec} from "@/session/CompileExec"
import {TemplateLibrary} from "@/core/library/TemplateLibrary"
import {TargetModel} from "@/core/ast/target/TargetModel"

export class ResolveContext<ObjType, AniType> {
  CHECK_PARAMETERS = true

  config = {
    defaultDuration: 1,
  }

  compileExec: CompileExec<ObjType, AniType>

  constructor(compileExec: CompileExec<ObjType, AniType>) {
    this.compileExec = compileExec
  }

  get templateLibrary(): TemplateLibrary {
    return this.compileExec.templateLibrary
  }

  get targetModel(): TargetModel<ObjType> {
    return this.compileExec.targetModel
  }
}
