import {BaseError} from "@/core/utils/error/BaseError"

export class ParseError extends BaseError {
  static build(msg: string): ParseError {
    return new ParseError('ParseError: ' + msg)
  }

  static buildWithPath(path: string, msg: string): ParseError {
    const message = `Failed to parse spec at path ${path}. ${msg}`
    return ParseError.build(message)
  }

  static buildByChecker(path: string, errors: any[]): ParseError {
    const str = JSON.stringify(errors)
    const message = `ParseError: Failed to parse spec at path ${path}. Errors: \n${str}`
    console.error(message)
    return new ParseError(message)
  }
}
