import {BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {ResolveRule} from "@/core/exec/resolve/ResolveRule"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {LogicalAnimationUtils} from "@/core/ast/animation/logical/utils/LogicalAnimationUtils"
import assert from "assert"
import {LoggerManager} from "@/core/utils/Logger"
import {toTreeString} from "@/core/utils/TreeUtils"

/**
 * Resolves time settings for all animations.
 */
export class ResolveTiming extends ResolveRule {
  private static readonly logger = LoggerManager.getLogger("ResolveTiming")

  constructor() {
    super("ResolveTiming")
  }

  apply(cxt: ResolveContext<unknown, unknown>, input: BaseLogicalAniType): BaseLogicalAniType {
    LogicalAnimationUtils.postorderTraversal(input, node => {
      // todo: check sync and concat offset here

      const duration = node.computeDuration(cxt)
      assert(duration >= 0, `Original duration must be non-negative, `
          + `but got ${duration} in ${node}:\n${toTreeString(node)}`)
      const totalDuration = node.computeTotalDuration(cxt)
      node.computedDuration = duration
      node.computedTotalDuration = totalDuration

      const givenDuration = node.timing.duration
      if (givenDuration != null && givenDuration != totalDuration && totalDuration !== 0) {
        // need rescale
        ResolveTiming.logger.debug(`Rescaling ${node} from ${totalDuration} to ${givenDuration}`)
        node.rescale(cxt, givenDuration / totalDuration)
      }

      return false  // not to stop
    })

    // if (node.timing.duration == null && forceDuration == null) {
    //   this.finalDuration = this.originalDuration
    // } else if (forceDuration == null || this.timing.duration == null) {
    //   // use given duration or forceDuration which is not nul.
    //   const finalDuration = this.timing.duration ?? forceDuration
    //   // rescale children
    //   const rate = finalDuration / this.originalDuration
    //
    // } else {
    //   // this.timing.duration != null && forceDuration != null
    //   throw new Error(`Cannot set both duration and forceDuration (given by parent) in ${this}. Please use only one of them.`)
    // }

    return input
  }
}
