import { computeUUID } from "@/utils/common/uuid";
import vegaChartDirectionJSON from "@/assets/vegaChartDirection.json5";

function csv2JSON(str) {
    var lines = str.split("; ");
    var result = {};

    for (var i = 0; i < lines.length; i++) {
        var currentline = lines[i].split(": ");
        result[currentline[0]] = currentline[1]
    }

    return result; //JSON
}

function updateSVGModel(svgStr, chartId) {
    // assume it's bar chart for now
    const chartType = 'bar-chart'

    const svg = (new DOMParser())
        .parseFromString(svgStr, 'image/svg+xml')
        .querySelector('svg');

    const domElList = Array.from(svg.querySelectorAll('*:not(g,svg,defs,defs *)')).filter(domEl => domEl.ariaHidden != "true");

    const parentElList = Array.from(svg.querySelectorAll('*')).filter(domEl => domEl.ariaRoleDescription != null);
    // axis, rect mark container (with bar in it), legend

    var all_data = []

    domElList.forEach(domEl => {
        domEl.classList.add('chart');
        if (domEl.ariaLabel != null) {
           domEl.setAttribute('ac-data', JSON.stringify(csv2JSON(domEl.ariaLabel)));
           all_data.push(csv2JSON(domEl.ariaLabel))
        }
    })

    parentElList.forEach(domEl => {
        const ariaLabel = domEl.ariaLabel;
        switch (domEl.ariaRoleDescription) {
            case 'axis':
                var XY = domEl.ariaLabel[0].toLowerCase();
                // role-axis-tick, role-axis-label, role-axis-domain, role-axis-title
                
                if (domEl.getElementsByClassName('role-axis-tick').length > 0) {
                    Array.from(domEl.getElementsByClassName('role-axis-tick')).forEach(el => {
                        Array.from(el.querySelectorAll('*:not(g,svg,defs,defs *)')).forEach(domEl => {
                            domEl.classList.add(chartId);
                            domEl.classList.add('axis');
                            domEl.classList.add(XY+'-axis');
                            domEl.classList.add(XY+'-tick');
                        })
                    });
                }

                if (domEl.getElementsByClassName('role-axis-label').length > 0) {
                    Array.from(domEl.getElementsByClassName('role-axis-label')).forEach(el => {
                        Array.from(el.querySelectorAll('*:not(g,svg,defs,defs *)')).forEach(domEl => {
                            if (ariaLabel.includes('discrete')) {
                                // only record categorical data
                                domEl.setAttribute('ac-data', JSON.stringify({'TEXT': domEl.innerHTML}))
                            }
                            domEl.classList.add(chartId);
                            domEl.classList.add('axis');
                            domEl.classList.add(XY+'-axis');
                            domEl.classList.add(XY+'-label');
                        })
                    });
                }

                if (domEl.getElementsByClassName('role-axis-domain').length > 0) {
                    Array.from(domEl.getElementsByClassName('role-axis-domain')).forEach(el => {
                        Array.from(el.querySelectorAll('*:not(g,svg,defs,defs *)')).forEach(domEl => {
                            domEl.classList.add(chartId);
                            domEl.classList.add('axis');
                            domEl.classList.add(XY+'-axis');
                            domEl.classList.add(XY+'-domain');
                        })
                    });
                }
                
                if (domEl.getElementsByClassName('role-axis-title').length > 0) {
                    Array.from(domEl.getElementsByClassName('role-axis-title')).forEach(el => {
                        Array.from(el.querySelectorAll('*:not(g,svg,defs,defs *)')).forEach(domEl => {
                            domEl.classList.add(chartId);
                            domEl.classList.add('axis');
                            domEl.classList.add(XY+'-title');
                        })
                    });
                }

                break;
            case 'rect mark container':
                var barEls = Array.from(domEl.querySelectorAll('*:not(g,svg,defs,defs *)'));
                if (barEls.length > 0) {
                    barEls.forEach(el => {
                        el.classList.add(chartId);
                        el.classList.add('mark');
                        el.classList.add('bar');
                    })
                }
                break;
            case 'line mark container':
                var lineEls = Array.from(domEl.querySelectorAll('*:not(g,svg,defs,defs *)'));
                if (lineEls.length > 0) {
                    lineEls.forEach(el => {
                        el.classList.add(chartId);
                        el.classList.add('mark');
                        el.classList.add('line');
                    })
                }
                break;
            case 'arc mark container':
                var arcEls = Array.from(domEl.querySelectorAll('*:not(g,svg,defs,defs *)'));
                if (arcEls.length > 0) {
                    arcEls.forEach(el => {
                        el.classList.add(chartId);
                        el.classList.add('mark');
                        el.classList.add('sector');
                    })
                }
                break;
            case 'text mark container':
                var textEls = Array.from(domEl.querySelectorAll('*:not(g,svg,defs,defs *)'));
                if (textEls.length > 0) {
                    textEls.forEach(el => {
                        el.classList.add(chartId);
                        el.classList.add('mark');
                        el.classList.add('sector-label');
                    })
                }
                break;
            case 'point':
                domEl.classList.add(chartId);
                domEl.classList.add('mark');
                domEl.classList.add('point');
                break;
            case 'legend':
                // legend label, legend symbol

                if (domEl.getElementsByClassName('role-legend-label').length > 0 && domEl.getElementsByClassName('role-legend-symbol').length > 0) {
                    var labelArr = Array.from(domEl.getElementsByClassName('role-legend-label'));
                    var symbolArr = Array.from(domEl.getElementsByClassName('role-legend-symbol'));
                    labelArr.forEach((labelEl, idx) => {
                        var legend_text = labelEl.textContent.trim();
                        var symbolEl = symbolArr[idx];
                        Array.from(labelEl.querySelectorAll('*:not(g,svg,defs,defs *)')).forEach(domEl => {
                            domEl.setAttribute('ac-data', JSON.stringify({'TEXT': legend_text}))
                            domEl.classList.add(chartId);
                            domEl.classList.add('legend');
                            domEl.classList.add('legend-label')
                        })

                        Array.from(symbolEl.querySelectorAll('*:not(g,svg,defs,defs *)')).forEach(domEl => {
                            domEl.setAttribute('ac-data', JSON.stringify({'TEXT': legend_text}))
                            domEl.classList.add(chartId);
                            domEl.classList.add('legend');
                            domEl.classList.add('legend-symbol')
                        })
                    });
                } 
                break;   
            case 'title':
                Array.from(domEl.querySelectorAll('*:not(g,svg,defs,defs *)')).forEach(domEl => {
                    domEl.classList.add(chartId);
                    domEl.classList.add('title');
                    domEl.classList.add('sub-title')
                })
                break
        }
    });

    // role-axis-grid
    Array.from(svg.getElementsByClassName('role-axis-grid')).forEach(el => {
        Array.from(el.querySelectorAll('*:not(g,svg,defs,defs *)')).forEach(domEl => {
            domEl.classList.add(chartId);
            domEl.classList.add('axis');
            domEl.classList.add('grid');
        })
    })

    // fix id
    const counters = new Map();
    domElList.forEach(domEl => {
        let type = domEl.classList[domEl.classList.length-1]
        if (type) {
            counters.set(type, (counters.get(type) ?? 0) + 1);
        }
    });

    counters.forEach((value, key) => {
      if (value >= 1) {
        domElList
            .filter(domEl => domEl.classList[domEl.classList.length-1] === key)
            .forEach((domEl, i) => domEl.setAttribute('id', `${key}-${i}`));
      }
    });

    // fix id
    domElList.forEach(domEl => {
        const elId = domEl.getAttribute('id');
        if (!elId || elId === '') {
            domEl.setAttribute('id', domEl.classList[domEl.classList.length-1] + '-ac-' + computeUUID());
        }
    });

    // fix class
    domElList.forEach(domEl => {
        if (!domEl.classList.contains(chartId)) {
            domEl.classList.add(chartId);
            // TODO: more unkown types
            if (domEl.tagName === 'rect') {
                domEl.classList.add('background');
                domEl.classList.add('bg-rect')
            } else {
                domEl.classList.add('unknown-type')
            }
        }
    })

    const elements = getElements(domElList, all_data);
    
    // TODO: determine direction automatically
    const layout = vegaChartDirectionJSON[chartId] === undefined ? {
        "xAxis": "left",
        "yAxis": "unknown",
        "title": "bottom"
      } : vegaChartDirectionJSON[chartId];

    const targetModel = { chartType: chartType, elements, layout, target_type: 'vega' };
    const newSvgStr = domElToString(svg);

    // Array.from(svg.querySelectorAll('*:not(svg *)')).filter(domEl => domEl.classList.length > 0).forEach(domEl => {
    //     domEl
    //     return
    // });

    return {
        targetModel,
        svgStr: newSvgStr,
    };
}

function domElToString(domEl) {
    const tmp = document.createElement("div");
    tmp.appendChild(domEl);
    return tmp.innerHTML;
}

function getChartData(domEl) {
    let data = domEl.getAttribute('ac-data');
    if (!data) {
        return null;
    }
    return JSON.parse(data);
}

function getElements(domElList, all_data) {
    const elements = [];
    domElList.forEach(domEl => {
        const el = {};
        const id = domEl.getAttribute('id');
        const clazz = domEl.getAttribute('class');

        const clazzList = clazz.split(' ');
        const tag = domEl.tagName;
        const data = getChartData(domEl);

        el.type = clazzList[clazzList.length - 1];
        el.id = id;
        el.clazz = clazz;
        el.data = data;
        el.tag = tag;

        if (tag === 'text') {
            if (domEl.children.length !== 0) {
                el.text = Array.from(domEl.children)
                    .reduce((str, node) => str + ' ' + node.innerHTML, '')
                    .trim();
            } else {
                el.text = domEl.innerHTML;
            }
        } else {
            el.text = null;
        }

        if (el.data != null && 'TEXT' in el.data) {
            // TODO: store into el.data before get stage
            el.data = all_data.filter(d => JSON.stringify(d).toLowerCase().includes(el.data['TEXT'].toLowerCase()));
        }

        elements.push(el);
    });
    return elements;
}

export const VegaTargetUtils = {
    updateSVGModel, domElToString,
}