import {Transform, TransformType} from "@/core/ast/target/Transform"
import {TargetSelection} from "@/core/ast/target/TargetSelection"

/**
 * SelectAll can only be used for tree-like virtual model
 */
export class SelectAllTransform extends Transform {
  readonly selector: string

  constructor(selector: string) {
    super()
    this.selector = selector
  }

  get type(): TransformType {
    return "selectAll"
  }

  execute(selection: TargetSelection): TargetSelection {
    return selection.selectAll(this.selector)
  }

  toString() {
    return `selectAll(${this.selector})`
  }

  clone(): SelectAllTransform {
    return new SelectAllTransform(this.selector)
  }
}
