import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const TypePropSchema = EffectPropSchema.extend({
  type: z.enum(['in', 'out'])
      .default('in')
      .describe("Type of animation (in or out, default: in)"),
  effect: z.enum(['fade', 'show'])
      .default('show')
      .describe("Effect of animation (default: show)"),
  typeStagger: z.number()
      .default(0.1)
      .describe("Stagger of animation for each character (default: 0.1)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class TypeTemplate extends EffectTemplate<typeof TypePropSchema> {
  constructor() {
    super({
      name: "Type",
      packageName: "gaia.text",
      imports: [],
      targetType: "Text",
      propSchema: TypePropSchema,
    })
  }
}
