import {Target} from "@/core/ast/target/Target"
import {Timing} from "@/core/ast/animation/other/Timing"
import {Proto, PType} from "@/module/Proto"
import {ResolveError} from "@/core/utils/error/ResolveError"
import {z} from "zod"
import {TreeLike} from "@/core/utils/TreeUtils"
import {Import} from "@/core/ast/animation/other/Import"
import {nanoid} from "nanoid"
import {isNumber, isObject, isString, omit} from "lodash"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {RefAnimation} from "@/core/ast/animation/logical/impl/RefAnimation"
import {BaseTemplateType} from "@/core/ast/template/BaseTemplate"


export interface AniMetaSchema<PropType extends typeof AniPropSchema> {
  name: string,
  packageName: string,
  imports: Import[],
  targetType: string,
  propSchema: PropType
}

export const AniPropSchema = z.object({
  id: z.string()
      .or(z.number())
      .default(() => nanoid(10)),
  target: z.instanceof(Target)
      .or(z.string())
      .default(Target.build())
      .transform(val => {
        if (isString(val)) {
          return Target.build().selectAll(val)
        }
        return val
      }).default(Target.build())
      .describe("The target of the animation"),
  timing: z.instanceof(Timing)
      .or(z.number())
      .default(Timing.build())
      .transform(val => {
        if (isNumber(val)) return Timing.build({duration: val})
        return val
      })
      .describe("The timing of the animation"),
})

export type BaseAniPropSchema = typeof AniPropSchema
export type BaseLogicalAniType = LogicalAnimation<BaseAniPropSchema>

export abstract class LogicalAnimation<PropType extends BaseAniPropSchema>
  extends Proto<LogicalAnimation<PropType>>
  implements TreeLike {

  private readonly _meta: AniMetaSchema<PropType>

  _rawProps: z.input<PropType>
  _props: z.infer<PropType> = null

  _templateSrc: BaseTemplateType = null
  _refSrc: RefAnimation<PropType> = null

  // set when resolving timing
  computedDuration: number = null
  computedTotalDuration: number = null

  constructor(meta: AniMetaSchema<PropType>, props: z.input<PropType>) {
    super()

    this._meta = meta

    this._rawProps = props
    this._props = this.parseProps(props)

    this.init()
  }

  protected init(): void {
    // do nothing
  }

  abstract visitChildren(): LogicalAnimation<BaseAniPropSchema>[]

  get type(): PType {
    return PType.LogicalAnimation
  }

  get name(): string {
    return this._meta.name
  }

  get packageName(): string {
    return this._meta.packageName
  }

  get imports(): Import[] {
    return this._meta.imports
  }

  get targetType(): string {
    return this._meta.targetType
  }

  get propSchema(): PropType {
    return this._meta.propSchema
  }

  setSrc(refSrc: RefAnimation<PropType>, templateSrc: BaseTemplateType): void {
    this._refSrc = refSrc
    this._templateSrc = templateSrc
  }

  props(): z.infer<PropType> {
    return this._props
  }

  prop<K extends keyof z.infer<PropType>>(key: K): z.infer<PropType>[K] {
    return this._props[key]
  }

  get id(): string | number {
    return this.prop('id')
  }

  get target(): Target {
    return this.prop('target')
  }

  get timing(): Timing {
    return this.prop('timing')
  }

  get refSrc(): RefAnimation<PropType> | null {
    return this._refSrc
  }

  get initialRefSrc(): RefAnimation<BaseAniPropSchema> | null {
    let result = this._refSrc as RefAnimation<BaseAniPropSchema>
    if (result === null) {
      return null
    }
    while (result.refSrc !== null) {
      result = result.refSrc as RefAnimation<BaseAniPropSchema>
    }
    return result
  }

  get templateSrc(): BaseTemplateType {
    return this._templateSrc
  }

  private parseProps(props: z.input<PropType>): z.infer<PropType> {
    const result = this.propSchema.safeParse(props)
    if (result.success !== true) {
      throw ResolveError.build(result.error.message)
    }
    return result.data
  }

  /**
   * Computes the duration of the animation according to children.
   * It uses computedTotalDuration of children so children must be resolved first.
   */
  abstract computeDuration(resolveContext: ResolveContext<unknown, unknown>): number
  abstract rescale(resolveContext: ResolveContext<unknown, unknown>, rate: number): void

  computeTotalDuration(cxt: ResolveContext<unknown, unknown>): number {
    let dur = this.computeDuration(cxt)
    if (this.timing.yoyo) {
      const {repeat, delay} = this.timing.yoyo
      dur = dur * (repeat + 1) + delay * repeat
    }
    const stagger = this.timing.stagger ?? 0
    const staggerCnt = Math.max(0, this.target.selection.size - 1)
    return dur + staggerCnt * stagger
  }

  toString(){
    return `${this.name}(${this.id})`
  }

  toOneLineString(): string {
    return `${this.name}`
        + `${this.refSrc === null ? '' : ` ref=${this.templateSrc.name}`}`
        + ` target=${this.getTargetStr()}`
        + ` timing=${this.getTimingStr()}`
        + ` props=${this.getPropsStr()}`
  }

  getTargetStr(): string {
    const transformsStr = this.target.transforms.toString()
    const elementsStr = this.target.selection ? this.target.selection.toString() : '?'
    return `[${transformsStr}]->[${elementsStr}]`
  }

  getTimingStr(): string {
    const attrList: [key: string, value: string | boolean][] = []

    attrList.push(['computedDur', this.computedDuration ? (this.computedDuration + 's') : '?'])
    attrList.push(['computedTotDur', this.computedTotalDuration ? (this.computedTotalDuration + 's') : '?'])
    if (this.timing.duration) {
      attrList.push(['dur', this.timing.duration + 's'])
    }
    if (this.timing.offset) {
      attrList.push(['offset', this.timing.offset + 's'])
    }
    if (this.timing.stagger) {
      attrList.push(['stagger', this.timing.stagger + 's'])
    }
    if (this.timing.reverse) {
      attrList.push(['reverse', this.timing.reverse])
    }
    if (this.timing.yoyo) {
      const yoyo = this.timing.yoyo
      attrList.push(['yoyo', `(delay=${yoyo.delay}s,repeat=${yoyo.repeat})`])
    }
    const attrStr = attrList.map(([k, v]) => `${k}=${v}`).join(', ')

    return `[${attrStr}]`
  }

  getPropsStr(): string {
    const otherProps = omit(this.props(), ['id', 'target', 'timing'])
    const otherPropStr = Object.entries(otherProps)
        .map(([k, v]) => `${k}=${isObject(v) ? JSON.stringify(v) : v}`)
        .join(', ')
    return `[${otherPropStr}]`
  }
}
