import {Transform, TransformType} from "@/core/ast/target/Transform"
import {TargetSelection} from "@/core/ast/target/TargetSelection"
import {isArray} from "lodash"
import {LoggerManager} from "@/core/utils/Logger"
import {TargetModel, VirtualElementType} from "@/core/ast/target/TargetModel"

export class SortTransform extends Transform {
  private static readonly logger = LoggerManager.getLogger('SortTransform')

  sortBy: string
  order: (number | string)[] | 'desc' | 'asc'

  constructor(sortBy: string, order: (number | string)[] | 'desc' | 'asc' = 'asc') {
    super()
    this.sortBy = sortBy
    this.order = order
  }

  get type(): TransformType {
    return 'sort'
  }

  execute(selection: TargetSelection): TargetSelection {
    const sortBy = this.sortBy
        .replaceAll('@id', '__el__.id')
        .replaceAll('@class', 'Array.from(__el__.classList)')
        .replaceAll('@index', '__idx__')
        .replaceAll('@data', '(__el__.data ?? {})')
        .replaceAll('@el', '__el__')
        .replaceAll('@coll', '__collection__')
    SortTransform.logger.debug(`Sort ${selection} with sortBy: ${sortBy}, order: ${this.order}.`)
    const elements = selection.elements
    const keyMap = new Map<VirtualElementType, number | string>()
    for (let idx = 0; idx < elements.length; idx++) {
      const group = elements[idx]
      let value
      try {
        value = mapperEval(sortBy, group, idx, elements, selection.targetModel)
      } catch (e) {
        SortTransform.logger.error(`Error while evaluating sortBy: ${sortBy} on group ${group}.`, e)
        throw e
      }
      keyMap.set(group, value)
    }
    if (this.order === 'asc') {
      return selection.sort((a, b) => (keyMap.get(a) as number) - (keyMap.get(b) as number))
    } else if (this.order === 'desc') {
      return selection.sort((a, b) => (keyMap.get(b) as number) - (keyMap.get(a) as number))
    } else if (isArray(this.order)) {
      return selection.sort((a, b) => {
        const keyA = keyMap.get(a)
        const keyB = keyMap.get(b)
        // todo type check
        const idxA = this.order.indexOf(keyA as any)
        const idxB = this.order.indexOf(keyB as any)
        return idxA - idxB
      })
    }

  }

  toString() {
    return `sort(${this.sortBy}, ${isArray(this.order) ? `[${this.order.join(', ')}]` : this.order})`
  }

  clone(): Transform {
    return new SortTransform(this.sortBy, this.order)
  }
}

/* eslint-disable @typescript-eslint/no-unused-vars */
function mapperEval(__key__: string,
                    __el__: VirtualElementType,
                    __idx__: number,
                    __collection__: VirtualElementType[],
                    __model__: TargetModel<unknown>): number | string {
  return eval(__key__)
}
/* eslint-enable @typescript-eslint/no-unused-vars */
