import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {CutPropSchema} from "@/preset/template/common/CutTemplate"
import {createOrGetG, getRelativeBox} from "@/core/utils/SvgUtils"
import {BaseSVGTargetModel} from "@/core/ast/target/impl/svg/BaseSVGTargetModel"

/**
 * Cut animation = Wipe + Move
 *
 * E.g., CutIn from top = WipeIn from bottom + Move from top
 */
export class CutImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof CutPropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    const model = cxt.targetModel as BaseSVGTargetModel

    const tl = gsap.timeline()
    const stagger: number = this.stagger
    this.elements.forEach((el, i) => {
      const tw = this.createTw(el, model)
      const position = i === 0 ? 0 : ('<' + stagger)
      tl.add(tw, position)
    })

    return tl
  }

  createTw(el: SVGElement, model: BaseSVGTargetModel): GsapAnimation {
    const {type, from, ease} = this.props()
    const svg = model.getOrigin()

    // save original box because it changes the position
    model.saveOriginalBox(el as SVGGraphicsElement)

    const {left, top, right, bottom, totalWidth, totalHeight} = getRelativeBox(el as SVGGraphicsElement)
    const [[ltx, lty], [rtx, rty], [lbx, lby], [rbx, rby]] = this.getOffsetFactorMatrix(from)
    const vars = this.mergeVars({ease})
    const gClipPath = createOrGetG(svg, el as SVGGraphicsElement, `clip-path-${el.id}`, ['cut'])
    const gTransform = createOrGetG(svg, el as SVGGraphicsElement, `transform-${el.id}`, ['cut'])

    const tl = gsap.timeline()

    const hiddenClipPath = `polygon(`
        + `${ltx * totalWidth + left}px ${lty * totalHeight + top}px, `
        + `${rtx * totalWidth + right}px ${rty * totalHeight + top}px, `
        + `${rbx * totalWidth + right}px ${rby * totalHeight + bottom}px, `
        + `${lbx * totalWidth + left}px ${lby * totalHeight + bottom}px)`
    const shownClipPath = `polygon(${left}px ${top}px, ${right}px ${top}px, ${right}px ${bottom}px, ${left}px ${bottom}px)`

    const moveVars = this.getMoveVars(from, totalWidth, totalHeight)

    switch (type) {
      case 'in':
        tl.fromTo(gClipPath, { clipPath: hiddenClipPath }, { clipPath: shownClipPath, ...vars }, 0)
        tl.fromTo(gTransform, { ...moveVars }, { x: 0, y: 0, ...vars }, 0)
        break
      case 'out':
        tl.fromTo(gClipPath, { clipPath: shownClipPath }, { clipPath: hiddenClipPath, ...vars }, 0)
        tl.fromTo(gTransform, { x: 0, y: 0 }, { ...moveVars, ...vars }, 0)
        break
      default:
        // never goes here as type is checked by zod
        throw new Error(`Unknown type: ${type}`)
    }

    return tl
  }

  getMoveVars(from: string, totalWidth: number, totalHeight: number): {x?: number, y?: number} {
    switch (from) {
      case 'top': return {x: 0, y: -totalHeight}
      case 'bottom': return {x: 0, y: totalHeight}
      case 'left': return {x: -totalWidth, y: 0}
      case 'right': return {x: totalWidth, y: 0}
      default:
        throw new Error(`Wrong value of from: ${from}. Must be 'top', 'bottom', 'left' or 'right'. At ${this}`)
    }
  }



  /**
   * @param from top, bottom, left, right
   * @return widthFactor, heightFactor for each point (-1, 0, 1)
   */
  getOffsetFactorMatrix(from: string): number[][] {
    switch (from) {
      case 'bottom':    // top for Wipe
        return [
          [0, 0], [0, 0],     // left top, right top
          [0, -1], [0, -1],   // left bottom, right bottom
        ]
      case 'top':      // bottom for Wipe
        return [
          [0, 1], [0, 1],
          [0, 0], [0, 0],
        ]
      case 'right':    // left for Wipe
        return [
          [0, 0], [-1, 0],
          [0, 0], [-1, 0],
        ]
      case 'left':    // right for Wipe
        return [
          [1, 0], [0, 0],
          [1, 0], [0, 0],
        ]
      default:
        throw new Error(`Wrong value of from: ${from}. Must be 'top', 'bottom', 'left' or 'right'. At ${this}`)
    }
  }
}

