import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {HighlightPropSchema} from "@/preset/template/common/HighlightTemplate"
import {createOrGetG} from "@/core/utils/SvgUtils"


export class HighlightImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof HighlightPropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // target
    // const elements = this.elements
    // other target
    const {contrastTarget} = this.props()
    const contrastElements = this.logical.target.selection.parent
        .filter(contrastTarget)
        .realElements as SVGElement[]

    // don't support stagger

    const tl = gsap.timeline()
    contrastElements.forEach((el, i) => {
      tl.add(this.createTw(el, cxt), 0)
    })
    return tl
  }

  createTw(el: SVGElement, cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // effect
    const {contrastOpacity, ease} = this.props()

    const model = cxt.targetModel
    const svg = model.getOrigin() as SVGSVGElement
    const gOpacity = createOrGetG(svg, el as SVGGraphicsElement, `opacity-${el.id}`, ['highlight'])

    const vars = this.mergeVars({ease})
    return gsap.fromTo(gOpacity, {opacity: 1}, {...vars, opacity: contrastOpacity})
  }
}

