import {BaseLogicalAniType, LogicalAnimation} from "@/core/ast/animation/logical/LogicalAnimation"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {z} from "zod"
import {TreeLike} from "@/core/utils/TreeUtils"

export abstract class PhysicalAnimation<ObjType, LogicalAniType extends LogicalAnimation<any>, AniType> implements TreeLike {
  private readonly _logical: LogicalAniType

  children: PhysicalAnimation<ObjType, BaseLogicalAniType, AniType>[]

  constructor(logical: LogicalAniType) {
    this._logical = logical
  }

  get logical(): LogicalAniType {
    return this._logical
  }

  get duration(): number {
    return this._logical.computedDuration
  }

  get totalDuration(): number {
    return this._logical.computedTotalDuration
  }

  get stagger(): number | null {
    return this._logical.timing.stagger
  }

  get yoyo(): {repeat: number, delay: number} | null {
    return this._logical.timing.yoyo
  }

  get reverse(): boolean {
    return this._logical.timing.reverse
  }

  get elements(): ObjType[] {
    return this._logical.target.selection.realElements as ObjType[]
  }

  props(): z.infer<LogicalAniType['propSchema']> {
    return this._logical.props()
  }

  prop<K extends keyof z.infer<LogicalAniType['propSchema']>>(key: K): z.infer<LogicalAniType['propSchema']>[K] {
    return this._logical.prop(key)
  }

  generate(cxt: SynthesisContext<ObjType, AniType>): AniType {
    return this.doGenerate(cxt)
  }

  abstract doGenerate(cxt: SynthesisContext<ObjType, AniType>): AniType

  toString(): string {
    return `PhysicalAnimation ${this._logical.toString()}`
  }

  toOneLineString(): string {
    return `PhysicalAnimation ${this._logical.toOneLineString()}`
  }

  visitChildren(): TreeLike[] {
    return this.children
  }
}

