export enum PType {
  AnimationTemplate,
  LogicalAnimation,
  TargetType,
  Binder,
}

export abstract class Proto<T extends Proto<T>> {
  abstract get name(): string
  abstract get type(): PType
  abstract get packageName(): string   // full package name

  get fullName(): string {
    if (this.packageName === '') {
      return this.name
    } else {
      return `${this.packageName}.${this.name}`
    }
  }

  abstract clone(): T

  toString(): string {
    return `<${this.name}>`
  }
}

