import JSON5 from "json5"
import {BaseSVGTargetModel} from "@/core/ast/target/impl/svg/BaseSVGTargetModel"
import {TargetSelection} from "@/core/ast/target/TargetSelection"
import {VirtualElementType} from "@/core/ast/target/TargetModel"
import assert from "assert"

export class SSVGTargetModel extends BaseSVGTargetModel {
  static readonly TYPE_ATTR = 'g-type'    // selector applied on target model
  static readonly CLASS_ATTR = 'g-class'    // extra class added to target model
  static readonly DATA_ATTR = 'g-data'
  static readonly TARGET_TYPE_ATTR = 'g-target-type'

  protected vNodeId2RealId: Map<string, string> = new Map()

  constructor(origin: SVGSVGElement) {
    super(origin)
  }

  initRootType() {
    const rootEl = this.origin.querySelector(`[${SSVGTargetModel.TARGET_TYPE_ATTR}]`)
    let targetTypeName
    if (rootEl == null || rootEl.getAttribute(SSVGTargetModel.TARGET_TYPE_ATTR) == '') {
      targetTypeName = 'Any'
    } else {
      targetTypeName = rootEl.getAttribute(SSVGTargetModel.TARGET_TYPE_ATTR)
    }
    this.rootTargetType = this.session.typeLibrary.get(targetTypeName)
    if (this.rootTargetType == null) {
      throw new Error(`Target type ${targetTypeName} not found when processing SSVG root target type.`)
    }
  }

  completeVirtualModel() {
    let idCnt = 0
    const getNextId = () => {
      idCnt++
      let _id = `el-${idCnt}`
      while (this.realElementIdSet.has(_id)) {
        idCnt++
        _id = `el-${idCnt}`
      }
      return _id
    }

    if (this.rootTargetType.name === 'Any') {
      // add id in origin obj
      if (this.origin.id == null || this.origin.id == '') {
        this.origin.id = getNextId()
      }
      Array.from(this.origin.querySelectorAll('*')).forEach(el => {
        if (el.id == null || el.id == '') {
          el.id = getNextId()
        }
        this.vNodeId2RealId.set(el.id, el.id)
      })

      const vm = this.virtualModel
      const vEl = this.origin.cloneNode(true) as VirtualElementType
      vm.appendChild(vEl)

      // create selection
      this.rootSelection = new TargetSelection([vEl], this, null)
      return
    }

    const elements = this.origin.querySelectorAll<SVGElement>(`[${SSVGTargetModel.TYPE_ATTR}]`)
    elements.forEach(el => {
      const typeSelector = el.getAttribute(SSVGTargetModel.TYPE_ATTR)
      const dataStr = el.getAttribute(SSVGTargetModel.DATA_ATTR)

      // process id
      let finalId
      const isElIdValid = el.id != null && el.id != ''
      if (isElIdValid) {
        finalId = el.id
      } else {
        finalId = getNextId()
        el.id = finalId
      }
      this.vNodeId2RealId.set(finalId, el.id)

      // process classList
      const typeNode = this.queryOneTypeNode(typeSelector)
      const classList = typeNode.classList
      if (el.hasAttribute(SSVGTargetModel.CLASS_ATTR)) {
        const extraClassList = el.getAttribute(SSVGTargetModel.CLASS_ATTR).split(' ')
        extraClassList.forEach(c => {
          if (!classList.includes(c)) {
            classList.push(c)
          }
        })
      }

      // parse data
      let data = {}
      if (dataStr != null && dataStr != '') {
        data = JSON5.parse(dataStr)
      }

      const vEl = this.createVirtualElement(el, finalId, classList, data)
      this.virtualModel.appendChild(vEl)
    })

    const virtualElements = Array.from(this.virtualModel.children) as VirtualElementType[]
    this.rootSelection = new TargetSelection(virtualElements, this, null)
  }

  getRealElementById(id: string): SVGElement {
    const realId = this.vNodeId2RealId.get(id)
    assert(realId != null, `Cannot find real id for virtual id ${id}.`)
    return this.origin.querySelector(`#${realId}`)
  }
}
