import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const FadePropSchema = EffectPropSchema.extend({
  type: z.enum(['in', 'out'])
      .default('in')
      .describe("Fade type of animation (default: in)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class FadeTemplate extends EffectTemplate<typeof FadePropSchema> {
  constructor() {
    super({
      name: "Fade",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: FadePropSchema,
    })
  }
}
