import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const DynamicNumberPropSchema = EffectPropSchema.extend({
  numStart: z.number()
      .nullable()
      .default(0)
      .describe("Start number of animation (default: 0). If null, the start number will be the current number of the target."),
  numEnd: z.number()
      .nullable()
      .default(null)
      .describe("End number of animation (default: null). If null, the end number will be the current number of the target."),
  toFixed: z.number()
      .default(0)
      .describe("Number of digits after the decimal point (default: 0)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class DynamicNumberTemplate extends EffectTemplate<typeof DynamicNumberPropSchema> {
  constructor() {
    super({
      name: "DynamicNumber",
      packageName: "gaia.text",
      imports: [],
      targetType: "Text",
      propSchema: DynamicNumberPropSchema,
    })
  }
}
