import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const SectorWheelPropSchema = EffectPropSchema.extend({
  type: z.enum(['in', 'out'])
      .default('in')
      .describe("Type of animation (default: in)"),
  centerTarget: z.string()
      .default('')
      .describe("Filter transform for elements that used for center computing. Default: '', means using same elements in selected by target."),
  centerPos: z.string()
      .default('')
      .describe("Center position of animation (default: ''. E.g., 50% 50%). If set, centerTarget will be ignored."),
  startAngle: z.number()
      .default(0)
      .describe("Start angle of animation (default: 0)"),
  sweepAngle: z.number()
      .default(360)
      .describe("Sweep angle of animation (default: 360)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class SectorWheelTemplate extends EffectTemplate<typeof SectorWheelPropSchema> {
  constructor() {
    super({
      name: "SectorWheel",
      packageName: "gaia.chart",
      imports: [],
      targetType: "Any",
      propSchema: SectorWheelPropSchema,
    })
  }
}
