import {Notification} from 'element-ui';
import {
  queryStaticSVG,
  queryTargetModel,
  queryStaticAudio,
  queryTranscript,
  queryProjectSpec,
} from "@/service/projService";
import JSON5 from 'json5';
import PROJECT_LIST from '@/assets/projects.json5';
import {VegaTargetUtils} from "@/utils/target/VegaTargetUtils";

// initial state
const state = () => ({
  projectList: PROJECT_LIST,

  staticSvgMap: new Map(),
  staticSvgMapSign: false,

  projectId: null,    // default project
  projectSpec: null,    // list of single spec

  svgStrMap: new Map(),
  audioStrMap: new Map(),
  transcriptMap: new Map(),
  targetModelMap: new Map(),
  extraProjectSpecMap: new Map(),
  linkingMap: new Map(),

  audioEl: null,
})

// getters
const getters = {
}

// actions
const actions = {
  async queryProjectSpec2({state, commit, dispatch}, projectId) {
    function queryAllSceneData(projSpec) {
      const promiseList = [];
      const projectId = projSpec.projectId;
      const svgFileName = projSpec.chart;
      // const transcriptFileName = projSpec.transcript;
      const audioFileName = projSpec.audio;
      promiseList.push(dispatch('queryStaticSVG', {projectId, svgFileName}));
      // promiseList.push(dispatch('queryTranscript', {projectId, fileName: transcriptFileName}));
      promiseList.push(dispatch('queryStaticAudio', {projectId, fileName: audioFileName}));
      // promiseList.push(dispatch('queryTargetModel', projectId));
      return Promise.all(promiseList);
    }

    if (state.extraProjectSpecMap.has(projectId)) {
      commit('changeProjectId', projectId);
      commit('initResources');
      const {svgStr, fakeProjectSpec} = state.extraProjectSpecMap.get(projectId);
      commit('changeProjectSpec', fakeProjectSpec);
      commit('addSVGStr', {projectId, svgStr});
      commit('addStaticSVGStr', {projectId, svgStr});

      commit('project/initProjectView', null, {root: true});
      commit('editor/changeSceneById', 0, {root: true});
      commit('editor/initWorkspace', true, {root: true});   // load example (a special empty aniamtion spec)
    } else {
      commit('changeProjectId', projectId);
      commit('initResources');

      const projectFileName = projectId + '.json5';
      queryProjectSpec(projectFileName, resp => {
        const projectSpec = typeof resp.data === 'string' ? JSON5.parse(resp.data) : resp.data;
        commit('changeProjectSpec', projectSpec);
        queryAllSceneData(projectSpec).then(() => {
          commit('project/initProjectView', null, {root: true});
          commit('editor/changeSceneById', 0, {root: true});
          commit('editor/initWorkspace', true, {root: true});
        });
      }, errorResp => {
        Notification({title: "Fail to get sample file.", message: errorResp.message, type: 'error'})
        console.error(errorResp);
      })
    }
// In 2017, 94% of defendants were released pretrial, 88% of those returned for every single court date, 86% weren't arrested during that time.

  },
  async queryAllSvg({state, commit}) {
    state.projectList.forEach(project => {
      const projectId = project.id;

      if (state.staticSvgMap.has(projectId)) {
        return;
      }

      const projectFileName = projectId + '.json5';

      queryProjectSpec(projectFileName, resp => {
        console.log("queryProjectSpec success: " + projectFileName)
        const projectSpec = typeof resp.data === 'string' ? JSON5.parse(resp.data) : resp.data;
        const svgFileName = projectSpec.chart

        if (svgFileName !== '') {
          const srcId = svgFileName.trim().replace('./', '').replace('.svg', '');
          const fileName = srcId + '.svg';
          queryStaticSVG(fileName, resp => {
            commit('addStaticSVGStr', {projectId, svgStr: resp.data});
          }, errorResp => {
            commit('addStaticSVGStr', {projectId, svgStr: ''});
            console.error(errorResp);
          })
        }
      }, errorResp => {
        console.error(errorResp);
      })
    })
  },
  queryStaticSVG({commit}, {projectId, svgFileName}) {
    return queryStaticSVG(svgFileName, resp => {
      commit('addSVGStr', {projectId, svgStr: resp.data});
    }, errorResp => {
      commit('addSVGStr', {projectId, svgStr: ''});
      Notification({title: `Chart resource ${svgFileName} not found.`,
        message: errorResp.message, type: 'warning'})
      console.error(errorResp);
    })
  },
  queryStaticAudio({commit}, {projectId, fileName}) {
    return queryStaticAudio(fileName, resp => {
      commit('addAudioStr', {projectId, audioStr: resp.data});
    }, errorResp => {
      commit('addAudioStr', {projectId, audioStr: ''});
      Notification({title: "Fail to get static audio.", message: errorResp.message, type: 'error'})
      console.log(errorResp)
    })
  },
  queryTranscript({commit}, {projectId, fileName}) {
    return queryTranscript(fileName, resp => {
      commit('addTranscript', {projectId, transcript: resp.data});
    }, errorResp => {
      commit('addTranscript', {projectId, transcript: {}});
      Notification({title: `Transcript resource ${fileName} not found.`,
        message: errorResp.message, type: 'warning'})
      console.error(errorResp);
    })
  },
  queryTargetModel({commit}, srcId) {
    const fileName = srcId + '.json';
    return queryTargetModel(fileName, resp => {
      commit('addTargetModel', {srcId, targetModel: resp.data});
    }, errorResp => {
      commit('addTargetModel', {srcId, targetModel: null});
      if (fileName.includes('vega')) {
        return;
      }
      Notification({title: `Target model ${fileName} not found.`,
        message: errorResp.message, type: 'warning'})
      console.log(errorResp);
    })
  },
  addProject({state, commit}, {srcId, svgStr}) {
    // chartId = srcId
    // let projectId = srcId.toLowerCase().replace(/\s+/g, '-');
    let projectId = srcId

    // add count no. to project id
    let no = 0;
    const projectIdSet = new Set(state.projectList.map(p => p.id));
    let newProjectId = projectId;
    while (projectIdSet.has(newProjectId)) {
      no += 1;
      newProjectId = projectId + '-' + no;
    }
    projectId = newProjectId;

    state.projectList.unshift({id: projectId, label: projectId, group: "Uploaded"});
    // generate svg, empty transcript, empty audio, target model
    const projectSpec = {
      svgStr: svgStr,
      fakeProjectSpec: {
        projectId,
        chart: `./${projectId}.svg`,
        animation: {
          main: {
            target: {selectAll: '*:not(g)'},
            sync: [],
          }
        },
        templates: [],
      }
    }
    state.extraProjectSpecMap.set(projectId, projectSpec)
    commit('addStaticSVGStr', {projectId, svgStr})
    commit('changeProjectId', projectId);
  },
}

// mutations
const mutations = {
  changeProjectId(state, projectId) {
    state.projectId = projectId;
  },
  initResources(state) {
    state.projectSpec = null;

    state.svgStrMap = new Map();
    state.audioStrMap = new Map();
    state.transcriptMap = new Map();
    state.targetModelMap = new Map();
  },
  changeProjectSpec(state, projectSpec) {
    state.projectSpec = projectSpec;
  },

  addSVGStr(state, {projectId, svgStr}) {
    state.svgStrMap.set(projectId, svgStr);
  },
  addStaticSVGStr(state, {projectId, svgStr}) {
    state.staticSvgMap.set(projectId, svgStr);
    setTimeout(() => this.commit('resource/changeStaticSvgMapSign'), 0);
  },
  changeStaticSvgMapSign(state) {
    state.staticSvgMapSign ^= true;
  },

  addAudioStr(state, {projectId, audioStr}) {
    state.audioStrMap.set(projectId, audioStr);
  },
  addTranscript(state, {projectId, transcript}) {
    state.transcriptMap.set(projectId, transcript);
  },
  addTargetModel(state, {projectId, targetModel}) {
    state.targetModelMap.set(projectId, targetModel);
  },
  addLinking(state, {srcId, linking}) {
    state.linkingMap.set(srcId, linking);
  },

  updateSVGModel(state, {chartId, svgStr, targetModel}) {
    state.svgStrMap.set(chartId, svgStr);
    state.targetModelMap.set(chartId, targetModel);
  },

  registerAudioEl(state, audioEl) {
    state.audioEl = audioEl
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
