import {Proto, PType} from "@/module/Proto"
import {Import} from "@/core/ast/animation/other/Import"

export class TargetTypeComponent {
  readonly ref: string
  readonly classList: string[]
  readonly required: boolean

  constructor(ref: string, classList: string[], required: boolean) {
    this.ref = ref
    this.classList = classList
    this.required = required
  }

  static build(type: string, classList: string[], required = false) {
    return new TargetTypeComponent(type, classList, required)
  }
}

export class TargetType extends Proto<TargetType> {
  _name: string
  _packageName: string

  readonly imports: Import[]
  readonly classList: string[]
  readonly components: TargetTypeComponent[]

  constructor(name: string, packageName: string,
              imports: Import[], classList: string[], components: TargetTypeComponent[]) {
    super()
    this._name = name
    this._packageName = packageName
    this.imports = imports
    this.classList = classList
    this.components = components
  }

  get name(): string {
    return this._name
  }

  get packageName(): string {
    return this._packageName
  }

  get type(): PType {
    return PType.TargetType
  }

  isLeaf(): boolean {
    return this.components.length === 0
  }

  clone(): TargetType {
    throw new Error("Not implemented")
    // return new TargetType(this.name, this.packageName, this.imports, this.components)
  }

  static build(name: string, packageName: string,
               {imports = [], classList = [], components = []}:
                   {classList: string[], imports: Import[], components: TargetTypeComponent[]}) {
    return new TargetType(name, packageName, imports, classList, components)
  }
}
