import {Transform, TransformType} from "@/core/ast/target/Transform"
import {TargetSelection} from "@/core/ast/target/TargetSelection"

export class AtTransform extends Transform {
  readonly index: number

  constructor(index: number) {
    super()
    this.index = index
  }

  get type(): TransformType {
    return "at"
  }

  execute(selection: TargetSelection): TargetSelection {
    return selection.slice(this.index, this.index + 1)
  }

  toString() {
    return `at(${this.index})`
  }

  clone(): AtTransform {
    return new AtTransform(this.index)
  }
}
