<template>
  <div class="ani-svg-container-master">
    <svg ref="svg">
      <g class="scene"/>
    </svg>
  </div>
</template>

<script>
  import {mapState} from "vuex";

  export default {
    name: 'AniSVGContainer',
    data() {
      return {
        svg: null,
        scene: null,
      }
    },
    methods: {
      loadSVG() {
        // reset svg transform
        this.svg._gsap = null;      // remove the gsap cache
        this.svg.removeAttribute('style')
        this.svg.removeAttribute('transform')
        // remove all element except the scene
        this.svg.querySelectorAll('*:not(.scene)').forEach(el => el.remove());
        // reset the attr changed by Camera-zoom effect
        this.scene.innerHTML = this.svgStr;
        this.scene._gsap = null;      // remove the gsap cache
        this.scene.removeAttribute('transform')
      }
    },
    mounted() {
      this.svg = this.$refs.svg;
      this.scene = this.svg.querySelector('.scene');
      this.$store.commit('editor/mountAniSvgElement', this.svg);
      this.$store.commit('editor/mountAniSceneElement', this.scene);
    },
    watch: {
      aniSvgRenderSign() {
        this.loadSVG();
      }
    },
    computed: {
      ...mapState('editor', {
        aniSvgRenderSign: state => state.aniSvgRenderSign,
        svgStr: state => state.scene?.svgStr ?? '',
      }),
    }
  }
</script>

<style scoped>
.ani-svg-container-master {
  user-select: none;
  overflow: hidden;
  border: 1px solid #ccc;
}
</style>
