import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const MovePropSchema = EffectPropSchema.extend({
  from: z.object({
    x: z.number().or(z.string()).optional(),
    y: z.number().or(z.string()).optional(),
  }).optional()
      .describe("Move from position (x, y). Example: {x: 100, y: 100} or {x: '-=10', y: '+=10px'}"),
  to: z.object({
    x: z.number().or(z.string()).optional(),
    y: z.number().or(z.string()).optional(),
  }).optional()
      .describe("Move to position (x, y). Example: {x: 100, y: 100} or {x: '-=10', y: '+=10px'}"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class MoveTemplate extends EffectTemplate<typeof MovePropSchema> {
  constructor() {
    super({
      name: "Move",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: MovePropSchema,
    })
  }
}
