export interface TreeLike {
  visitChildren(): TreeLike[]
  toOneLineString(): string
}

/**
 * Creates a tree string like
 * ```
 * Root
 *  Child1
 *    Child1.1
 *    Child1.2
 *  Child2
 *     Child2.1
 * ```
 *
 * @param root
 */
export function toTreeString(root: TreeLike): string {
  return toTreeStringHelper(root).join('\n')
}

function toTreeStringHelper(node: TreeLike, prefix = '', isLast = true): string[] {
  const ret: string[] = []
  const isRoot = prefix === ''
  // const oneLineString = prefix + (isRoot ? '' : (isLast ? '+- ' : ':- ')) + node.toOneLineString()
  const oneLineString = prefix + (isRoot ? '' : (isLast ? ' ' : ' ')) + node.toOneLineString()
  ret.push(oneLineString)
  const children = node.visitChildren()
  for (let i = 0; i < children.length; i++) {
    const child = children[i]
    const isLastChild = i === children.length - 1
    // const childPrefix = prefix + (isRoot ? ' ' : (isLast ? '   ' : ':  '))
    const childPrefix = prefix + (isRoot ? ' ' : (isLast ? '   ' : '  '))
    const childLines = toTreeStringHelper(child, childPrefix, isLastChild)
    ret.push(...childLines)
  }
  return ret
}

