import {LogicalAnimation, AniPropSchema, BaseAniPropSchema} from "./LogicalAnimation"
import {z} from "zod"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"


export const UnaryAnimationProps = AniPropSchema.extend({
  animation: z.instanceof(LogicalAnimation)
      .describe('Child animation')
})

export abstract class UnaryAnimation<P extends typeof UnaryAnimationProps> extends LogicalAnimation<P> {
  get child(): LogicalAnimation<BaseAniPropSchema> {
    return this.prop('animation')
  }

  visitChildren(): LogicalAnimation<BaseAniPropSchema>[] {
    return [this.child]
  }

  /**
   * Computes the duration of the animation according to children.
   * It uses computedDuration of children so children must be resolved first.
   */
  computeDuration(cxt: ResolveContext<unknown, unknown>): number {
    return this.child.computedDuration
  }

  rescale(cxt: ResolveContext<unknown, unknown>, rate: number): void {
    this.child.rescale(cxt, rate)
  }

  replace(animation: LogicalAnimation<BaseAniPropSchema>): UnaryAnimation<P> {
    this._props.animation = animation
    return this
  }
}

