import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const CustomPropSchema = EffectPropSchema.extend({
  // type: z.enum(['from', 'to', 'fromTo', 'auto'])
  //     .default('auto')
  //     .describe('Type of animation: "from" | "to" | "fromTo" | "auto" (default: "auto", determined by "from" and "to")'),
  attr: z.string()
      .describe("Attribute to animate"),
  from: z.union([z.string(), z.number()])
      .nullable()
      .default(null)
      .describe("Starting value of the animation"),
  to: z.union([z.string(), z.number()])
      .nullable()
      .default(null)
      .describe("Ending value of the animation"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class CustomTemplate extends EffectTemplate<typeof CustomPropSchema> {
  constructor() {
    super({
      name: "Custom",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: CustomPropSchema,
    })
  }
}
