import {BaseTemplateType} from "@/core/ast/template/BaseTemplate"
import {BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {TargetType} from "@/core/ast/target/TargetType"
import {ParseContext} from "@/parser/json/ParseContext"
import {Import} from "@/core/ast/animation/other/Import"
import {Binder} from "@/core/ast/target/Binder"

export abstract class BaseSpecParser<SpecType> {
  public abstract parseTemplate(spec: SpecType, cxt?: ParseContext): BaseTemplateType
  public abstract parseAnimation(spec: SpecType, cxt?: ParseContext): BaseLogicalAniType
  public abstract parseTargetType(spec: SpecType, cxt?: ParseContext): TargetType
  public abstract parseImports(spec: SpecType, cxt?: ParseContext): Import[]
  public abstract parseBinder(spec: SpecType, cxt?: ParseContext): Binder
}
