import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const CutPropSchema = EffectPropSchema.extend({
  type: z.enum(['in', 'out'])
      .default('in')
      .describe("Wipe enter or wipe exit (default: enter)"),
  from: z.enum(['bottom', 'top', 'left', 'right'])
      .default('bottom')
      .describe("Wipe direction (default: bottom)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class CutTemplate extends EffectTemplate<typeof CutPropSchema> {
  constructor() {
    super({
      name: "Cut",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: CutPropSchema,
    })
  }
}
