<template>
  <el-container class="gallery-view">
    <el-header>
      <PageHeader></PageHeader>
    </el-header>

    <el-main>
      <el-col :span="18" :offset="3">
        <el-row>
          <span class="page-text">Gallery</span>
        </el-row>

        <el-row v-for="projGroup in projectGroups" :key="projGroup.name">
          <div class="group-title">{{ projGroup.name }} ({{ projGroup.projects.length }})</div>
          <div class="svg-group">
            <el-card v-for="project in projGroup.projects" :key="project.id"
                     class="svg-item clickable"
                     shadow="hover"
                     :body-style="{ padding: '0px'}"
                     @click.native="handleProjectClick(project)">
              <SvgPreviewer :project="project" :height="180" :width="290"></SvgPreviewer>
              <div class="item-text-container">
                <div>{{ project.label }}</div>
              </div>
            </el-card>
<!--            <div v-for="i in [...Array((3 - (presetCharts.length % 3)) % 3).keys()]"-->
<!--                 class="svg-item"-->
<!--                 :key="'empty-block-' + i"></div>-->
          </div>
        </el-row>
      </el-col>
    </el-main>

    <el-footer>
      <PageFooter></PageFooter>
    </el-footer>
  </el-container>
</template>

<script>

import PageHeader from "@/components/webpage/common/PageHeader";
import PageFooter from "@/components/webpage/common/PageFooter";
// use the svg previewer from editor
import SvgPreviewer from "@/components/editor/ProjSelPanel/SvgPreviewer";
import {mapState} from "vuex";

export default {
  name: 'GalleryView',
  components: {
    SvgPreviewer, PageFooter, PageHeader
  },
  data() {
    return {
    }
  },
  mounted() {
    this.$store.dispatch('resource/queryAllSvg')
        .then(() => {
          this.$store.commit('resource/changeStaticSvgMapSign');
        })
  },
  methods: {
    handleProjectClick(project) {
      // this.$router.push({
      //   path: 'example',
      //   query: {
      //     'example-index': this.projectList.indexOf(project)
      //   },
      // })
      this.$router.push({
        path: 'editor',
        query: {
          'example-index': this.projectList.indexOf(project),
        }
      })
    },
  },
  computed: {
    ...mapState('resource', {
      projectList: state => state.projectList,
    }),
    projectGroups() {
      const groups = []
      const groupName2idx = new Map();
      this.projectList.forEach(project => {
        const groupName = project.group;
        if (!groupName2idx.has(groupName)) {
          groupName2idx.set(groupName, groups.length);
          groups.push({
            name: groupName,
            projects: []
          })
        }
        groups[groupName2idx.get(groupName)].projects.push(project);
      });

      const order = [
        'Real video reproduction',
        'Vega-Lite chart',
        'Infographics from related work',
        'User study',
      ]
      groups.sort((a, b) => {
        return order.indexOf(a.name) - order.indexOf(b.name);
      })

      // remove user study group
      groups.splice(3, 1);

      // console.log('>>>', groups);
      return groups;
    },
  }
}
</script>

