import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const FloatPropSchema = EffectPropSchema.extend({
  type: z.enum(['in', 'out'])
      .default('in')
      .describe("Type of animation: in, out (default: in)"),
  from: z.enum(['bottom', 'top', 'left', 'right'])
      .default('bottom')
      .describe("Animation direction (default: bottom)"),
  distance: z.number()
      .default(30)
      .describe("Distance of animation (default: 30)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class FloatTemplate extends EffectTemplate<typeof FloatPropSchema> {
  constructor() {
    super({
      name: "Float",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: FloatPropSchema,
    })
  }
}
