import {GAIA_COMMON_PKG} from "@/module/CommonPackage"
import {AryAnimation, AryPropSchema} from "@/core/ast/animation/logical/AryAnimation"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import assert from "assert"
import {z} from "zod"
import {BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {isString, omit, zip} from "lodash"
import {Target} from "@/core/ast/target/Target"
import {Timing} from "@/core/ast/animation/other/Timing"

export const SyncPropSchema = AryPropSchema.extend({
})

export class SyncAnimation extends AryAnimation<typeof SyncPropSchema> {
  private scaleRate = 1

  constructor(props: z.input<typeof SyncPropSchema>) {
    super({
      name: "Sync",
      packageName: GAIA_COMMON_PKG,
      imports: [],
      targetType: "Any",
      propSchema: SyncPropSchema,
    }, props)
  }

  get offsets(): number[] {
    return this.prop("animations").map(a => (a.timing.offset ?? 0) * this.scaleRate)
  }

  computeDuration(cxt: ResolveContext<unknown, unknown>): number {
    let duration = 0
    for (const [animation, offset] of zip(this.animations, this.offsets)) {
      const endTime = animation.computedTotalDuration + offset
      duration = Math.max(duration, endTime)
    }
    if (this.timing.yoyo != null) {
      throw new Error(`Cannot set yoyo in ${this}. Please use yoyo in child animations.`)
    }
    return duration
  }

  rescale(cxt: ResolveContext<unknown, unknown>, rate: number): void {
    this.scaleRate *= rate
    this.computedDuration *= rate
    this.computedTotalDuration *= rate
    this.children.forEach(c => c.rescale(cxt, rate))
  }

  replace(oldAnimation: BaseLogicalAniType, animation: BaseLogicalAniType): SyncAnimation {
    const index = this.animations.indexOf(oldAnimation)
    assert(index != -1, "Animation not found")
    this.animations[index] = animation
    return this
  }

  getPropsStr(): string {
    const otherProps = Object.assign(omit(this.props(), ['id', 'target', 'timing', 'animations']), {
      animations: `Array(${this.animations.length})`,
      scaleRate: this.scaleRate,
    })
    if (this.foreachSrc) {
      Object.assign(otherProps, {
        foreachStagger: this.foreachSrc?.timing.stagger,
      })
    }
    const otherPropsStr = Object.entries(otherProps).map(([k, v]) => `${k}=${v}`).join(', ')
    return `[${otherPropsStr}]`
  }

  clone(): SyncAnimation {
    const cloned = new SyncAnimation({
      id: this.id,
      target: this.target.clone(),
      timing: this.timing.clone(),
      animations: this.animations.map(a => a.clone()),
    })
    cloned.setForeachSrc(this.foreachSrc)
    return cloned
  }
}


function createSync(animationsOrProps: BaseLogicalAniType[] | z.input<typeof SyncPropSchema>): SyncAnimation {
  if (Array.isArray(animationsOrProps)) {
    return new SyncAnimation({animations: animationsOrProps})
  } else {
    return new SyncAnimation(animationsOrProps)
  }
}

// don't use a base class as using this as return value will cause type error
export class SyncBuilder {
  protected readonly _props: z.input<typeof SyncPropSchema>

  constructor() {
    this._props = {
      target: Target.build(),
      timing: Timing.build(),
      animations: [],
    }
  }

  id(id: string | number): this {
    this._props.id = id
    return this
  }

  target(target: Target | string): this {
    if (isString(target)) {
      this._props.target = Target.build().selectAll(target)
    } else {
      this._props.target = target
    }
    return this
  }

  timing(timing: Timing): this {
    this._props.timing = timing
    return this
  }

  prop<K extends keyof z.input<typeof SyncPropSchema>>(key: K, value: z.input<typeof SyncPropSchema>[K]): this {
    this._props[key] = value
    return this
  }

  props(props: z.input<typeof SyncPropSchema>): this {
    Object.assign(this._props, props)
    return this
  }

  select(selector: string): this {
    assert(this._props.target instanceof Target)
    this._props.target.select(selector)
    return this
  }

  selectAll(selector: string): this {
    assert(this._props.target instanceof Target)
    this._props.target.selectAll(selector)
    return this
  }

  filter(selector: string): this {
    assert(this._props.target instanceof Target)
    this._props.target.filter(selector)
    return this
  }

  filterExpr(predicate: string): this {
    assert(this._props.target instanceof Target)
    this._props.target.filter(predicate)
    return this
  }

  sort(sortBy: string, order: (number | string)[] | 'desc' | 'asc' = 'asc'): this {
    assert(this._props.target instanceof Target)
    this._props.target.sort(sortBy, order)
    return this
  }

  slice(start: number, end: number | null = null, step: number | null = null): this {
    assert(this._props.target instanceof Target)
    this._props.target.slice(start, end, step)
    return this
  }


  animations(animations: BaseLogicalAniType[]): this {
    this._props.animations = animations
    return this
  }

  append(ani: BaseLogicalAniType): this {
    this._props.animations.push(ani)
    return this
  }

  build(): SyncAnimation {
    return createSync(this._props)
  }
}

export type Sync = {
  // to separate two overloads
  (animations: BaseLogicalAniType[]): SyncAnimation
  (props: z.input<typeof SyncPropSchema>): SyncAnimation
  builder(): SyncBuilder
}

export const Sync: Sync = Object.assign(createSync, { builder: () => new SyncBuilder() })
