export type BaseTemplateType = BaseTemplate<BaseAniPropSchema, LogicalAnimation<BaseAniPropSchema>>

export abstract class BaseTemplate<PropType extends BaseAniPropSchema, LogicalAniType extends LogicalAnimation<BaseAniPropSchema>>
  extends Proto<BaseTemplate<PropType, LogicalAniType>> {

  private readonly _meta: AniMetaSchema<PropType>

  protected constructor(meta: AniMetaSchema<PropType>) {
    super()
    this._meta = meta
  }

  get type(): PType {
    return PType.AnimationTemplate
  }

  get meta(): AniMetaSchema<PropType> {
    return this._meta
  }

  get name(): string {
    return this._meta.name
  }

  get packageName(): string {
    return this._meta.packageName
  }

  get targetType(): string {
    return this._meta.targetType
  }

  get propSchema(): PropType {
    return this._meta.propSchema
  }

  // abstract get templateType(): TemplateType
  abstract create<ObjType, AniType>(cxt: ResolveContext<ObjType, AniType>, props: z.input<PropType>): LogicalAniType

  clone(): BaseTemplate<PropType, LogicalAniType> {
    throw new Error("Method not implemented.")
  }
}
