import {BaseAniPropSchema, BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {ResolveRule} from "@/core/exec/resolve/ResolveRule"
import {RefAnimation} from "@/core/ast/animation/logical/impl/RefAnimation"
import {ResolveContext} from "../ResolveContext"
import {LogicalAnimationUtils} from "@/core/ast/animation/logical/utils/LogicalAnimationUtils"
import {LoggerManager} from "@/core/utils/Logger"
import assert from "assert"

export class ResolveRef extends ResolveRule {
  private static readonly logger = LoggerManager.getLogger("ResolveRef")

  constructor() {
    super("ResolveRef")
  }

  apply(cxt: ResolveContext<unknown, unknown>, input: BaseLogicalAniType): BaseLogicalAniType {
    return LogicalAnimationUtils.transformDown(input, node => {
      if (!(node instanceof RefAnimation)) return node

      let cur = node
      let result = null

      // eslint-disable-next-line no-constant-condition
      while(true) {
        const template = cxt.templateLibrary.get(cur.ref)
        if (template === undefined) {
          throw new Error(`Template ${cur.ref} not found in the library.`)
        }
        ResolveRef.logger.debug(`Resolving ref ${cur.ref} to ${template.fullName}`)
        const animation = template.create(cxt, cur.refProps())
        animation.setSrc(cur as RefAnimation<BaseAniPropSchema>, template)
        ResolveRef.logger.debug(`Instantiated animation ${animation.fullName} with props ${JSON.stringify(cur.refProps())}`)

        if (!(animation instanceof RefAnimation)) {
          result = animation
          break
        }

        cur = animation as RefAnimation<BaseAniPropSchema>
      }

      assert(result !== null)
      return result
    })
  }
}
