import {AniUnitSpecWithParam} from "@/parser/json/animation/AniUnitSpec"
import {ImportSpec} from "@/parser/json/animation/ImportSpec"
import {ParamSpec} from "@/parser/json/animation/ParamSpec"
import {z} from "zod"

export const AniClassSpec = z.object({
  name: z.string().default("Main"),
  package: z.string().default(""),
  import: z.array(ImportSpec).default([]),
  targetType: z.string().default("Any"),
  params: z.record(z.string(), ParamSpec).default({}),
  main: AniUnitSpecWithParam
})

export type AniClassSpec = z.infer<typeof AniClassSpec>
export type AniClassSpecInput = z.input<typeof AniClassSpec>


