import {z} from "zod"
import {LeafAnimation, LeafAniProps} from "@/core/ast/animation/logical/LeafAnimation"
import {cloneDeep, omit} from "lodash"

export const EffectPropSchema = LeafAniProps.extend({
})

export class EffectAnimation<PropType extends typeof LeafAniProps> extends LeafAnimation<PropType> {
  constructor(name: string, packageName: string, targetType: string, propSchema: PropType, props: z.input<PropType>) {
    super({ name, packageName, imports: [], targetType, propSchema }, props)
  }

  clone(): EffectAnimation<PropType> {
    return new EffectAnimation(this.name, this.packageName, this.targetType, this.propSchema, {
      id: this.id,
      target: this.target.clone(),
      timing: this.timing.clone(),
      ...cloneDeep(omit(this.props(), ['id', 'target', 'timing'])),
    })
  }
}
