import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const HighlightPropSchema = EffectPropSchema.extend({
  contrastTarget: z.string()
      .default('*')
      .describe("Selector of contrast target (default: *, all other elements in the parent selection)"),
  contrastOpacity: z.number()
      .default(0.3)
      .describe("Opacity of contrast target (default: 0.3)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class HighlightTemplate extends EffectTemplate<typeof HighlightPropSchema> {
  constructor() {
    super({
      name: "Highlight",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: HighlightPropSchema,
    })
  }
}
