import {UnresolvedError} from "@/core/utils/error/UnresolvedError"
import {Transform} from "@/core/ast/target/Transform"
import {TargetSelection} from "@/core/ast/target/TargetSelection"
import {FilterTransform} from "@/core/ast/target/trans/FilterTransform"
import {SortTransform} from "@/core/ast/target/trans/SortTransform"
import {SelectTransform} from "@/core/ast/target/trans/SelectTransform"
import {SelectAllTransform} from "@/core/ast/target/trans/SelectAllTransform"
import {SliceTransform} from "@/core/ast/target/trans/SliceTransform"
import {FilterExprTransform} from "@/core/ast/target/trans/FilterExprTransform"

export class Target {
  transforms: Transform[] = []

  private _selection: TargetSelection = null

  constructor(transforms: Transform[] = []) {
    this.transforms = transforms
  }

  concatAtFront(other: Target): void {
    this.transforms.unshift(...other.transforms)
  }

  /**
   * Clones and returns a new Target object. Resolved data won't be cloned.
   */
  clone(): Target {
    return new Target(this.transforms.map(tran => tran.clone()))
  }

  get selection(): TargetSelection {
    if (this._selection === undefined) {
      throw UnresolvedError.build(`Visit field elements before resolving.\n` +
          `In object Target.`)
    }
    return this._selection
  }

  set selection(value: TargetSelection) {
    this._selection = value
  }

  static build(transforms: Transform[] = []): Target {
    return new Target(transforms)
  }

  select(selector: string): this {
    this.transforms.push(new SelectTransform(selector))
    return this
  }

  selectAll(selector: string): this {
    this.transforms.push(new SelectAllTransform(selector))
    return this
  }

  filter(selector: string): this {
    this.transforms.push(new FilterTransform(selector))
    return this
  }

  filterExpr(predicate: string): this {
    this.transforms.push(new FilterExprTransform(predicate))
    return this
  }

  sort(sortBy: string, order: (number | string)[] | 'desc' | 'asc' = 'asc'): this {
    this.transforms.push(new SortTransform(sortBy, order))
    return this
  }

  slice(start: number, end: number | null = null, step: number | null = null): this {
    this.transforms.push(new SliceTransform(start, end, step))
    return this
  }
}
