import {LeafAnimation, LeafAniProps} from "@/core/ast/animation/logical/LeafAnimation"
import {BaseAniPropSchema} from "@/core/ast/animation/logical/LogicalAnimation"
import {z} from "zod"
import {GAIA_COMMON_PKG} from "@/module/CommonPackage"
import {isString, omit} from "lodash"
import {Target} from "@/core/ast/target/Target"
import {Timing} from "@/core/ast/animation/other/Timing"
import assert from "assert"


export const RefAnimationProps = LeafAniProps.extend({
  ref: z.string()
})

export class RefAnimation<RefPropType extends BaseAniPropSchema> extends LeafAnimation<typeof RefAnimationProps> {
  constructor(props: z.input<typeof RefAnimationProps> & z.input<RefPropType>) {
    super({
      name: "Ref",
      packageName: GAIA_COMMON_PKG,
      imports: [],
      targetType: "Any",
      propSchema: RefAnimationProps
    }, props)
  }

  get ref(): string {
    return this.prop("ref")
  }

  refProps(): z.input<RefPropType> {
    return omit(this._rawProps, ["ref"])
  }

  refProp<K extends keyof z.input<RefPropType>>(key: K): z.input<RefPropType>[K] {
    return this.refProps()[key]
  }

  static create<RefPropType extends BaseAniPropSchema>(ref: string, props?: z.input<RefPropType>): RefAnimation<RefPropType>
  static create<RefPropType extends BaseAniPropSchema>(props: z.input<typeof RefAnimationProps> & z.input<RefPropType>): RefAnimation<RefPropType>
  static create<RefPropType extends BaseAniPropSchema>(refOrProps: string | (z.input<typeof RefAnimationProps> & z.input<RefPropType>), props?: z.input<RefPropType>): RefAnimation<RefPropType> {
    if (isString(refOrProps)) {
      return new RefAnimation<RefPropType>({ref: refOrProps, ...props})
    } else {
      return new RefAnimation<RefPropType>(refOrProps)
    }
  }

  clone(): RefAnimation<RefPropType> {
    return new RefAnimation<RefPropType>({
      ref: this.ref,
      id: this.id,
      target: this.target.clone(),
      timing: this.timing.clone(),
    })
  }
}


function createRef<PropType extends BaseAniPropSchema>(ref: string, props?: z.input<PropType>): RefAnimation<PropType> {
  if (props == null) {
    return new RefAnimation<PropType>({ref})
  }
  return new RefAnimation<PropType>({ref, ...props})
}

// don't use a base class as using this as return value will cause type error
export class RefBuilder<PropType extends BaseAniPropSchema>  {
  protected _ref: string
  protected readonly _props: z.input<PropType>

  constructor() {
    this._props = {
      target: Target.build(),
      timing: Timing.build(),
    }
    this._ref = null
  }

  id(id: string | number): this {
    this._props.id = id
    return this
  }

  target(target: Target | string): this {
    if (isString(target)) {
      this._props.target = Target.build().selectAll(target)
    } else {
      this._props.target = target
    }
    return this
  }

  timing(timing: Timing): this {
    this._props.timing = timing
    return this
  }

  prop<K extends keyof z.input<PropType>>(key: K, value: z.input<PropType>[K]): this {
    this._props[key] = value
    return this
  }

  props(props: z.input<PropType>): this {
    Object.assign(this._props, props)
    return this
  }

  select(selector: string): this {
    assert(this._props.target instanceof Target)
    this._props.target.select(selector)
    return this
  }

  selectAll(selector: string): this {
    assert(this._props.target instanceof Target)
    this._props.target.selectAll(selector)
    return this
  }

  filter(selector: string): this {
    assert(this._props.target instanceof Target)
    this._props.target.filter(selector)
    return this
  }

  filterExpr(predicate: string): this {
    assert(this._props.target instanceof Target)
    this._props.target.filter(predicate)
    return this
  }

  sort(sortBy: string, order: (number | string)[] | 'desc' | 'asc' = 'asc'): this {
    assert(this._props.target instanceof Target)
    this._props.target.sort(sortBy, order)
    return this
  }

  slice(start: number, end: number | null = null, step: number | null = null): this {
    assert(this._props.target instanceof Target)
    this._props.target.slice(start, end, step)
    return this
  }


  ref(ref: string): this {
    this._ref = ref
    return this
  }

  build(): RefAnimation<PropType> {
    if (this._ref == null) {
      throw new Error(`Ref is not set`)
    }
    return new RefAnimation<PropType>({
      ref: this._ref,
      ...this._props
    })
  }
}

export const Ref = Object.assign(createRef,
    {builder: <PropType extends BaseAniPropSchema>() => new RefBuilder<PropType>()})
