import {GaiaSession} from "@/session/GaiaSession"
import {TemplateLibrary} from "@/core/library/TemplateLibrary"
import {Resolver} from "@/core/exec/resolve/Resolver"
import {TargetModel} from "@/core/ast/target/TargetModel"
import {BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {PhysicalAnimation} from "@/core/ast/animation/physical/PhysicalAnimation"
import {Synthesizer} from "@/core/exec/synthesis/Synthesizer"


export class CompileExec<ObjType, AniType> {
  readonly session: GaiaSession
  readonly _unresolved: BaseLogicalAniType | null
  readonly _targetModel: TargetModel<ObjType> | null

  private _resolver: Resolver<ObjType, AniType>
  private _synthesizer: Synthesizer<ObjType, AniType>

  private _resolved: BaseLogicalAniType = null
  private _planned: PhysicalAnimation<ObjType, BaseLogicalAniType, AniType> = null
  private _generated: AniType = null

  /**
   * Used for converting spec to a LogicalAnimation via template.
   * @param session
   */
  static quickExec(session: GaiaSession): CompileExec<unknown, unknown> {
    return new CompileExec<unknown, unknown>(session, null, null)
  }

  static fullyExec<ObjType, AniType>(session: GaiaSession, input: BaseLogicalAniType, targetModel: TargetModel<ObjType>): CompileExec<ObjType, AniType> {
    return new CompileExec<ObjType, AniType>(session, input, targetModel)
  }

  private constructor(session: GaiaSession, input: BaseLogicalAniType, targetModel: TargetModel<ObjType>) {
    this.session = session
    this._unresolved = input
    this._targetModel = targetModel
  }

  setResolver(resolver: Resolver<ObjType, AniType>): this {
    this._resolver = resolver
    return this
  }

  setSynthesizer(synthesizer: Synthesizer<ObjType, AniType>): this {
    this._synthesizer = synthesizer
    return this
  }

  get templateLibrary(): TemplateLibrary {
    return this.session.templateLibrary
  }

  get targetModel(): TargetModel<ObjType> {
    if (this._unresolved === null) {
      throw new Error('QuickExec does not contain a TargetModel.')
    }
    return this._targetModel
  }

  get parsed(): BaseLogicalAniType {
    if (this._unresolved === null) {
      throw new Error('QuickExec does not contain a LogicalAnimation.')
    }
    return this._unresolved
  }

  get resolved(): BaseLogicalAniType {
    if (this._unresolved === null) {
      throw new Error('QuickExec cannot be used to resolve LogicalAnimation.')
    }
    if (this._resolved === null) {
      this._resolved = this._resolver.execute(this._unresolved)
    }
    return this._resolved
  }

  get planned(): PhysicalAnimation<ObjType, BaseLogicalAniType, AniType> {
    if (this._planned === null) {
      this._planned = this._synthesizer.plan(this.resolved)
    }
    return this._planned
  }

  get generated(): AniType {
    if (this._generated === null) {
      this._generated = this._synthesizer.generate(this.planned)
    }
    return this._generated
  }
}
