import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"

export const ZoomPropSchema = EffectPropSchema.extend({
  type: z.enum(['in', 'out'])
      .default('in')
      .describe("Wipe enter or wipe exit (default: enter)"),
  ease: z.string()
      .default('back.out')
      .describe("Easing function of animation (default: back.out)"),
})

export class ZoomTemplate extends EffectTemplate<typeof ZoomPropSchema> {
  constructor() {
    super({
      name: "Zoom",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: ZoomPropSchema,
    })
  }
}
