import {z} from "zod"
import {EffectTemplate} from "@/core/ast/template/EffectTemplate"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"


export const PathDrawPropSchema = EffectPropSchema.extend({
  reverseDirection: z.boolean()
      .default(false)
      .describe("Reverse animation direction (default: false)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class PathDrawTemplate extends EffectTemplate<typeof PathDrawPropSchema> {
  constructor() {
    super({
      name: "PathDraw",
      packageName: "gaia.path",
      imports: [],
      targetType: "Line",
      propSchema: PathDrawPropSchema,
    })
  }
}
