import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {FlyPropSchema} from "@/preset/template/common/FlyTemplate"
import {BaseSVGTargetModel} from "@/core/ast/target/impl/svg/BaseSVGTargetModel"
import {createOrGetG, getRelativeBox} from "@/core/utils/SvgUtils"


export class FlyImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof FlyPropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // target
    const elements = this.elements
    // timing
    const stagger = this.stagger

    const tl = gsap.timeline()
    elements.forEach((el, i) => {
      tl.add(this.createTw(el, cxt), i === 0 ? 0 : ('<' + stagger))
    })
    return tl
  }

  createTw(el: SVGElement, cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    const model = cxt.targetModel as BaseSVGTargetModel
    const svg = model.getOrigin()

    // effect
    const {type, from, ease} = this.props()

    const {width: svgWidth, height: svgHeight} = model.getCanvasSize()
    const {x, y} = model.getElBox(el as SVGGraphicsElement)
    const {totalWidth, totalHeight} = getRelativeBox(el as SVGGraphicsElement)
    const merging = 10
    const gTransform = createOrGetG(svg, el as SVGGraphicsElement, `transform-${el.id}`, ['cut'])

    let distX, distY    // offset compared to the original position
    switch (from) {
      case 'top':
        distX = 0
        distY = -y - totalHeight - merging
        break
      case 'bottom':
        distX = 0
        distY = svgHeight - y + merging
        break
      case 'left':
        distX = -x - totalWidth - merging
        distY = 0
        break
      case 'right':
        distX = svgWidth - x + merging
        distY = 0
        break
      default:
        throw new Error(`Unknown from: ${from}`)
    }

    let tw: GsapAnimation

    const vars = this.mergeVars({ease})
    let translateX, translateY

    switch (type) {
      case 'in':
        translateX = '+=' + distX
        translateY = '+=' + distY
        tw = gsap.fromTo(gTransform, {translateX, translateY, ...vars},
            {translateX: 0, translateY: 0, ...vars})
        break
      case 'out':
        translateX = '-=' + distX
        translateY = '-=' + distY
        tw = gsap.fromTo(gTransform, {translateX: 0, translateY: 0, ...vars},
            {translateX, translateY, ...vars})
        break
      default:
        // never goes here as type is checked by zod
        throw new Error(`Unknown type: ${type}`)
    }

    return tw
  }
}

