export class Import {
  pkg: string | null
  clazz: string | null
  as: string | null

  private constructor(pkg?: string, clazz?: string, as?: string) {
    this.pkg = pkg ?? null
    this.clazz = clazz ?? null
    this.as = as ?? null
  }

  isPackageImport(): boolean {
    return this.pkg !== null
  }

  static clazz(clazz: string, as?: string) {
    return new Import(null, clazz, as ?? clazz)
  }

  /**
   * Creates a package import.
   * @param pkg Package name (doesn't end with ".*")
   */
  static pkg(pkg: string) {
    return new Import(pkg)
  }
}
