<template>
  <div class="svg-container-master">
    <svg ref="svg">
      <g class="scene"/>
    </svg>
  </div>
</template>

<script>
  import {mapState} from "vuex";

  export default {
    name: 'SVGContainer',
    data() {
      return {
        svg: null,
        scene: null,
      }
    },
    methods: {
      loadSVG() {
        // reset the attr changed by Camera-zoom effect
        this.scene.innerHTML = this.svgStr;
        this.scene._gsap = null;      // remove the gsap cache
        this.scene.removeAttribute('transform')

        this.$store.commit('editor/updateChartSize');
      }
    },
    mounted() {
      this.svg = this.$refs.svg;
      this.scene = this.svg.querySelector('.scene');
      this.$store.commit('editor/mountSvgElement', this.svg);
      this.$store.commit('editor/mountSceneElement', this.scene);
    },
    watch: {
      svgRenderSign() {
        this.loadSVG();
      }
    },
    computed: {
      ...mapState('editor', {
        svgRenderSign: state => state.svgRenderSign,
        svgStr: state => state.scene?.svgStr ?? '',
      }),
    }
  }
</script>

<style scoped>
.svg-container-master {
  user-select: none;
  border: 1px solid rgb(215, 216, 221);
}
</style>
