import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {CirclePropSchema} from "@/preset/template/annotation/CircleTemplate"
import {BaseSVGTargetModel} from "@/core/ast/target/impl/svg/BaseSVGTargetModel"
import assert from "assert"


export class CircleImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof CirclePropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    const model = cxt.targetModel
    assert(model instanceof BaseSVGTargetModel, `CircleImpl: target model must be BaseSVGTargetModel, but got: ${model}`)
    const svgModel = model as BaseSVGTargetModel

    // target
    const elements = this.elements
    // timing
    const stagger = this.stagger

    const tl = gsap.timeline()
    elements.forEach((el, i) => {
      tl.add(this.createTw(el, svgModel), i === 0 ? 0 : ('<' + stagger))
    })
    return tl
  }

  createTw(el: SVGElement, model: BaseSVGTargetModel): GsapAnimation {
    // effect
    const {direction, aniTime, displayTime, stroke, strokeWidth, ease} = this.props()
    const reverseDirection = direction === 'counterclockwise'

    const vars = this.mergeVars({ease}, {duration: false, yoyo: false, reverse: true})
    const circle =this.addCircle(el, model, stroke, strokeWidth)

    const tl = gsap.timeline()

    let len = 0
    try {
      len = circle.getTotalLength()
    } catch(e) {
      throw new Error(`CircleImpl: get total length failed: ${e}. Cause by: ${circle}`)
    }
    circle.setAttribute('stroke-dasharray', String(len))

    tl.set(circle, {opacity: 1}, 0)
    tl.fromTo(circle, {strokeDashoffset: reverseDirection ? -len : len},
        {...vars, duration: aniTime, strokeDashoffset: 0}, 0)
    tl.to(circle, {strokeDashoffset: reverseDirection ? len : -len, duration: aniTime}, displayTime)
    tl.set(circle, {opacity: 0}, displayTime + aniTime)

    return tl
  }

  addCircle(el: SVGElement, model: BaseSVGTargetModel, stroke: string, strokeWidth: number): SVGEllipseElement {
    const originalBox = model.getOriginalBox(el as SVGGraphicsElement)
    const {x, y, width, height} = originalBox ?? model.getElBox(el as SVGGraphicsElement)
    const root = model.getOrigin()

    const ellipse = document.createElementNS('http://www.w3.org/2000/svg', 'ellipse')
    ellipse.setAttribute('transform', `translate(${x+width/2}, ${y+height/2})`)
    ellipse.setAttribute('rx', `${width/2*1.2}`)
    ellipse.setAttribute('ry', `${height/2*1.3}`)
    ellipse.setAttribute('fill', 'none')
    ellipse.setAttribute('stroke', stroke)
    ellipse.setAttribute('stroke-width', String(strokeWidth))
    // hide at first
    ellipse.setAttribute('opacity', '0')

    root.appendChild(ellipse)

    return ellipse
  }
}

