import {TargetType} from "@/core/ast/target/TargetType"
import {Import} from "@/core/ast/animation/other/Import"
import {Logger, LoggerManager} from "@/core/utils/Logger"
import assert from "assert"
import {Library} from "@/core/library/Library"

export class TypeLibrary extends Library<TargetType> {
  createCaller(): TypeLibraryCaller {
    return new TypeLibraryCaller(this)
  }
}

export class TypeLibraryCaller {
  private static readonly logger: Logger = LoggerManager.getLogger("TypeLibraryCaller")

  private _instance: TypeLibrary = null
  private _aliasMap: Map<string, string> = new Map()
  // full name -> type
  private _importMap: Map<string, TargetType> = new Map()

  constructor(typeLibrary: TypeLibrary) {
    this._instance = typeLibrary
  }

  setImport(importItem: Import): TypeLibraryCaller {
    if (importItem.isPackageImport()) {
      const types = this._instance.getByPackage(importItem.pkg)
      if (types == null) {
        throw new Error(`Package ${importItem.pkg} not found.`)
      }
      // put into import map
      for (const type of types) {
        this._importMap.set(type.fullName, type)
      }
      TypeLibraryCaller.logger.trace(`Import package ${importItem.pkg} successfully. `
          + `Imported types: ${types.map(t => t.fullName).join(", ")}`)
    } else {
      // is class import
      const type = this._instance.get(importItem.clazz)
      if (type == null) {
        throw new Error(`Type class ${importItem.clazz} not found.`)
      }
      // set alias
      this._aliasMap.set(importItem.as, type.fullName)
      // put into import map
      this._importMap.set(type.fullName, type)
      TypeLibraryCaller.logger.trace(`Import class ${importItem.clazz} as ${importItem.as} successfully.`)
    }
    return this
  }

  setAllImport(imports: Import[]): TypeLibraryCaller {
    imports.forEach(importItem => this.setImport(importItem))
    return this
  }

  get(name: string): TargetType {
    const {result, status} = this.safeGet(name)
    switch (status) {
      case "ok":
        return result
      case "not-found":
        throw new Error(`Type ${name} not found.`)
      case "not-imported":
        throw new Error(`Type ${name} not found. Maybe you forget to import it.`)
    }
  }

  safeGet(name: string): { result: TargetType | undefined, status: "ok" | "not-found" | "not-imported" } {
    if (this._aliasMap.has(name)) {
      const alias = this._aliasMap.get(name)
      const result = this._importMap.get(alias)
      assert(result != null)
      return {result: result, status: "ok"}
    }
    // name might be full name, name with prefix, or name without prefix
    // so first try to get by proto manager to address it
    const result = this._instance.get(name)
    if (result == null) {
      return {result: undefined, status: "not-found"}
    }
    if (this._importMap.get(result.fullName) == undefined) {
      return {result: undefined, status: "not-imported"}
    }
    return {result: result, status: "ok"}
  }
}
