import {GAIA_COMMON_PKG} from "@/module/CommonPackage"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {z} from "zod"
import {omit} from "lodash"
import {UnaryAnimation, UnaryAnimationProps} from "@/core/ast/animation/logical/UnaryAnimation"

export const ForeachPropSchema = UnaryAnimationProps.extend({
  groupBy: z.string().default('@id'),
  start: z.enum(['with prev', 'after prev']).default('with prev'),
})

export class ForeachAnimation extends UnaryAnimation<typeof ForeachPropSchema> {
  constructor(props: z.input<typeof ForeachPropSchema>) {
    super({
      name: "Foreach",
      packageName: GAIA_COMMON_PKG,
      imports: [],
      targetType: "Any",
      propSchema: ForeachPropSchema,
    }, props)
  }

  computeDuration(cxt: ResolveContext<unknown, unknown>): number {
    throw new Error("Can't get duration from foreach animation. It should be transformed to Sync or Concat animation.")
  }

  rescale(cxt: ResolveContext<unknown, unknown>, rate: number): void {
    throw new Error("Can't rescale foreach animation. It should be transformed to Sync or Concat animation.")
  }

  getPropsStr(): string {
    const otherProps = Object.assign(omit(this.props(), ['id', 'target', 'timing', 'animation']), {
      animation: `Array(${this.child})`,
    })
    const otherPropsStr = Object.entries(otherProps).map(([k, v]) => `${k}=${v}`).join(', ')
    return `[${otherPropsStr}]`
  }

  clone(): ForeachAnimation {
    return new ForeachAnimation({
      id: this.id,
      target: this.target.clone(),
      timing: this.timing.clone(),
      animation: this.child.clone(),
      groupBy: this.props().groupBy,
      start: this.props().start,
    })
  }
}


