import {AniUnitSpec} from "@/parser/json/animation/AniUnitSpec"
import {TargetSpec, TransformSpec} from "@/parser/json/animation/TargetSpec"
import {YoyoSpec} from "@/parser/json/animation/YoyoSpec"
import {z} from "zod"

export const NumberParamSpec = z.union([
  z.number(),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('number').default('number'),
    value: z.number(),
  }),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('number'),
    value: z.number().optional(),
  }),
])

export const StringParamSpec = z.union([
  z.string(),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('string').default('string'),
    value: z.string(),
  }),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('string'),
    value: z.string().optional(),
  })
])

export const BoolParamSpec = z.union([
  z.boolean(),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('bool').default('bool'),
    value: z.boolean(),
  }),
  z.object({
    private: z.boolean().default(false),
    type: z.literal('bool'),
    value: z.boolean().optional(),
  })
])

export const AniUnitArrayParamSpec = z.object({
  private: z.boolean().default(false),
  type: z.literal('aniUnitArray'),
  value: z.array(AniUnitSpec).default([]),
})

export const AniUnitParamSpec = z.object({
  private: z.boolean().default(false),
  type: z.literal('aniUnit'),
  value: AniUnitSpec.optional(),
})

export const TargetParamSpec = z.object({
  private: z.boolean().default(false),
  type: z.literal('target'),
  value: TargetSpec.optional(),
})

export const TransformParamSpec = z.object({
  private: z.boolean().default(false),
  type: z.literal('transform'),
  value: TransformSpec.optional(),
})

export const YoyoParamSpec = z.object({
  private: z.boolean().default(false),
  type: z.literal('yoyo'),
  value: YoyoSpec.optional(),
})

export const ParamSpec = z.union([
  NumberParamSpec,
  StringParamSpec,
  BoolParamSpec,
  AniUnitArrayParamSpec,
  AniUnitParamSpec,
  TargetParamSpec,
  TransformParamSpec,
  YoyoParamSpec,
])

export type ParamSpec = z.infer<typeof ParamSpec>


