import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {FloatPropSchema} from "@/preset/template/common/FloatTemplate"
import {createOrGetG} from "@/core/utils/SvgUtils"
import {BaseSVGTargetModel} from "@/core/ast/target/impl/svg/BaseSVGTargetModel"


export class FloatImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof FloatPropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // target
    const elements = this.elements
    // timing
    const stagger = this.stagger

    const tl = gsap.timeline()
    elements.forEach((el, i) => {
      tl.add(this.createTw(el, cxt), i === 0 ? 0 : ('<' + stagger))
    })
    return tl
  }

  createTw(el: SVGElement, cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    const model = cxt.targetModel as BaseSVGTargetModel

    // effect
    const {type, from, distance, ease} = this.props()
    const svg = model.getOrigin()

    let tw: GsapAnimation

    const vars = this.mergeVars({ease})
    const {factor, attr} = this.getParam(from)
    const gTransform = createOrGetG(svg, el as SVGGraphicsElement, `transform-${el.id}`, ['fly'])

    switch (type) {
      case 'in':
        tw = gsap.fromTo(gTransform, {[attr]: `+=${distance * factor}px`, opacity: 0}, {[attr]: 0, opacity: 1, ...vars})
        break
      case 'out':
        tw = gsap.fromTo(gTransform, {[attr]: 0, opacity: 1}, {[attr]: `+=${distance * factor}px`, opacity: 0, ...vars})
        break
      default:
        // never goes here as type is checked by zod
        throw new Error(`Unknown type: ${type}`)
    }

    return tw
  }

  getParam(from: string): {factor: number, attr: string} {
    const attr = (from === 'left' || from === 'right') ? 'x' : 'y'
    const factor = (from === 'right' || from === 'bottom') ? 1 : -1
    return {factor, attr}
  }
}

