import {ProtoManager} from "@/module/ProtoManager"
import {Proto} from "@/module/Proto"


export class Library<P extends Proto<P>> {
  protected protoManager: ProtoManager<P>

  constructor() {
    this.protoManager = new ProtoManager()
  }

  get size() {
    return this.protoManager.protoSize
  }

  register(proto: P): this
  register(protoList: P[]): this
  register(proto: P | P[]): this {
    if (Array.isArray(proto)) {
      proto.forEach(p => this.protoManager.registerProto(p))
    } else {
      this.protoManager.registerProto(proto)
    }
    return this
  }

  registerOrUpdate(proto: P): this
  registerOrUpdate(protoList: P[]): this
  registerOrUpdate(proto: P | P[]): this {
    if (Array.isArray(proto)) {
      proto.forEach(p => this.protoManager.registerOrUpdateProto(p))
    } else {
      this.protoManager.registerOrUpdateProto(proto)
    }
    return this
  }

  unregister(fullName: string) {
    return this.protoManager.unregisterProto(fullName)
  }

  /**
   * @param name Proto name (might be with identifier prefix, e.g., "b.Clazz")
   * @returns Proto instance (full name might be "a.b.Clazz"). If not found, return undefined.
   */
  get(name: string): P | undefined {
    return this.protoManager.queryProto(name)
  }

  getAll() {
    return this.protoManager.queryAllProto()
  }

  /**
   * @param pkg Package name (e.g., "a.b")
   * @returns All protos in the package. If not found, return undefined.
   */
  getByPackage(pkg: string): P[] | undefined {
    return this.protoManager.queryProtoByPackage(pkg)
  }
}
