import {BaseLogicalAniType, LogicalAnimation} from "@/core/ast/animation/logical/LogicalAnimation"
import {ResolveRule} from "@/core/exec/resolve/ResolveRule"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {LogicalAnimationUtils} from "@/core/ast/animation/logical/utils/LogicalAnimationUtils"
import {UnaryAnimation} from "@/core/ast/animation/logical/UnaryAnimation"
import {AryAnimation} from "@/core/ast/animation/logical/AryAnimation"
import {LeafAnimation} from "@/core/ast/animation/logical/LeafAnimation"
import {LoggerManager} from "@/core/utils/Logger"
import assert from "assert"
import {GroupTransform} from "@/core/ast/target/trans/GroupTransform"
import {TargetSelection} from "@/core/ast/target/TargetSelection"
import {VirtualElementType} from "@/core/ast/target/TargetModel"

/**
 * Resolves target and set target selection for all animations.
 *
 * Grouping operations (e.g., group) should be processed before it.
 */
export class ResolveTarget extends ResolveRule {
  private static readonly logger = LoggerManager.getLogger("ResolveTarget")

  constructor() {
    super("ResolveTarget")
  }

  apply(cxt: ResolveContext<unknown, unknown>, input: LogicalAnimation<any>): LogicalAnimation<any> {
    const model = cxt.targetModel

    let sel = model.rootSelection
    for (const trans of input.target.transforms) {
      const newSel = trans.execute(sel)
      ResolveTarget.logger.trace(`Converting [${sel}] to [${newSel}]`)
      sel = newSel
    }
    input.target.selection = sel
    ResolveTarget.logger.debug(`Execute ${input.target.transforms.length} transform(s), for ROOT -> ${input}, `
        + `converting [${model.rootSelection}] to [${sel}]`)

    LogicalAnimationUtils.preorderTraversal(input, (node: BaseLogicalAniType) => {
      if (node instanceof UnaryAnimation) {
        this.applyTransform(node as BaseLogicalAniType, node.child)
      } else if (node instanceof AryAnimation) {
        if (node.foreachSrc != null) {
          this.applyTransformForeach(node as AryAnimation<any>)
        } else {
          for (const child of node.children) {
            this.applyTransform(node as BaseLogicalAniType, child)
          }
        }
      } else if (node instanceof LeafAnimation) {
        // do nothing
      }
      return false
    })

    return input
  }

  applyTransform(parent: BaseLogicalAniType, child: BaseLogicalAniType) {
    let sel = parent.target.selection
    for (const trans of child.target.transforms) {
      const newSel = trans.execute(sel)
      ResolveTarget.logger.trace(`${trans} executed, converting [${sel}] to [${newSel}]`)
      sel = newSel
    }
    ResolveTarget.logger.debug(`Execute ${child.target.transforms.length} transform(s) for ${parent} -> ${child}, `
        + `converting [${parent.target.selection}] to [${sel}]`)
    child.target.selection = sel
  }

  applyTransformForeach(parent: AryAnimation<any>) {
    const {groupBy} = parent.foreachSrc.props()
    const stagger = parent.foreachSrc.timing.stagger ?? 0

    assert(parent.children.length === 1, `${parent} should have only one child from Foreach animation.`)
    const resolved = parent.children[0]   // this animation's ref is resolved

    // generate initial sel
    const initialSel = parent.target.selection

    // split it by groupBy
    const groupTransform = new GroupTransform(groupBy)
    const groupSel = groupTransform.execute(initialSel)

    // generate animations
    const selections = []
    for (const groupEl of groupSel.elements) {
      const elements = groupEl.querySelectorAll<VirtualElementType>(':scope > *')
      // set sel as the parent instead of groupEl
      const newSel = new TargetSelection(Array.from(elements), groupSel.targetModel, initialSel)
      selections.push(newSel)
    }
    const animations = selections.map((sel, i) => {
      const newResolved = resolved.clone()
      newResolved.timing.offset = stagger * i
      return newResolved
    })

    // replace
    parent.setChildren(animations)

    // apply transform
    for (let i = 0; i < parent.children.length; i++) {
      const child = parent.children[i]
      let sel = selections[i]
      for (const trans of child.target.transforms) {
        const newSel = trans.execute(sel)
        ResolveTarget.logger.trace(`${trans} executed, converting [${sel}] to [${newSel}]`)
        sel = newSel
      }
      ResolveTarget.logger.debug(`Split group and execute ${child.target.transforms.length} transform(s) for ${parent} -> ${child}, `
          + `converting [${selections[i]}] to [${sel}]`)
      child.target.selection = sel
    }

    // console.log('resolve target', parent.target.selection, groupSel, selections, animations)
  }
}
