import {Proto} from "@/module/Proto"
import {Package} from "@/module/Package"
import {isString} from "lodash"

export class ProtoManager<T extends Proto<T>> {
  readonly rootPackage: Package = Package.createDefaultPackage()
  private _protoSize = 0

  get protoSize() {
    return this._protoSize
  }

  private createPackage(packageName: string | string[]): Package {
    const pathArr = isString(packageName) ? ProtoManager.splitPath(packageName) : packageName
    let curPkg = this.rootPackage
    for (const name of pathArr) {
      let subPkg = curPkg.querySubPackage(name)
      if (subPkg === null) {
        subPkg = Package.createPackage(name, curPkg)
        curPkg.addSubPackage(subPkg)
      }
      curPkg = subPkg
    }
    return curPkg
  }

  private queryPackage(packageName: string | string[]): Package {
    const pathArr = isString(packageName) ? ProtoManager.splitPath(packageName) : packageName
    let curPkg = this.rootPackage
    for (let i = 0; i < pathArr.length; ++i) {
      const subPkg = curPkg.querySubPackage(pathArr[i])
      if (subPkg === null) {
        return null
      }
      curPkg = subPkg
    }
    return curPkg
  }

  registerProto(proto: Proto<T>) {
    const protoName = proto.name
    const packageName = proto.packageName

    let pkg = this.queryPackage(packageName)
    if (pkg === null) {
      pkg = this.createPackage(packageName)
    } else {
      // package exists
      if (pkg.queryProto(protoName) !== null) {
        throw new Error(`Proto ${proto.fullName} has already been registered.`)
      }
    }
    pkg.registerProto(proto)
    this._protoSize += 1
  }

  registerOrUpdateProto(proto: Proto<T>) {
    const protoName = proto.name
    const packageName = proto.packageName

    let pkg = this.queryPackage(packageName)
    if (pkg === null) {
      pkg = this.createPackage(packageName)
    }
    if (pkg.queryProto(protoName) === null) {
      // is a new proto
      this._protoSize += 1
    } else {
      pkg.unregisterProto(protoName)
    }
    pkg.registerProto(proto)
  }

  unregisterProto(fullName: string) {
    const proto = this.queryProtoByFullName(fullName)
    if (proto === null) {
      throw new Error(`Proto ${fullName} doesn't exist.`)
    }
    const pkg = this.queryPackage(proto.packageName)
    const success = pkg.unregisterProto(proto.name)
    if (success) {
      this._protoSize -= 1
    }
    return success
  }

  queryProtoByFullName(fullName: string | string[]): T {
    const pathArr = isString(fullName) ? ProtoManager.splitPath(fullName) : fullName
    if (pathArr.length === 0) {
      throw new Error(`Invalid proto name path ${fullName}.`)
    }
    const clsName = pathArr.pop()
    const pkg = this.queryPackage(pathArr)
    return pkg.queryProto(clsName)
  }

  containProtoByFullName(fullName: string | string[]): boolean {
    return this.queryProtoByFullName(fullName) === null
  }

  queryProto(protoName: string | string[]): T | undefined {
    const pathArr = isString(protoName) ? ProtoManager.splitPath(protoName) : protoName
    if (pathArr.length === 0) {
      throw new Error(`Invalid proto name path ${protoName}.`)
    }

    const clsName = pathArr.pop()
    const suffix = pathArr.join('.')
    const candidates = this.findProto(clsName)
        .filter(c => c.packageName.endsWith(suffix))

    if (candidates.length === 1) {
      return candidates[0] as T
    } else if (candidates.length === 0) {
      // throw new Error(`Invalid class path ${protoName}.`)
      return undefined
    } else {
      // return class in default package if it exists
      const candidateInDefaultPkg = candidates.find(c => c.packageName === '')
      if (candidateInDefaultPkg !== undefined) {
        return candidateInDefaultPkg as T
      } else {
        throw new Error(`Ambiguous proto name. ` +
            `${candidates.length} prototypes found: \n` +
            `${candidates.map(c => c.fullName).join('\n')}\n`)
      }
    }
  }

  queryProtoByPackage(pkgName: string): T[] {
    const pkg = this.queryPackage(pkgName)
    if (pkg === null) {
      return undefined
    }
    return pkg.queryAllProto()
  }

  queryAllProto(): T[] {
    const result: T[] = []
    this.queryAllProtoHelper(this.rootPackage, result)
    return result
  }

  private queryAllProtoHelper(pkg: Package, ret: T[]) {
    pkg.queryAllProto().forEach(p => ret.push(p as T))
    pkg.querySubPackages().forEach(p => this.queryAllProtoHelper(p, ret))
  }

  private findProto(protoName: string): T[] {
    const ret: T[] = []
    const dfs = (pkg: Package) => {
      const proto = pkg.queryProto(protoName) as T
      if (proto !== null) {
        ret.push(proto)
      }
      pkg.querySubPackages().forEach(p => dfs(p))
    }
    dfs(this.rootPackage)
    return ret
  }

  static splitPath(path: string): string[] {
    if (path.trim() === '') {
      return []
    }
    const pathArr = path.split('.').map(s => s.trim())
    const valid = pathArr.every(s => s.match('[a-zA-Z0-9]+'))
    if (!valid) {
      throw new Error(`Invalid package/proto path ${path}.`)
    }
    return pathArr
  }
}
