import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import assert from "assert"
import {PathDrawPropSchema} from "@/preset/template/path/PathDrawTemplate"


export class PathDrawImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof PathDrawPropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // target
    const elements = this.elements
    // timing
    const stagger = this.stagger

    const tl = gsap.timeline()
    elements.forEach((el, i) => {
      tl.add(this.createTw(el, cxt), i === 0 ? 0 : ('<' + stagger))
    })
    tl.totalDuration(this.totalDuration)    // special to this animation
    return tl
  }

  createTw(el: SVGElement, cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // effect
    const {reverseDirection, ease} = this.props()

    const vars = this.mergeVars({ease})

    assert(el instanceof SVGGeometryElement, `PathBlinkImpl: element is not SVGGeometryElement: ${el}`)
    let len = 0
    try {
      len = el.getTotalLength()
    } catch(e) {
      throw new Error(`PathBlinkImpl: get total length failed: ${e}. Cause by: ${el}`)
    }
    const strokeWith = parseFloat(el.getAttribute('stroke-width') ?? '0')
    const marge = strokeWith + 1
    el.setAttribute('stroke-dasharray', `${len} ${len + marge * 2}`)

    return gsap.fromTo(el, {strokeDashoffset: reverseDirection ? -(len + marge) : (len + marge)},
        {...vars, strokeDashoffset: 0})
  }
}

