<template>
  <div class="proj-selection-panel">
    <el-scrollbar>

      <div class="upload-title">Upload an SVG</div>
      <el-upload
          class="drag-upload"
          drag
          style="width: 100%"
          :on-success="handleFile"
          :http-request='uploadFile'
          :before-upload="fileFilter"
          :show-file-list="false"
          :auto-upload="true"
          action="none"
          multiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">Drop or select file (.svg)</div>
        <!--        <div class="el-upload__tip" slot="tip">/div>-->
      </el-upload>

      <el-divider></el-divider>

      <div v-for="projGroup in projectGroups" :key="projGroup.name">
        <div class="upload-title">{{ projGroup.name }} ({{ projGroup.projects.length }})</div>
        <div class="svg-group">
          <el-card v-for="project in projGroup.projects" :key="project.id"
                   class="svg-item clickable"
                   shadow="hover"
                   :body-style="{ padding: '0px'}"
                   @click.native="handleProjectClick(project)">
            <SvgPreviewer :project="project" :height="160" :width="240"></SvgPreviewer>
            <div style="display: flex; justify-content: center; align-items: center; height: 20px;">{{ project.label }}</div>
          </el-card>
<!--          <div v-for="i in [...Array((3 - (projGroup.projects.length % 3)) % 3).keys()]"-->
<!--               class="svg-item"-->
<!--               :key="'empty-block-' + i"></div>-->
        </div>
        <el-divider></el-divider>
      </div>

      <div class="upload-title">Load uploaded charts ({{ customCharts.length }})</div>
      <div class="svg-group ">
        <el-card v-for="project in customCharts" :key="project.id"
                 class="svg-item clickable"
                 shadow="hover"
                 :body-style="{ padding: '0px'}"
                 @click.native="handleProjectClick(project)">
          <SvgPreviewer :project="project" :height="160" :width="240"></SvgPreviewer>
          <div style="display: flex; justify-content: center; align-items: center; height: 20px;">{{ project.label }}</div>
        </el-card>
<!--        <div v-for="i in [...Array((3 - (customCharts.length % 3)) % 3).keys()]"-->
<!--             class="svg-item"-->
<!--             :key="'empty-block-' + i"></div>-->
      </div>

    </el-scrollbar>
  </div>
</template>

<script>
  import {mapState} from "vuex";
  import axios from "axios";
  import SvgPreviewer from "@/components/editor/ProjSelPanel/SvgPreviewer";

  export default {
    name: 'ProjSelectionPanel',
    components: {
      SvgPreviewer,
    },
    data() {
      return {
        customCharts: []
      }
    },
    mounted() {
      this.$store.dispatch('resource/queryAllSvg')
          .then(() => {
            this.$store.commit('resource/changeStaticSvgMapSign');
          })
    },
    methods: {
      uploadFile(param) {
        this.svgUrl = URL.createObjectURL(param.file);
        axios.get(this.svgUrl)
            .then(rep => {
              this.$store.dispatch('resource/addProject', {srcId: param.file.name.split('.')[0], svgStr: rep.data});
            }).catch(error => {
              this.$message.error(error);
            })
        this.$emit('closeProjSelPanel');
      },
      handleFile(res, file) {
        // if need to store svg in backend
        console.log(res, file)
      },
      fileFilter(file) {
        const isSVG = file.type === 'image/svg+xml'
        if (!isSVG) {
          this.$message.error('Only support svg chart!');
        }
        return isSVG;
      },
      handleProjectClick(project) {
        console.log('change proj', project)
        this.$store.commit('resource/changeProjectId', project.id);
        this.$emit('closeProjSelPanel');
      },
    },
    watch: {
      staticSvgMap() {
        console.log(this.staticSvgMap);
      }
    },
    computed: {
      projectId: {
        set(value) {
          this.$store.commit('resource/changeProjectId', value);
        },
        get() {
          return this.$store.state.resource.projectId;
        }
      },
      ...mapState('resource', {
        projectList: state => state.projectList,
        staticSvgMap: state => state.staticSvgMap,
      }),
      projectGroups() {
        const groups = []
        const groupName2idx = new Map();
        this.projectList.forEach(project => {
          const groupName = project.group;
          if (!groupName2idx.has(groupName)) {
            groupName2idx.set(groupName, groups.length);
            groups.push({
              name: groupName,
              projects: []
            })
          }
          groups[groupName2idx.get(groupName)].projects.push(project);
        });

        const order = [
            'Real video reproduction',
            'Vega-Lite chart',
            'Infographics from related work',
            'User study',
        ]
        groups.sort((a, b) => {
          return order.indexOf(a.name) - order.indexOf(b.name);
        })

        // console.log('>>>', groups);
        return groups;
      },
    }
  }
</script>

<style scoped>
.proj-selection-panel {
  user-select: none;
  height: 80%;
}
.svg-group {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}
.svg-item {
  height: 183px;
  width: 240px;
  margin: 0 10px 10px 10px;
}
.clickable {
  cursor: pointer;
}
.proj-selection-panel >>> .el-scrollbar__wrap {
  overflow-x: hidden;
}

.drag-upload >>> .el-upload-dragger, .drag-upload >>> .el-upload--text {
  width: 100%;
}
.upload-title {
  font-size: 1.2rem;
  margin-bottom: 8px;
}
</style>
