import {z} from "zod"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {CompositeTemplate} from "@/core/ast/template/CompositeTemplate"
import {Ref, RefAnimation} from "@/core/ast/animation/logical/impl/RefAnimation"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {WipePropSchema} from "@/preset/template/common/WipeTemplate"

export const WipeInPropSchema = EffectPropSchema.extend({
  from: z.enum(['bottom', 'top', 'left', 'right'])
      .default('bottom')
      .describe("Wipe direction (default: bottom)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class WipeInTemplate extends CompositeTemplate<typeof WipeInPropSchema, RefAnimation<typeof WipePropSchema>> {
  constructor() {
    super({
      name: "WipeIn",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: WipeInPropSchema,
    })
  }

  instantiate<ObjType, AniType>(cxt: ResolveContext<ObjType, AniType>,
                                props: z.output<typeof WipeInPropSchema>): RefAnimation<typeof WipePropSchema> {
    const {id, target, timing, from, ease} = props
    return Ref.builder<typeof WipePropSchema>()
        .ref('Wipe')
        .props({id, target, timing, from, type: 'in', ease})
        .build()
  }
}
