import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {GrowPropSchema} from "@/preset/template/common/GrowTemplate"


export class GrowImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof GrowPropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // target
    const elements = this.elements
    // timing
    const stagger = this.stagger

    const tl = gsap.timeline()
    elements.forEach((el, i) => {
      tl.add(this.createTw(el, cxt), i === 0 ? 0 : ('<' + stagger))
    })
    return tl
  }

  createTw(el: SVGElement, cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // effect
    const {type, from, ease} = this.props()

    let tw: GsapAnimation

    const vars = this.mergeVars({ease})
    const {transformOrigin, attr} = this.getTransformParam(from)
    switch (type) {
      case 'in':
        tw = gsap.fromTo(el, {[attr]: 0, transformOrigin}, {[attr]: 1, transformOrigin, ...vars, })
        break
      case 'out':
        tw = gsap.fromTo(el, {[attr]: 1, transformOrigin}, {[attr]: 0, transformOrigin, ...vars})
        break
      default:
        // never goes here as type is checked by zod
        throw new Error(`Unknown type: ${type}`)
    }

    return tw
  }

  getTransformParam(from: string): {transformOrigin: string, attr: string} {
    switch (from) {
      case 'top':
        return {transformOrigin: 'center top', attr: 'scaleY'}
      case 'bottom':
        return {transformOrigin: 'center bottom', attr: 'scaleY'}
      case 'left':
        return {transformOrigin: 'left center', attr: 'scaleX'}
      case 'right':
        return {transformOrigin: 'right center', attr: 'scaleX'}
      default:
        throw new Error(`Unknown from: ${from}`)
    }
  }
}

