import {BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {ResolveRule} from "@/core/exec/resolve/ResolveRule"
import {toTreeString} from "@/core/utils/TreeUtils"

export enum LogLevel {
  TRACE = 'TRACE',
  DEBUG = 'DEBUG',
  INFO = 'INFO',
  WARN = 'WARN',
  ERROR = 'ERROR',
}

export type LogRecord = {
  level: LogLevel
  timestamp: number
  caller: string
  msg: string
}

export class Logger {
  private readonly manager: LoggerManagerClass
  private readonly caller: string

  constructor(logManager: LoggerManagerClass, caller: string) {
    this.manager = logManager
    this.caller = caller
  }

  trace(...msg: any[]) {
    this.log(LogLevel.TRACE, ...msg)
  }

  debug(...msg: any[]) {
    this.log(LogLevel.DEBUG, ...msg)
  }

  info(...msg: any[]) {
    this.log(LogLevel.INFO, ...msg)
  }

  warn(...msg: any[]) {
    this.log(LogLevel.WARN, ...msg)
  }

  error(...msg: any[]) {
    this.log(LogLevel.ERROR, ...msg)
  }

  log(level: LogLevel, ...msg: any[]) {
    this.manager.log(level, this.caller, msg.map(it => String(it)).join(' '))
  }

  logResolve(before: BaseLogicalAniType, after: BaseLogicalAniType, rule: ResolveRule): void {
    const msg = `Resolve ${before.toString()} -> ${after.toString()} by rule ${rule.ruleName}\n`
        + `Before:\n${toTreeString(before)}\nAfter:\n${toTreeString(after)}`
    this.debug(msg)
  }
}

class LoggerManagerClass {
  logLevel: LogLevel = LogLevel.TRACE

  static readonly logLevel2Method = {
    [LogLevel.TRACE]: console.debug,   // not to use console.trace, it's too verbose
    [LogLevel.DEBUG]: console.debug,
    [LogLevel.INFO]: console.info,
    [LogLevel.WARN]: console.warn,
    [LogLevel.ERROR]: console.error,
  }

  static readonly logLevel2Rank = {
    [LogLevel.TRACE]: 0,
    [LogLevel.DEBUG]: 1,
    [LogLevel.INFO]: 2,
    [LogLevel.WARN]: 3,
    [LogLevel.ERROR]: 4,
  }

  private _records: LogRecord[] = []

  getLogger(caller: string): Logger {
    return new Logger(this, caller)
  }

  setLogLevel(level: LogLevel) {
    this.logLevel = level
  }

  clear() {
    this._records = []
  }

  log(level: LogLevel, caller: string, msg: string) {
    const record = {
      timestamp: Date.now(),
      level,
      caller,
      msg
    }
    this._records.push(record)

    if (LoggerManagerClass.logLevel2Rank[level] >= LoggerManagerClass.logLevel2Rank[this.logLevel]) {
      LoggerManagerClass.logLevel2Method[level](LoggerManagerClass.log2Str(record))
    }
  }

  get records(): LogRecord[] {
    return [...this._records]
  }

  static log2Str(log: LogRecord): string {
    return `${new Date(log.timestamp).toLocaleString()} [${log.level}] [${log.caller}] ${log.msg}`
  }
}

export const LoggerManager = new LoggerManagerClass()
