import {AniClassSpec, AniClassSpecInput} from "@/parser/json/animation/AniClassSpec"
import {AniUnitSpec, AniUnitSpecWithParam} from "@/parser/json/animation/AniUnitSpec"
import {FullNamePattern, ImportSpec, WildcardPattern} from "@/parser/json/animation/ImportSpec"
import {AniPropSchema, BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {Import} from "@/core/ast/animation/other/Import"
import {TargetSpec, TransformSpec} from "@/parser/json/animation/TargetSpec"
import {ParseError} from "@/core/utils/error/ParseError"
import {Timing, TimingProps} from "@/core/ast/animation/other/Timing"
import {ParseContext} from "@/parser/json/ParseContext"
import {Ref} from "@/core/ast/animation/logical/impl/RefAnimation"
import {Transform} from "@/core/ast/target/Transform"
import {SortTransform} from "@/core/ast/target/trans/SortTransform"
import {Target} from "@/core/ast/target/Target"
import {BaseSpecParser} from "@/parser/BaseSpecParser"
import {TargetType, TargetTypeComponent} from '@/core/ast/target/TargetType'
import {BaseTemplateType} from '@/core/ast/template/BaseTemplate'
import {z} from "zod"
import {JsonCompositeTemplate} from "@/core/ast/template/JsonCompositeTemplate"
import {Sync} from "@/core/ast/animation/logical/impl/SyncAnimation"
import {
  AniUnitParamSpec,
  BoolParamSpec,
  NumberParamSpec,
  ParamSpec,
  StringParamSpec
} from "@/parser/json/animation/ParamSpec"
import assert from "assert"
import {isArray, isBoolean, isNumber, isObject, isString, pick} from "lodash"
import {TargetTypeComponentSpec, TargetTypeSpec} from "@/parser/json/target/TargetTypeSpec"
import {Logger, LoggerManager} from "@/core/utils/Logger"
import {Concat, ConcatPropSchema} from "@/core/ast/animation/logical/impl/ConcatAnimation"
import {toTreeString} from "@/core/utils/TreeUtils"
import {Binder, BinderRule} from "@/core/ast/target/Binder"
import {BinderSpec} from "@/parser/json/target/BinderSpec"
import {SelectAllTransform} from "@/core/ast/target/trans/SelectAllTransform"
import {SelectTransform} from "@/core/ast/target/trans/SelectTransform"
import {SliceTransform} from "@/core/ast/target/trans/SliceTransform"
import {AtTransform} from "@/core/ast/target/trans/AtTransform"
import {FilterTransform} from "@/core/ast/target/trans/FilterTransform"
import {FilterExprTransform} from "@/core/ast/target/trans/FilterExprTransform"
import {GroupTransform} from "@/core/ast/target/trans/GroupTransform"
import {ParamUsageSpec} from "@/parser/json/animation/ParamUsageSpec"
import {ForeachAnimation, ForeachPropSchema} from "@/core/ast/animation/logical/impl/ForeachAnimation"


export class JsonSpecParser extends BaseSpecParser<object> {
  private static readonly logger: Logger = LoggerManager.getLogger('JsonSpecParser')

  constructor() {
    super()
  }

  public parseTemplate(spec: AniClassSpecInput, cxt?: ParseContext): BaseTemplateType {
    JsonSpecParser.logger.debug('Parse template\n', JSON.stringify(spec, null, 2))

    cxt = cxt ?? new ParseContext()

    const result = AniClassSpec.safeParse(spec)
    if (result.success === false) {
      throw ParseError.buildWithPath(cxt.getProcessPath(), 'Parsing template. Error: ' + result.error.message)
    }
    const parsed = result.data

    JsonSpecParser.logger.debug('Parsed template result\n', JSON.stringify(parsed, null, 2))

    const imports: Import[] = this.parseImports(parsed.import, cxt)

    const targetType = parsed.targetType

    const otherParamSchema = this.visitParams(cxt, parsed.params)
    const propSchema = AniPropSchema.merge(otherParamSchema) as typeof AniPropSchema

    return new JsonCompositeTemplate({
      name: parsed.name,
      packageName: parsed.package,

      imports: imports,
      targetType: targetType,
      propSchema: propSchema,
    }, parsed.main)
  }

  public parseAnimation(spec: z.input<typeof AniUnitSpecWithParam>, cxt?: ParseContext): BaseLogicalAniType {
    JsonSpecParser.logger.debug('Parse animation\n', JSON.stringify(spec, null, 2))
    cxt = cxt ?? new ParseContext()
    return cxt.withProcessPath(() => this.visitAniUnit(cxt, spec), 'main')
  }

  public parseTargetType(spec: z.input<typeof TargetTypeSpec>, cxt?: ParseContext): TargetType {
    JsonSpecParser.logger.debug('Parse target type\n', JSON.stringify(spec, null, 2))

    cxt = cxt ?? new ParseContext()
    const result = TargetTypeSpec.safeParse(spec)
    if (result.success === false) {
      throw ParseError.buildWithPath(cxt.getProcessPath(),
          `Spec:\n${JSON.stringify(spec, null, 2)}\nError: ${result.error.message}`)
    }
    const parsed = result.data

    JsonSpecParser.logger.debug('Parsed type result\n', JSON.stringify(parsed, null, 2))

    const imports: Import[] = this.parseImports(parsed.import, cxt)

    const classList = parsed.class

    const components: TargetTypeComponent[] = []
    parsed.components.forEach((item, idx) => {
      let result
      if (isString(item)) {
        result = TargetTypeComponent.build(item, [])
      } else {
        result = cxt.withProcessPath(() => this.visitTypeComponent(cxt, item),
            'components', idx)
      }
      components.push(result)
    })

    return new TargetType(parsed.type, parsed.package, imports, classList, components)
  }

  public visitTypeComponent(cxt: ParseContext, spec: TargetTypeComponentSpec): TargetTypeComponent {
    return TargetTypeComponent.build(spec.ref, spec.class, spec.required)
  }

  public parseImports(spec: z.input<typeof ImportSpec>[], cxt?: ParseContext): Import[] {
    const imports: Import[] = []
    for (let idx = 0; idx < spec.length; idx++) {
      const item = spec[idx]
      const importItem = cxt.withProcessPath(() => this.parseOneImportItem(item, cxt), 'import', idx)
      imports.push(importItem)
    }
    return imports
  }

  private parseOneImportItem(item: z.input<typeof ImportSpec>, cxt: ParseContext): Import {
    let result
    if (isString(item)) {
      item = item.trim()
      if (WildcardPattern.safeParse(item).success) {
        const pkg = item.substring(0, item.length - 2)    // remove '.*'
        result = Import.pkg(pkg)
      } else if (FullNamePattern.safeParse(item).success) {
        result = Import.clazz(item)
      } else {
        throw ParseError.buildWithPath(cxt.getProcessPath(), `Invalid import spec: ${item}`)
      }
    } else {
      result = Import.clazz(item.class, item.as)
    }
    return result
  }

  public visitParams(cxt: ParseContext, spec: { [key: string]: ParamSpec }): z.ZodObject<{ [key: string]: z.ZodType }> {
    const propSchemaRaw: { [key: string]: z.ZodType } = {}
    Object.entries(spec).forEach(([paramName, spec], idx) => {
      propSchemaRaw[paramName] = cxt.withProcessPath(() => this.visitParam(cxt, spec),
          'params', idx)
    })
    return z.object(propSchemaRaw)
  }

  public visitParam(cxt: ParseContext, spec: ParamSpec): z.ZodType {
    let result

    result = NumberParamSpec.safeParse(spec)
    if (result.success === true) {
      const parsed = result.data
      // a single number
      if (isNumber(parsed)) {
        return z.number().default(parsed) as z.ZodType
      }
      assert(isObject(parsed) && parsed.type === 'number')
      if (parsed.value != null) {
        return z.number().default(parsed.value) as z.ZodType
      } else {
        return z.number() as z.ZodType
      }
    }

    result = StringParamSpec.safeParse(spec)
    if (result.success === true) {
      const parsed = result.data
      // a single string
      if (isString(parsed)) {
        return z.string().default(parsed) as z.ZodType
      }
      assert(isObject(parsed) && parsed.type === 'string')
      if (parsed.value != null) {
        return z.string().default(parsed.value) as z.ZodType
      } else {
        return z.string() as z.ZodType
      }
    }

    result = BoolParamSpec.safeParse(spec)
    if (result.success === true) {
      const parsed = result.data
      // a single boolean
      if (isBoolean(parsed)) {
        return z.boolean().default(parsed) as z.ZodType
      }
      assert(isObject(parsed) && parsed.type === 'bool')
      if (parsed.value != null) {
        return z.boolean().default(parsed.value) as z.ZodType
      } else {
        return z.boolean() as z.ZodType
      }
    }

    result = AniUnitParamSpec.safeParse(spec)
    if (result.success === true) {
      const parsed = result.data
      if (parsed.value != null) {
        return AniUnitSpec.default(parsed.value) as z.ZodType
      } else {
        return AniUnitSpec as z.ZodType
      }
    }

    throw ParseError.buildWithPath(cxt.getProcessPath(),
        "Failed to parse param definition spec. \n" + JSON.stringify(spec))
  }

  public visitAniUnit(cxt: ParseContext, spec: AniUnitSpecWithParam | ParamUsageSpec): BaseLogicalAniType {

    if (isString(spec)) {
      spec = this.tryParamReplace(cxt, spec) as AniUnitSpec
    }

    // process target

    const rawTarget = spec.target
    const target = cxt.withProcessPath(() => this.visitTarget(cxt, rawTarget), 'target')

    // process timing
    const rawTimingProps = pick(spec, ['duration', 'offset', 'stagger', 'yoyo', 'reverse'])
    const timingProps: TimingProps = {}
    if ('duration' in rawTimingProps) {
      if (isString(rawTimingProps.duration)) {
        const value = cxt.prop(rawTimingProps.duration)
        assert(isNumber(value))
        timingProps.duration = value
      } else {
        timingProps.duration = rawTimingProps.duration
      }
    }
    if ('offset' in rawTimingProps) {
      if (isString(rawTimingProps.offset)) {
        const value = cxt.prop(rawTimingProps.offset)
        assert(isNumber(value))
        timingProps.offset = value
      } else {
        timingProps.offset = rawTimingProps.offset
      }
    }
    if ('stagger' in rawTimingProps) {
      if (isString(rawTimingProps.stagger)) {
        const value = cxt.prop(rawTimingProps.stagger)
        assert(isNumber(value))
        timingProps.stagger = value
      } else {
        timingProps.stagger = rawTimingProps.stagger
      }
    }
    if ('yoyo' in rawTimingProps) {
      let value = rawTimingProps.yoyo
      if (isString(value)) {
        value = cxt.prop(value)
      }
      if (isBoolean(value)) {
        timingProps.yoyo = { repeat: 1, delay: 0 }
      } else {
        assert(isObject(value), `Yoyo must be an object. Got ${value}. At ${JSON.stringify(spec)}`)
        const yoyo = {...value}
        if ('repeat' in yoyo && isString(yoyo.repeat)) {
          const value = cxt.prop(yoyo.repeat)
          assert(isNumber(value), `Yoyo repeat must be a number. Got ${value}. At ${JSON.stringify(spec)}`)
          yoyo.repeat = value
        }
        if ('delay' in yoyo && isString(yoyo.delay)) {
          const value = cxt.prop(yoyo.delay)
          assert(isNumber(value), `Yoyo delay must be a number. Got ${value}. At ${JSON.stringify(spec)}`)
          yoyo.delay = value
        }
        timingProps.yoyo = Object.assign({repeat: 1, delay: 0}, yoyo)
      }
    }
    if ('reverse' in rawTimingProps) {
      if (isString(rawTimingProps.reverse)) {
        const value = cxt.prop(rawTimingProps.reverse)
        assert(isBoolean(value))
        timingProps.reverse = value
      } else {
        timingProps.reverse = rawTimingProps.reverse
      }
    }
    const timing = Timing.build(timingProps)

    let result
    if ("ref" in spec) {
      // a single ref animation
      const ref = spec.ref
      const props = Object.assign({}, spec, {
        id: spec.id,
        target,
        timing,
      })
      // for each key in props
      for (const key of Object.keys(props)) {
        let value = props[key]
        if (isString(value)) {
          value = this.tryParamReplace(cxt, value)
          props[key] = value
        }
      }
      result = Ref(ref, props)

    } else if ("sync" in spec) {
      assert(isArray(spec.sync), `Sync must be an array. Got ${spec.sync}. At ${spec}`)
      const animations = spec.sync.map((item, idx) => {
        return cxt.withProcessPath(() => this.visitAniUnit(cxt, item),
            'sync', idx)
      })
      result = Sync({
        id: spec.id,
        target,
        timing,
        animations,
      })

    } else if ("concat" in spec) {
      assert(isArray(spec.concat), `Concat must be an array. Got ${spec.concat}. At ${spec}`)
      const animations = spec.concat.map((item, idx) => {
        return cxt.withProcessPath(() => this.visitAniUnit(cxt, item),
            'concat', idx)
      })
      result = Concat({
        id: spec.id,
        target,
        timing,
        animations,
      } as z.input<typeof ConcatPropSchema>)

    } else if ("child" in spec) {
      let child = spec.child
      if (isString(child)) {
        child = this.tryParamReplace(cxt, child)
      }
      const animation = cxt.withProcessPath(() => this.visitAniUnit(cxt, child), 'child')
      result = Sync({
        id: spec.id,
        target,
        timing,
        animations: [animation],
      })

    } else if ('foreach' in spec) {
      const props: z.input<typeof ForeachPropSchema> = {
        id: spec.id,
        target,
        timing,
      }
      let foreach = spec.foreach
      if (isString(foreach)) {
        foreach = this.tryParamReplace(cxt, foreach)
      }
      props.animation = cxt.withProcessPath(() => this.visitAniUnit(cxt, foreach), 'foreach')
      // groupBy
      if ('groupBy' in spec) {
        let groupBy = spec.groupBy
        groupBy = this.tryParamReplace(cxt, groupBy)
        props.groupBy = groupBy
      }
      // start
      if ('start' in spec) {
        let start = spec.start
        start = this.tryParamReplace(cxt, start)
        props.start = start
      }
      result = new ForeachAnimation(props)

    } else {
      throw ParseError.buildWithPath(cxt.getProcessPath(),
          "Unknown animation definition spec.")
    }

    JsonSpecParser.logger.debug(`Parse result:\n${toTreeString(result)}`)
    return result
  }

  public visitTarget(cxt: ParseContext, spec: TargetSpec): Target {
    const transforms: Transform[] = []
    if (typeof spec === "string") {
      // default transform is filter
      const filter = this.tryParamReplace(cxt, spec)
      assert(isString(filter), `Param value of filter must be a string. Got ${filter}. At ${spec}`)
      const trans = new FilterTransform(filter)
      transforms.push(trans)
    } else if (spec instanceof Array) {
      for (const item of spec) {
        const trans = this.createTransform(cxt, item)
        transforms.push(trans)
      }
    } else if (spec == null) {
      // do nothing
    } else if (typeof spec === 'object') {
      const trans = this.createTransform(cxt, spec as TransformSpec)
      transforms.push(trans)
    } else {
      throw ParseError.buildWithPath(cxt.getProcessPath(),
          "Unknown target transform spec.")
    }
    return Target.build(transforms)
  }

  createTransform(cxt: ParseContext, tran: TransformSpec): Transform {
    if ('filter' in tran) {
      const filter = this.tryParamReplace(cxt, tran.filter)
      assert(isString(filter), `Param value of filter must be a string. Got ${filter}. At ${JSON.stringify(tran)}`)
      return new FilterTransform(filter)

    } else if ('selectAll' in tran) {
      const selectAll = this.tryParamReplace(cxt, tran.selectAll)
      assert(isString(selectAll), `Param value of selectAll must be a string. Got ${selectAll}. At ${JSON.stringify(tran)}`)
      return new SelectAllTransform(selectAll)

    } else if ('select' in tran) {
      const select = this.tryParamReplace(cxt, tran.select)
      assert(isString(select), `Param value of select must be a string. Got ${select}. At ${JSON.stringify(tran)}`)
      return new SelectTransform(select)

    } else if ('filterExpr' in tran) {
      const filterExpr = this.tryParamReplace(cxt, tran.filterExpr)
      assert(isString(filterExpr), `Param value of filterExpr must be a string. Got ${filterExpr}. At ${JSON.stringify(tran)}`)
      return new FilterExprTransform(filterExpr)

    } else if ('sortBy' in tran) {
      const sortBy = this.tryParamReplace(cxt, tran.sortBy)
      assert(isString(sortBy), `Param value of sortBy must be a string. Got ${sortBy}. At ${JSON.stringify(tran)}`)
      let order = tran.order
      if (isString(order)) {
        const resultOrder = this.tryParamReplace(cxt, order)
        assert(resultOrder === 'asc' || resultOrder === 'desc',
            'Only support "asc" or "desc" for order param.')
        order = resultOrder
      } else if (isArray(order)) {
        // do nothing
      } else {
        order = 'asc'
      }
      return new SortTransform(sortBy, order)

    } else if ('slice' in tran) {
      let slice = tran.slice
      if (isString(slice)) {
        const value = cxt.prop(slice)
        assert(isArray(value), `Slice param must be an array. Got ${value}. At ${slice}`)
        slice = value
      }
      if (isArray(slice)) {
        // each them might be number, string (need replace), or null
        for (let i = 0; i < slice.length; i++) {
          let item = slice[i]
          if (isString(item)) {
            item = this.tryParamReplace(cxt, item)
            slice[i] = item
          }
        }
      }
      const [start, end, step] = slice
      assert(isNumber(start), `Slice start must be a number. Got ${start}. At ${slice}`)
      assert(end == null || isNumber(end), `Slice end must be a number or null. Got ${end}. At ${slice}`)
      assert(step == null || isNumber(step), `Slice step must be a number or null. Got ${step}. At ${slice}`)
      return new SliceTransform(start, end as (number | null), step as (number | null))

    } else if ('at' in tran) {
      let index = tran.at
      if (isString(index)) {
        const value = cxt.prop(index)
        assert(isNumber(value))
        index = value
      }
      assert(isNumber(index), `At index must be a number. Got ${index}. At ${tran}`)
      return new AtTransform(index)

    } else if ('groupBy' in tran) {
      const groupBy = this.tryParamReplace(cxt, tran.groupBy)
      assert(isString(groupBy), `Param value of groupBy must be a string. Got ${groupBy}. At ${JSON.stringify(tran)}`)
      return new GroupTransform(groupBy)
    }

    throw ParseError.buildWithPath(cxt.getProcessPath(),
        "Unknown target transform spec.\n" + JSON.stringify(tran))
  }

  private tryParamReplace(cxt: ParseContext, str: string): any {
    let result = str
    if (str.startsWith('$')) {
      result = cxt.prop(str)
    } else if (str.includes('$')) {
      // get all $xxx but not \$xxx
      const matches = str.match(/(?<!\\)\$[a-zA-Z0-9_]+/g)
      if (matches) {
        for (const match of matches) {
          const value = cxt.prop(match)
          assert(isString(value) || isBoolean(value) || isNumber(value), `Param value of a string placeholder must be a string, boolean or number. Got ${value}. At ${str}`)
          result = result.replace(match, String(value))
        }
      }
    }
    if (isString(result)) {
      // change \$ to $ and \\ to \
      result = result.replace(/\\\$/g, '$').replace(/\\\\/g, '\\')
    }
    return result
  }

  public parseBinder(spec: BinderSpec, cxt?: ParseContext): Binder {
    JsonSpecParser.logger.debug('Parse binder\n', JSON.stringify(spec, null, 2))

    cxt = cxt ?? new ParseContext()

    const result = BinderSpec.safeParse(spec)
    if (result.success === false) {
      throw ParseError.buildWithPath(cxt.getProcessPath(), 'Paring binder. Error: ' + result.error.message)
    }
    const parsed = result.data

    JsonSpecParser.logger.debug('Parsed binder result\n', JSON.stringify(parsed, null, 2))

    const rules = parsed.rules.map((rule, idx) => {
      return cxt.withProcessPath(() => {
        if (rule.id != null) {
          // only this case we have a single binder
          return BinderRule.buildSingleBind({
            from: rule.from,
            to: rule.to,
            id: rule.id,
            classList: rule.classList,
            data: rule.data
          })
        }
        return BinderRule.buildBatchBind({
          from: rule.from,
          to: rule.to,
          classList: rule.classList,
          data: rule.data,
          dataList: rule.dataList
        })
      }, 'rules', idx)
    })

    return Binder.build(parsed.from, parsed.to, rules)
  }
}
