import {Transform, TransformType} from "@/core/ast/target/Transform"
import {PredFunc, TargetSelection} from "@/core/ast/target/TargetSelection"
import {isFunction, isString} from "lodash"
import {LoggerManager} from "@/core/utils/Logger"
import {TargetModel, VirtualElementType} from "@/core/ast/target/TargetModel"

export class FilterExprTransform extends Transform {
  private static readonly logger = LoggerManager.getLogger('FilterExprTransform')

  readonly predicate: string | PredFunc

  constructor(predicate: string | PredFunc) {
    super()
    this.predicate = predicate
  }

  get type(): TransformType {
    return 'filterExpr'
  }

  execute(selection: TargetSelection): TargetSelection {
    if (isString(this.predicate)) {
      const predicate = this.predicate
          .replaceAll('@id', '__el__.id')
          .replaceAll('@class', 'Array.from(__el__.classList)')
          .replaceAll('@index', '__idx__')
          .replaceAll('@data', '(__el__.data ?? {})')
          .replaceAll('@el', '__el__')
          .replaceAll('@coll', '__collection__')
      const targetModel = selection.targetModel
      FilterExprTransform.logger.debug(`Filtering ${selection} with predicate ${predicate}.`)
      return selection.filter((vNode, idx, vNodes) => {
        try {
          return predicateEval(predicate, vNode, idx, vNodes, targetModel)
        } catch (e) {
          FilterExprTransform.logger.error(`Error while evaluating predicate expression ${predicate} on element ${vNode}.`, e)
          throw e
        }
      })
    } else if (isFunction(this.predicate)) {
      return selection.filter(this.predicate)
    } else {
      throw new Error(`Predicate is neither a string nor a function, but got ${this.predicate}`)
    }
  }

  toString() {
    return `filterExpr(${this.predicate})`
  }

  clone(): Transform {
    return new FilterExprTransform(this.predicate)
  }
}

/* eslint-disable @typescript-eslint/no-unused-vars */
function predicateEval(__predicate__: string,
                       __el__: VirtualElementType,
                       __idx__: number,
                       __collection__: VirtualElementType[],
                       __model__: TargetModel<unknown>): boolean {
  console.log(__el__, __idx__, __el__.data, __predicate__, eval(__predicate__))
  return eval(__predicate__)
}
/* eslint-enable @typescript-eslint/no-unused-vars */
