import {TargetSelection} from "@/core/ast/target/TargetSelection"
import {TargetType} from "@/core/ast/target/TargetType"
import {GaiaSession} from "@/session/GaiaSession"
import {TypeModelTreeNode} from "@/core/ast/target/TypeModelTree"
import assert from "assert"
import {Logger, LoggerManager} from "@/core/utils/Logger"
import {toTreeString} from "@/core/utils/TreeUtils"
import {Import} from "@/core/ast/animation/other/Import"
import {domElToStr, getFormattedXmlString} from "@/core/utils/DomUtils"

export type DataType = {[key: string]: any}
export type VirtualElementType = Element & { data: DataType }

export abstract class TargetModel<ObjType> {
  protected static logger: Logger = LoggerManager.getLogger("TargetModel")

  session: GaiaSession = null

  rootTargetType: TargetType = null

  // root of the tree
  typeModelTree: TypeModelTreeNode = null
  // vGroup id -> type node
  typeModelNodeMap: Map<string, TypeModelTreeNode> = null
  typeModelDom: VirtualElementType = null

  realElementIdSet: Set<string> = new Set()

  virtualModel: VirtualElementType = null
  rootSelection: TargetSelection = null

  protected readonly origin: ObjType

  protected constructor(origin: ObjType) {
    this.origin = origin
  }

  getOrigin(): ObjType {
    return this.origin
  }

  init(session: GaiaSession): void {
    this.session = session

    this.initRootType()
    assert(this.rootTargetType != null, "rootTargetType is not initialized by initRootType()")
    TargetModel.logger.debug(`Root type: ${this.rootTargetType.fullName}`)

    this.processIdSet()
    this.initTypeModelTree()
    this.initTypeModelDom()
    this.initVirtualModelBackbone()
    this.completeVirtualModel()

    TargetModel.logger.debug(`Virtual model created.\n${getFormattedXmlString(this.virtualModel)}`)
  }

  abstract initRootType(): void
  abstract processIdSet(): void
  abstract completeVirtualModel(): void
  abstract getRealElementById(id: string): ObjType
  abstract getBox(id: string): {x: number, y: number, height: number, width: number}

  private initTypeModelTree(): void {
    // build targetModelTree & targetModelTreeMap using root target type
    let nodeId = 0
    const targetLibrary = this.session.typeLibrary
    const nodeMap = new Map()
    const samePackageImport = Import.pkg(this.rootTargetType.packageName)
    const expandTreeNode = (cur: TypeModelTreeNode) => {
      // TargetModel.logger.trace(`Expanding node ${cur.toOneLineString()}. `
      //     + `Children: [${cur.targetType.components.map(c => c.ref).join(', ')}].`)
      // process self
      nodeMap.set(cur.nodeId, cur)

      if (cur.targetType.components.length === 0) {
        return
      }
      // process children
      const caller = targetLibrary.createCaller()
          .setImport(samePackageImport)
          .setAllImport(cur.targetType.imports)
      for (const component of cur.targetType.components) {
        const typeResult = caller.safeGet(component.ref)
        let componentType: TargetType
        switch (typeResult.status) {
          case "ok": componentType = typeResult.result; break
          case "not-found": throw new Error(`Type ${component.ref} not found. In ${cur.targetType.fullName}.`)
          case "not-imported": throw new Error(`Type ${component.ref} not imported. In ${cur.targetType.fullName}.`)
        }
        // TargetModel.logger.trace(`Child ${componentType.name}(${nodeId}) of ${cur.toOneLineString()} created.`)
        const child = new TypeModelTreeNode(this.getTypeNodeId(nodeId), componentType, cur, component)
        nodeId++
        cur.children.push(child)
        expandTreeNode(child)
      }
    }
    this.typeModelTree = new TypeModelTreeNode(this.getTypeNodeId(nodeId), this.rootTargetType)
    nodeId++
    expandTreeNode(this.typeModelTree)
    this.typeModelNodeMap = nodeMap

    TargetModel.logger.trace("Type model tree created.\n", toTreeString(this.typeModelTree))
  }

  getTypeNodeId(nodeId: number): string {
    return `g-${nodeId}`
  }

  createVirtualGroup(type: string, id: string, classList: string[], data: DataType = {}): VirtualElementType {
    const vGroup = Object.assign(this.session.document.createElement('g'), {data})
    vGroup.setAttribute('id', id)
    vGroup.setAttribute('class', classList.join(' '))
    // mount data to attributes
    for (const [key, value] of Object.entries(data)) {
      if (vGroup.hasAttribute(key)) {
        console.warn(`Attribute ${key} already exists in ${vGroup}. Change it to data-${key}`)
        vGroup.setAttribute(`data-${key}`, String(value))
      } else {
        vGroup.setAttribute(key, String(value))
      }
    }
    return vGroup
  }

  initTypeModelDom(): void {
    /* Build tree-like type model dom according to typeModelTree */

    const processNode = (node: TypeModelTreeNode, parentVEl: VirtualElementType | null, classList: string[]) => {
      const vGroup = this.createVirtualGroup(node.targetType.name, String(node.nodeId), node.classList)

      if (parentVEl !== null) {
        parentVEl.appendChild(vGroup)
      } else {
        // is root
        vGroup.setAttribute('isRoot', 'true')
      }

      for (const child of node.children) {
        const childClassList = child.classList.concat(classList)
        processNode(child, vGroup, childClassList)
      }

      return vGroup
    }
    this.typeModelDom = processNode(this.typeModelTree, null, this.typeModelTree.classList)
  }

  initVirtualModelBackbone(): void {
    this.virtualModel = this.createVirtualGroup(this.rootTargetType.fullName, '--g-root', [], {})
    TargetModel.logger.trace("Virtual model backbone created.\n", getFormattedXmlString(this.virtualModel))
  }

  queryOneVGroup(selector: string): VirtualElementType {
    const vGroupList = this.querySelectorAll(selector)
    assert(vGroupList.length !== 0, `Virtual group element not found: "${selector}".`)
    assert(vGroupList.length < 2, `Multiple virtual group elements found: "${selector}".`)
    return vGroupList[0]
  }

  queryOneTypeNode(selector: string): TypeModelTreeNode {
    const vGroup = this.queryOneVGroup(selector)
    return this.vGroupToTypeNode(vGroup)
  }

  /**
   * Selects one element in typeModelDom by selector.
   * @param selector CSS selector
   * @protected
   */
  protected querySelector(selector: string): VirtualElementType {
    return this.typeModelDom.querySelector(selector)
  }

  protected querySelectorAll(selector: string): Array<VirtualElementType> {
    return Array.from(this.typeModelDom.querySelectorAll(selector))
  }

  protected vGroupToTypeNode(vGroup: VirtualElementType): TypeModelTreeNode {
    return this.typeModelNodeMap.get(vGroup.id)
  }

  getRealElementByVNode(vNode: VirtualElementType): ObjType {
    const targetEl = this.getRealElementById(vNode.id)
    if (targetEl == null) {
      throw new Error(`Cannot get elements from virtual node ${domElToStr(vNode)}. `
          + `Might built virtual model incorrectly.`)
    }
    return targetEl
  }
}
