import {z} from "zod"
import {ParamUsageSpec} from "@/parser/json/animation/ParamUsageSpec"
import {TargetSpec} from "@/parser/json/animation/TargetSpec"
import {YoyoSpec} from "@/parser/json/animation/YoyoSpec"

export const AniUnitBaseSpec = z.object({
  // misc
  id: z.union([z.number(), z.string()]).optional(),
  // target
  target: z.union([TargetSpec, ParamUsageSpec]).optional(),
  // timing
  duration: z.union([z.number().min(0), ParamUsageSpec]).optional(),
  offset: z.union([z.number(), ParamUsageSpec]).optional(),
  stagger: z.union([z.number().min(0), ParamUsageSpec]).optional(),
  reverse: z.boolean().optional(),
  yoyo: z.union([z.literal(true), YoyoSpec, ParamUsageSpec]).optional(),
})


export type SyncSpec = z.infer<typeof AniUnitBaseSpec> & {
  // we need a ? here. but sync is not optional
  sync?: AniUnitSpec[]
}
export const SyncSpec: z.ZodType<SyncSpec> = AniUnitBaseSpec.extend({
  sync: z.lazy(() => z.array(AniUnitSpec))
})

export type SyncSpecWithParam = z.infer<typeof AniUnitBaseSpec> & {
  // we need a ? here. but sync is not optional
  sync?: (AniUnitSpecWithParam | ParamUsageSpec)[]
}
export const SyncSpecWithParam: z.ZodType<SyncSpecWithParam> = AniUnitBaseSpec.extend({
  sync: z.lazy(() => z.array(z.union([AniUnitSpecWithParam, ParamUsageSpec])))
})


export type ConcatSpec = z.infer<typeof AniUnitBaseSpec> & {
  // we need a ? here. but concat is not optional
  concat?: AniUnitSpec[]
}
export const ConcatSpec: z.ZodType<ConcatSpec> = AniUnitBaseSpec.extend({
  concat: z.lazy(() => AniUnitSpec.array())
})
export type ConcatSpecWithParam = z.infer<typeof AniUnitBaseSpec> & {
  // we need a ? here. but concat is not optional
  concat?: (AniUnitSpecWithParam | ParamUsageSpec)[]
}
export const ConcatSpecWithParam: z.ZodType<ConcatSpecWithParam> = AniUnitBaseSpec.extend({
  concat: z.lazy(() => z.array(z.union([AniUnitSpecWithParam, ParamUsageSpec])))
})


export const RefSpec = z.intersection(
    z.object({
      ref: z.union([z.string(), ParamUsageSpec]),
    }),
    z.record(z.string(), z.any()),
    AniUnitBaseSpec,
)
export type RefSpec = z.infer<typeof RefSpec>


export type ChildSpec = z.infer<typeof AniUnitBaseSpec> & {
  child?: AniUnitSpec
}
export const ChildSpec: z.ZodType<ChildSpec> = AniUnitBaseSpec.extend({
  child: z.lazy(() => AniUnitSpec)
})
export type ChildSpecWithParam = z.infer<typeof AniUnitBaseSpec> & {
  child?: AniUnitSpecWithParam | ParamUsageSpec
}
export const ChildSpecWithParam: z.ZodType<ChildSpecWithParam> = AniUnitBaseSpec.extend({
  child: z.lazy(() => z.union([AniUnitSpecWithParam, ParamUsageSpec]))
})

export type ForeachSpec = z.infer<typeof AniUnitBaseSpec> & {
  foreach?: AniUnitSpec
  groupBy?: string
  start?: string
}
export const ForeachSpec: z.ZodType<ForeachSpec> = AniUnitBaseSpec.extend({
  foreach: z.lazy(() => AniUnitSpec),
  groupBy: z.string().optional(),
  start: z.string().optional(),
})
export type ForeachSpecWithParam = z.infer<typeof AniUnitBaseSpec> & {
  foreach?: AniUnitSpecWithParam | ParamUsageSpec
  groupBy?: string | ParamUsageSpec
  start?: 'with prev' | 'after prev' | ParamUsageSpec
}
export const ForeachSpecWithParam: z.ZodType<ForeachSpecWithParam> = AniUnitBaseSpec.extend({
  foreach: z.lazy(() => z.union([AniUnitSpecWithParam, ParamUsageSpec])),
  groupBy: z.union([z.string(), ParamUsageSpec]).optional(),
  start: z.union([z.enum(['with prev', 'after prev']), ParamUsageSpec]).optional(),
})


export const AniUnitSpec = z.union([
  SyncSpec,
  ConcatSpec,
  ForeachSpec,
  ChildSpec,
  RefSpec
])
export type AniUnitSpec = z.infer<typeof AniUnitSpec>

export const AniUnitSpecWithParam = z.union([
  SyncSpecWithParam,
  ConcatSpecWithParam,
  ForeachSpecWithParam,
  ChildSpecWithParam,
  RefSpec,
])
export type AniUnitSpecWithParam = z.infer<typeof AniUnitSpecWithParam>
