import {z} from "zod"
import {EffectPropSchema} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {CompositeTemplate} from "@/core/ast/template/CompositeTemplate"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {MovePropSchema} from "@/preset/template/common/MoveTemplate"
import {Ref, RefAnimation} from "@/core/ast/animation/logical/impl/RefAnimation"

export const MoveFromPropSchema = EffectPropSchema.extend({
  x: z.number().or(z.string()).optional(),
  y: z.number().or(z.string()).optional(),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class MoveFromTemplate extends CompositeTemplate<typeof MoveFromPropSchema, RefAnimation<typeof MovePropSchema>> {
  constructor() {
    super({
      name: "MoveFrom",
      packageName: "gaia.common",
      imports: [],
      targetType: "Any",
      propSchema: MoveFromPropSchema,
    })
  }

  instantiate<ObjType, AniType>(cxt: ResolveContext<ObjType, AniType>,
                                props: z.output<typeof MoveFromPropSchema>): RefAnimation<typeof MovePropSchema> {
    const {id, target, timing, x, y, ease} = props
    return Ref.builder<typeof MovePropSchema>()
        .ref('Move')
        .props({id, target, timing, from: {x, y}, ease})
        .build()
  }
}
