import {CompileExec} from "@/session/CompileExec"
import {TargetModel} from "@/core/ast/target/TargetModel"

export class SynthesisContext<ObjType, AniType> {
  readonly compileExec: CompileExec<ObjType, AniType>

  constructor(compileExec: CompileExec<ObjType, AniType>) {
    this.compileExec = compileExec
  }

  get targetModel(): TargetModel<ObjType> {
    return this.compileExec.targetModel
  }
}
