import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {ZoomPropSchema} from "@/preset/template/common/ZoomTemplate"


export class ZoomImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof ZoomPropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // target
    const elements = this.elements
    // timing
    const stagger = this.stagger

    const tl = gsap.timeline()
    elements.forEach((el, i) => {
      tl.add(this.createTw(el, cxt), i === 0 ? 0 : ('<' + stagger))
    })
    return tl
  }

  createTw(el: SVGElement, cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // effect
    const {type, ease} = this.props()

    let tw

    // stagger here won't take effect as this is a single element
    const vars = this.mergeYoyo(this.mergeDuration({scale: 0, transformOrigin: 'center center', ease}))
    switch (type) {
      case 'in': tw = gsap.from(el, vars); break
      case 'out': tw = gsap.to(el, vars); break
      default: throw new Error(`Wrong value of type: ${type}. Must be 'in' or 'out'. At ${this}`)
    }

    return tw
  }
}

