import {TargetModel, VirtualElementType} from "@/core/ast/target/TargetModel"
import {isFunction, isString} from "lodash"
import {LoggerManager} from "@/core/utils/Logger"

export type PredFunc = (vNode: VirtualElementType, idx: number, vNodes: VirtualElementType[]) => boolean
export type SortFunc = (vNode1: VirtualElementType, vNode2: VirtualElementType) => number

export type KeyType = string | number | boolean
export type MapFunc = (vNode: VirtualElementType, idx: number, vNodes: VirtualElementType[]) => KeyType
export type GroupCreator = (targetModel: TargetModel<unknown>, group: VirtualElementType[], key: KeyType) => VirtualElementType

export class TargetSelection {
  private static readonly logger = LoggerManager.getLogger('TargetSelection')

  public static readonly DEFAULT_GROUP_CREATOR: GroupCreator = (targetModel, groups, key) => {
    const group = targetModel.createVirtualGroup('Any', '', [], {key})
    for (const g of groups) {
      const newEl = g.cloneNode(true) as VirtualElementType
      Object.assign(newEl, {data: g.data})
      group.append(newEl)
    }
    return group
  }

  readonly elements: VirtualElementType[] = null   // the collection of group/element
  readonly targetModel: TargetModel<unknown>
  readonly parent: TargetSelection

  private _realElements: unknown[] = null

  constructor(elements: VirtualElementType[],
              targetModel: TargetModel<unknown>,
              parent: TargetSelection = null) {
    this.elements = elements
    this.targetModel = targetModel
    this.parent = parent
  }

  select(selector: string): TargetSelection {
    for (const vElParent of this.elements) {
      const child = vElParent.querySelector<VirtualElementType>(selector)
      if (child != null) {
        return new TargetSelection([child], this.targetModel, this)
      }
    }
    return new TargetSelection([], this.targetModel, this)
  }

  selectAll(selector: string): TargetSelection {
    const childVElGroups = this.elements
        .map(g => Array.from(g.querySelectorAll<VirtualElementType>(selector)))
        .flat()
    return new TargetSelection(childVElGroups, this.targetModel, this)
  }

  filter(selector: string): TargetSelection
  filter(predicate: PredFunc): TargetSelection
  filter(selectorOrPredicate: string | PredFunc): TargetSelection {

    TargetSelection.logger.trace("Filtering selection with "
        + (typeof selectorOrPredicate === 'function' ? '[native function]' : selectorOrPredicate)
        + ".\n Parent selection:\n"
        + this.toString())

    let childVNodes: VirtualElementType[] = null

    if (isString(selectorOrPredicate)) {
      // is selector
      childVNodes = this.elements.filter(g => g.matches(selectorOrPredicate))
    } else if (isFunction(selectorOrPredicate)) {
      // is predicate function
      childVNodes = this.elements.filter((g, idx) => selectorOrPredicate(g, idx, this.elements))
    } else {
      throw new Error(`predicate must be a string or a function, but got ${selectorOrPredicate}`)
    }

    const result = new TargetSelection(childVNodes, this.targetModel, this)
    TargetSelection.logger.trace(`Filtering result: ${result.toString()}`)
    return result
  }

  sort(comparator: SortFunc): TargetSelection {
    const newGroups = this.elements.slice().sort(comparator)
    return new TargetSelection(newGroups, this.targetModel, this)
  }

  slice(start: number, end: number | null = null, step: number | null = null): TargetSelection {
    // a slice implementation like python
    // check null
    if (end == null) {
      end = this.elements.length
    }
    if (step == null) {
      step = 1
    }
    // check negative index
    if (start < 0) {
      start = this.elements.length + start
    }
    if (end < 0) {
      end = this.elements.length + end
    }
    // check out of range
    if (start < 0 || start >= this.elements.length) {
      throw new Error(`Start index ${start} out of range. Collection size is ${this.elements.length}. At Selection(${this.toString()})`)
    }
    if (end < 0 || end > this.elements.length) {
      throw new Error(`End index ${end} out of range. Collection size is ${this.elements.length}. At Selection(${this.toString()})`)
    }
    // check step
    if (step === 0) {
      throw new Error(`Step cannot be zero. At ${this}`)
    }
    // check step and start/end
    if (step > 0) {
      if (start > end) {
        throw new Error(`Start index ${start} is larger than end index ${end}. At ${this}`)
      }
    } else {
      if (start < end) {
        throw new Error(`Start index ${start} is smaller than end index ${end}. At ${this}`)
      }
    }
    // slice
    const newGroups = []
    for (let i = start; i < end; i += step) {
      newGroups.push(this.elements[i])
    }
    return new TargetSelection(newGroups, this.targetModel, this)
  }

  group(mapper: MapFunc, creator: GroupCreator = TargetSelection.DEFAULT_GROUP_CREATOR): TargetSelection {
    const groupMap = new Map<string | number | boolean, VirtualElementType[]>()
    this.elements.forEach((group, idx) => {
      const key = mapper(group, idx, this.elements)
      if (!groupMap.has(key)) {
        groupMap.set(key, [])
      }
      groupMap.get(key).push(group)
    })
    // console.log('>>>', this.elements, groupMap)
    const newGroups = []
    for (const [key, groupList] of groupMap) {
      // create a new group element whose children are elements in groupList
      const newGroup = creator(this.targetModel, groupList, key)
      newGroups.push(newGroup)
    }
    return new TargetSelection(newGroups, this.targetModel, this)
  }

  get size(): number {
    return this.elements.length
  }

  get realElements(): unknown[] {
    if (this._realElements == null) {
      this._realElements = this.elements.map(e => this.targetModel.getRealElementByVNode(e))
    }
    return this._realElements
  }

  toString(): string {
    return this.elements.map(group => `${group.tagName}#${group.id}`).join(', ')
  }
}
