import {z} from "zod"
import {Sync, SyncAnimation} from "@/core/ast/animation/logical/impl/SyncAnimation"
import {AniPropSchema} from "@/core/ast/animation/logical/LogicalAnimation"
import {Ref} from "@/core/ast/animation/logical/impl/RefAnimation"
import {CompositeTemplate} from "@/core/ast/template/CompositeTemplate"
import {ResolveContext} from "@/core/exec/resolve/ResolveContext"
import {FadePropSchema} from "@/preset/template/common/FadeTemplate"
import {WipePropSchema} from "@/preset/template/common/WipeTemplate"

export const BarLabelEnterPropSchema = AniPropSchema.extend({
  from: z.enum(['bottom', 'top', 'left', 'right'])
      .default('bottom')
      .describe("Wipe direction (default: bottom)"),
  ease: z.string()
      .default('power1.out')
      .describe("Easing function of animation (default: power1.out)"),
})

export class BarLabelEnterTemplate extends CompositeTemplate<typeof BarLabelEnterPropSchema, SyncAnimation> {
  constructor() {
    super({
      name: "BarLabelEnter",
      packageName: "gaia.chart",
      imports: [],
      targetType: "gaia.chart.BarChart",
      propSchema: BarLabelEnterPropSchema,
    })
  }

  instantiate(cxt: ResolveContext<unknown, unknown>,
              props: z.output<typeof BarLabelEnterPropSchema>): SyncAnimation {
    const {id, target, timing, from, ease} = props

    // const labelFadeIn = Ref<typeof FadePropSchema>({
    //   ref: 'Fade',
    //   target: 'label',
    //   type: 'in',
    //   ease: ease,
    // })

    const labelFadeIn = Ref.builder<typeof FadePropSchema>()
        .ref('Fade')
        .selectAll(".BarMark .Label")
        .props({type: 'in', ease})
        .build()

    const barWipe = Ref.builder<typeof WipePropSchema>()
        .ref('Wipe')
        .selectAll(".BarMark .Bar")
        .props({from, ease})
        .build()

    return Sync.builder()
        .props({id, target, timing})
        .append(labelFadeIn)
        .append(barWipe)
        .build()
  }
}
