import {BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {PhysicalAnimation} from "@/core/ast/animation/physical/PhysicalAnimation"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"

export abstract class SynthesisRule<ObjType, LogicalAniType extends BaseLogicalAniType, AniType> {
  readonly ruleName: string
  readonly targetName: string

  protected constructor(ruleName: string, targetName: string) {
    this.ruleName = ruleName
    this.targetName = targetName
  }

  abstract apply(cxt: SynthesisContext<ObjType, AniType>, input: LogicalAniType): PhysicalAnimation<ObjType, LogicalAniType, AniType> | null
}
