import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {MovePropSchema} from "@/preset/template/common/MoveTemplate"
import {BaseSVGTargetModel} from "@/core/ast/target/impl/svg/BaseSVGTargetModel"


export class MoveImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof MovePropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // target
    const elements = this.elements
    // timing
    const stagger = this.stagger

    const tl = gsap.timeline()
    elements.forEach((el, i) => {
      tl.add(this.createTw(el, cxt), i === 0 ? 0 : ('<' + stagger))
    })
    return tl
  }

  createTw(el: SVGElement, cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // effect
    const {from, to, ease} = this.props()

    // save original box because it changes the position
    const model = cxt.targetModel as BaseSVGTargetModel
    model.saveOriginalBox(el as SVGGraphicsElement)

    let tw: GsapAnimation

    if (from != null && to != null) {
      const fromVars = {...from}
      const toVars = this.mergeYoyo(this.mergeDuration({...to, ease}))
      tw = gsap.fromTo(el, fromVars, toVars)

    } else if (from != null) {
      const fromVars = this.mergeYoyo(this.mergeDuration({...from, ease}))
      tw = gsap.from(el, fromVars)

    } else if (to != null) {
      const toVars = this.mergeYoyo(this.mergeDuration({...to, ease}))
      tw = gsap.to(el, toVars)

    } else {
      throw new Error(`Either from or to must be specified for ${this}`)
    }

    return tw
  }
}

