import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {BaseSVGTargetModel} from "@/core/ast/target/impl/svg/BaseSVGTargetModel"
import {CameraPropSchema} from "@/preset/template/camera/CameraTemplate"
import {isString} from "lodash"


export class CameraImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof CameraPropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // effect
    const {from, to, origin, reset, ease} = this.props()

    // save original box because it changes the position
    const model = cxt.targetModel as BaseSVGTargetModel
    const svg = model.getOrigin()

    if (reset) {
      const toVars = this.mergeYoyo(this.mergeDuration({scale: 1, x: 0, y: 0, ease}))
      return gsap.to(svg, toVars)
    }

    let transformOrigin = null
    // if there is scale in from or to, set transform origin
    if ((from != null && from.scale != null) || (to != null && to.scale != null)) {
      transformOrigin = origin
    }

    let tw: GsapAnimation

    if (from != null && to != null) {
      const fromVars: {[key: string]: any} = {...this.refine(from)}
      const toVars = this.mergeYoyo(this.mergeDuration({...this.refine(to), ease}))
      if (transformOrigin != null) {
        fromVars.transformOrigin = transformOrigin
        toVars.transformOrigin = transformOrigin
      }
      tw = gsap.fromTo(svg, fromVars, toVars)

    } else if (from != null) {
      const fromVars = this.mergeYoyo(this.mergeDuration({...this.refine(from), ease}))
      if (transformOrigin != null) {
        fromVars.transformOrigin = transformOrigin
      }
      tw = gsap.from(svg, fromVars)

    } else if (to != null) {
      const toVars = this.mergeYoyo(this.mergeDuration({...this.refine(to), ease}))
      if (transformOrigin != null) {
        toVars.transformOrigin = transformOrigin
      }
      tw = gsap.to(svg, toVars)

    } else {
      throw new Error(`Either from or to must be specified for ${this}`)
    }

    return tw
  }

  refine(toOrFrom: {x?: string | number, y?: string | number}) {
    if (isString(toOrFrom.x)) {
      toOrFrom.x = this.reverseSign(toOrFrom.x)
    }
    if (isString(toOrFrom.y)) {
      toOrFrom.y = this.reverseSign(toOrFrom.y)
    }
    return toOrFrom
  }

  reverseSign(str: string) {
    return str.replace('+', '<temp>').replace('-', '+').replace('<temp>', '-')
  }
}

