import {Proto} from "@/module/Proto"


export class Package {
  readonly packageName: string
  readonly parent: Package | null

  private classMap: Map<string, unknown> = new Map()
  private subPackageMap: Map<string, Package> = new Map()

  private constructor(name: string, parent: Package) {
    this.packageName = name
    this.parent = parent
  }

  static createPackage(name: string, parent: Package) {
    return new Package(name, parent)
  }

  static createDefaultPackage(): Package {
    return new Package('', null)
  }

  isDefault(): boolean {
    return this.parent === null
  }

  registerProto<T extends Proto<T>>(proto: T): Package {
    this.classMap.set(proto.name, proto)
    return this
  }

  unregisterProto(name: string): boolean {
    return this.classMap.delete(name)
  }

  queryAllProto<T extends Proto<T>>(): T[] {
    return Array.from(this.classMap.values()) as T[]
  }

  queryProto<T extends Proto<T>>(clsName: string): T | null {
    return this.classMap.get(clsName) as T ?? null
  }

  addSubPackage(pkg: Package) {
    this.subPackageMap.set(pkg.packageName, pkg)
  }

  querySubPackages(): Array<Package> {
    return Array.from(this.subPackageMap.values())
  }

  querySubPackage(pkgName: string): Package | null {
    return this.subPackageMap.get(pkgName) ?? null
  }
}
