import {cloneDeep} from 'lodash'

export type TimingProps = {
  duration?: number
  offset?: number
  stagger?: number
  yoyo?: {repeat: number, delay: number}
  reverse?: boolean
}

export class Timing {
  duration: number | null
  offset: number | null
  stagger: number | null
  yoyo: {repeat: number, delay: number} | null
  reverse = false

  constructor({duration, offset, stagger, yoyo, reverse}: TimingProps) {
    this.duration = duration
    this.offset = offset
    this.stagger = stagger
    this.yoyo = yoyo
    this.reverse = reverse
  }

  rewriteBy(timing: Timing) {
    if (timing.duration !== null) {
      this.duration = timing.duration
    }
    if (timing.offset !== null) {
      this.offset = timing.offset
    }
    if (timing.stagger !== null) {
      this.stagger = timing.stagger
    }
    if (timing.yoyo !== null) {
      this.yoyo = timing.yoyo
    }
    if (timing.reverse !== null) {
      this.reverse = timing.reverse
    }
  }

  static build({duration, offset, stagger, yoyo, reverse}: TimingProps = {}): Timing {
    // set default value here
    return new Timing({
      duration: duration ?? null,
      offset: offset ?? null,
      stagger: stagger ?? null,
      yoyo: yoyo ?? null,
      reverse: reverse ?? false
    })
  }

  clone(): Timing {
    return new Timing({
      duration: this.duration,
      offset: this.offset,
      stagger: this.stagger,
      yoyo: this.yoyo ? cloneDeep(this.yoyo) : null,
      reverse: this.reverse
    })
  }
}
