<template>
  <div class="text-panel" id="text-panel" ref="textPanel"
    :style="{'height': panelHeight + 'px'}"
        @click="click">

    <div v-show="inEditMode"
      id="editBox" style="padding: 0 10px; position: relative;">
      <el-input
      type="textarea"
      placeholder="Enter your script here"
      :rows="5"
      v-model="textPanelText"
      autosize>
      </el-input>
    </div>

    <div v-show="!inEditMode"
      id="showBox" style="padding: 0 10px; position: relative;">

      <div class="timeStampWrapper" v-show="!audioRunning&&showToolbox" contenteditable="false"
        :style="{
                  'top': timeStampTop,
                  'left': timeStampLeft
                }">
        <span class="timeStamp">
          {{timeStamp}}
        </span>
      </div>

      <div class="word-container">
        <div v-for="(wordObj, wordIdx) in words" :key="'word-' + wordIdx" :ref="'word-' + wordIdx"
            :class="'narration-wrapper-'+wordIdx">

          <div class="narration-icon noSelection"
              v-for="(iconObj, iconIdx) in wordIdxIcons(wordIdx)" :key="'animationIcon-'+iconIdx"
                :id="'animationIcon-wrapper-'+wordIdx+'-'+iconIdx"
                style="display:contents;">
            <el-tooltip placement="top"
                        :content="iconObj.use" >
              <div class="animationIcon" :id="'animationIcon-'+wordIdx+'-'+iconIdx"
                    @click="clickIcon(iconObj, true)"
                    @mouseenter="mouseenterIcon(iconObj)"
                    @mouseleave="mouseleaveIcon(iconObj)">
                <font-awesome-icon id="animationIcon"
                    :icon="animationTypeToIcon(iconObj)"
                    :style="{
                      'background-color': animationTypeToColor(iconObj,'background'),
                      'color': animationTypeToColor(iconObj,'color'),
                      'border-color': animationTypeToColor(iconObj,'border'),
                    }"/>
              </div>
            </el-tooltip>

            <span class="narration-word noSelection">&nbsp;</span>

          </div>

          <div class="narration-icon noSelection"
              v-for="(iconObj, iconIdx) in keywordIcons(wordIdx)" :key="'keywordIcon-'+iconIdx"
              :id="'keywordIcon-wrapper-'+wordIdx"
              style="display:contents;">

              <div class="keywordIcon" :id="'keywordIcon-'+wordIdx+'-'+iconIdx"
                  @click="clickIcon(iconObj, false)"
                  @mouseenter="mouseenterIcon(iconObj)"
                  @mouseleave="mouseleaveIcon(iconObj)">
                <font-awesome-icon id="keywordIcon"
                    :icon="iconObj.icon"
                    :style="{
                      'background-color': keywordIconStyle(iconObj, 'background'),
                      'color': keywordIconStyle(iconObj, 'color'),
                      'border-color': keywordIconStyle(iconObj, 'border'),
                    }"/>
              </div>

              <span class="narration-word noSelection">&nbsp;</span>

          </div>

          <span class="narration-word noSelection"
                :id="'narration-word-'+wordIdx"
                :class="{
                    'highlight-word': !audioRunning&&highlightWordIndex===wordIdx,
                    'selected-word': inSelectedRange(wordIdx),
                    'audio-word': aniStatus !== 'stop' && audioWordIndex === wordIdx,
                    // 'bolden-word': isLinkedWord(wordIdx),
                    'underline-word': isLinkedWord(wordIdx),
                  }"
                @mousedown="mousedown($event, wordIdx)"
                @mouseenter="mouseenter(wordIdx)"
                @mouseleave="mouseleave(wordIdx)">{{wordObj.word}}</span>

          <!-- Keep this format as it is now, or there will be hidden spaces -->
          <span class="narration-word noSelection"
                :class="{
                    'selected-word': inSelectedRange(wordIdx) && wordIdx !== Math.max(selectedStart, selectedEnd),
                    'underline-word': isLinkedWord(wordIdx) && wordIdx !== Math.max(linkedStart, linkedEnd),
                  }"
                @mousedown="mousedown($event, wordIdx)">&nbsp;</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapState} from 'vuex';
import {SelectionToolType} from "@/utils/common/SeletionToolType";
import {FontAwesomeIcon} from '@fortawesome/vue-fontawesome';

export default {
    name: 'TextPanel',
    components: {
      FontAwesomeIcon
    },
    props: ['audioRunning','compile','panelHeight'],
    data() {
      return {
        highlightWordIndex: -1,
        audioWordIndex: -1,

        // don't confuse with textHasChanged
        // this means the text has changed while editing text
        textChanged: false,

        selectedStart: -1,
        selectedEnd: -1,
        isSelecting: false,

        linkedStart: -1,
        linkedEnd: -1,

        textPanelWords: [],

        showToolbox: false,
        isTargetButtonClicked: false,

        timeStamp: 0,
        timeStampTop: 0,
        timeStampLeft: 0,

        animationButtonsTop: 0,
        animationButtonsLeft: 0,

        animationTargets: null,
        justHoveredIcon: null,

        animationActions: [],

        isPreviewingIcon: false,
        isEditingIcon: false,
        iconHasChanged: false,

        isEditingText: false,
        // This is a bad name.
        // It means "selected target has changed while editing text"
        textHasChanged: false,

        changeHighlightItemInTextPanel: false,

        isCreatingItem: false,
      }
    },
    methods: {
      handleSelect (key, keyPath) {
        console.log(key, keyPath)
      },
      shortNotify(msg = 'Please commit your selection before continuing.') {
        this.$notify({
          title: msg,
          type: 'warning',
        });
      },
      inRange(wordIdx, bound1, bound2) {
        return Math.min(bound1, bound2) <= wordIdx
            && Math.max(bound1, bound2) >= wordIdx;
      },
      inSelectedRange(wordIdx) {
        return this.inRange(wordIdx, this.selectedStart, this.selectedEnd);
      },
      isLinkedWord(wordIdx) {
        return this.inRange(wordIdx, this.linkedStart, this.linkedEnd);
      },
      animationTypeToIcon(iconObj){
        const ani_type = this.useToAniMap[iconObj.use] ?? iconObj.ani_type ?? 'Other';
        return this.animationStyle[ani_type].icon;
      },
      animationTypeToColor(iconObj, type){
        const ani_type = this.useToAniMap[iconObj.use] ?? iconObj.ani_type ?? 'Other';
        if (type === "background")
          return (iconObj.originItem === this.highlightItem)
                ? this.animationStyle[ani_type].color
                : "white";
        else if (type === "color")
          return (iconObj.originItem === this.highlightItem)
                ? "white"
                : this.animationStyle[ani_type].color;
        else if (type === "border")
          return this.animationStyle[ani_type].color;
      },
      keywordIconStyle(iconObj, type) {
        if (type === 'background')
          return (iconObj.originItem === this.highlightItem)?
            'gray': 'white';
        else if (type === 'color')
          return (iconObj.originItem === this.highlightItem)?
            'white': 'gray';
        else if (type === "border")
          return 'gray';
      },
      click(event) {
        const target = event.target;
        if (target !== this.$refs.textPanel) {
          if (!target.id.includes("animationIcon") && !target.id.includes("keywordIcon")) {
            // Seems no use here
            this.keyup(event);
          }
          return;
        }

        const leave = (out, save) => {
          if (out) {
            if (save)
              this.$store.commit('editor/clearSelectionConfirm', true);
            else
              this.$store.commit('editor/clearSelectionLeave', true);

            this.$store.commit('editor/changeSelectionTool', SelectionToolType.NORMAL);
          }

          this.selectedStart = this.selectedEnd = -1;
        }

        if (this.isEditingText) {
          if (this.textHasChanged) {
            this.$confirm('Do you want to save your edit before leaving? (Close to cancel)', 'Warning', {
              confirmButtonText: 'Yes',
              cancelButtonText: 'No',
              type: 'warning',
              distinguishCancelAndClose: true,
            })
            .then(() => leave(true, true))
            .catch(action => {
              if (action === 'cancel')
                leave(true, false);
            });
          }
          else leave(true, false);
        }
        else leave(false);

      },
      changeTextContent(event){
        let textPanelText = "";
        this.textPanelWords = [];
        this.textPanelWords = event.target.innerText.split(/[\s\n]/).filter(_ => _);
        this.textPanelWords.forEach((word,idx)=>{
          if (!word.includes("s")||idx!==0){
            textPanelText += word+" ";
          }
        });
        textPanelText = textPanelText.slice(0,-1);
        if (this.textPanelText != textPanelText) {
          this.textPanelText = textPanelText;
          this.$emit('textChange');
        }
      },
      keyup(){
        if (this.isTargetButtonClicked) return;
      },
      handleKeyDelete(event) {
        event.stopPropagation();

        const textpanel = this.$refs.textPanel;
        const aniIcons = textpanel.getElementsByClassName('animationIcon');
        if (aniIcons.length !== this.animationActions.length) {
          const aniIconIds = Array.from(aniIcons).map(d => d.id);
          for (let index in this.animationActions) {
            const i = this.animationActions[index];
            const wordIconIdx = this.animationActions.filter(d => d.startIdx === i.startIdx).indexOf(i);
            const thisId = 'animationIcon-' + i.startIdx + "-" + wordIconIdx;
            if (!aniIconIds.find(id => id === thisId)) {
              this.animationActions.splice(index, 1);
              this.$store.commit('editor/deleteItemByIcon', i);
            }
          }
        }

        const keyIcons = textpanel.getElementsByClassName('keywordIcon');
        if (aniIcons.length !== this.wordTargetIconPos.length) {
          const keyIconIds = Array.from(keyIcons).map(d => d.id);
          for (let index in this.wordTargetIconPos) {
            const i = this.wordTargetIconPos[index];
            const wordIconIdx = this.wordTargetIconPos.filter(d => d.startIdx === i.startIdx).indexOf(i);
            const thisId = 'keywordIcon-' + i.startIdx + "-" + wordIconIdx;
            if (!keyIconIds.find(id => id === thisId)) {
              this.$store.commit('editor/deleteWordTarget', i);
            }
          }
        }
      },
      mousedown(event, wordIdx) {
        const work = () => {
          console.log('mousedown', wordIdx);

          this.selectedStart = this.selectedEnd = wordIdx;
          this.isSelecting = false;

          this.transcriptSrc.words.forEach(w => w.selected = false);

          document.onmousemove = () => {
            if (this.isTargetButtonClicked) return;

            this.isSelecting = true;
            if (this.highlightWordIndex !== -1) {
              this.selectedEnd = this.highlightWordIndex;
              const min = Math.min(this.selectedStart, this.selectedEnd),
                    max = Math.max(this.selectedStart, this.selectedEnd);
              for (let i = min; i <= max; i++) {
                this.transcriptSrc.words.selected = true;
              }
            }
          };
          document.onmouseup = () => {
            if (this.isTargetButtonClicked) return;

            console.log('select:', this.selectedStart, this.selectedEnd);
            this.isSelecting = false;
            document.onmousemove = null;
            document.onmouseup = null;

            this.clickTargetButton();
          };
        }

        if (this.isTargetButtonClicked) {
          if (!this.isEditingText) {
            this.shortNotify('Please commit your edit before continuing.');
            return;
          }
          const leave = (out, save) => {
            if (out) {
              if (save)
                this.$store.commit('editor/clearSelectionConfirm', true);
              else
                this.$store.commit('editor/clearSelectionLeave', true);
            }
            work();
          }

          if (this.textHasChanged) {
            this.$confirm('Do you want to save your edit before leaving? (Close to cancel)', 'Warning', {
              confirmButtonText: 'Yes',
              cancelButtonText: 'No',
              type: 'warning',
              distinguishCancelAndClose: true,
            })
            .then(() => leave(true, true))
            .catch(action => {
              if (action === 'cancel')
                leave(true, false);
            });
          }
          else leave(true, false);
        }
        else work();
      },
      mouseenter(index) {
        this.highlightWordIndex = index;
        const editingEle = this.$refs['word-' + index][0];
        const word = this.words[index];
        this.timeStamp = parseFloat(word.startTime).toFixed(2)+"s";
        this.computeTopLeft(editingEle, [-15,0]);
        this.showToolbox = true;
      },
      mouseleave() {
        this.highlightWordIndex = -1;
        this.showToolbox = false;
      },

      createNewItem() {
        const minWordIdx = Math.min(this.selectedStart, this.selectedEnd),
            maxWordIdx = Math.max(this.selectedStart, this.selectedEnd);
        const start = this.words[minWordIdx].startTime,
            end = this.words[maxWordIdx].endTime;
        const duration = Math.round((end - start) * 1000) / 1000;

        return {
          use: '',
          target: '',
          timing: {
            start: start,
            offset: 0,
            duration: duration,
          },
          ani_type: '',
        };
      },
      clickTargetButton(){
        this.isTargetButtonClicked = true;
        this.animationTargets = null;
        const beforeState = this.aniStatus;

        this.isEditingText = true;
        this.textHasChanged = false;

        this.$store.commit('editor/changeMode', 'animation');

        // this.isCreatingItem = true;
        // const newItem = this.createNewItem();
        // this.$store.commit('editor/changeItemForSettings', newItem);
        // this.$store.commit('editor/changeHighlightItem', newItem);

        this.$nextTick(() => {
          this.$store.commit('target/changeSelectedTargets', []);
          this.$store.commit('target/changeSvgSelectionChangeSign');
        });

        const clear = () => {
          this.selectedStart = this.selectedEnd = -1;
          this.isEditingText = false;
          this.$store.commit('editor/changeMode', 'normal');

          this.$store.commit('target/changeSelectedTargets', []);

          this.$store.commit('editor/changeTempAnimationSelection', {
            aniName: '',
            aniType: '',
          });

          this.$store.commit('editor/changeSettingPanelVisible', false);
        }

        this.$store.commit('editor/registerSelectionConfirm', () => {
          this.commitSelectedChange(
            Math.min(this.selectedStart, this.selectedEnd),
            Math.max(this.selectedStart, this.selectedEnd));
          clear();
        });

        this.$store.commit('editor/registerSelectionLeave', () => {
          clear();
          this.$store.commit('editor/changeAniStatus', beforeState);
        });
      },
      commitSelectedChange(min, max) {
        if (this.selectedTargets.length === 0) {
          this.shortNotify('No element selected!');
          return;
        }

        if (this.tempAnimationName && this.tempAnimationName !== '') {
          this.$store.commit('editor/appendItemByTextView',{
            minWordIdx: min,
            maxWordIdx: max,
            target: this.computeAniTarget(this.selectedTargets),
            toFitDuration: true,
            settings: this.aniSettings,
          });
          this.$emit('aniChange');
        } else {
          // this.$store.commit('editor/addWordTarget', {
          //   start: min,
          //   end: max,
          //   target: this.selectedTargets.map(d => d.id),
          // });

          this.shortNotify('No animation selected!');
          return;
        }

        this.selectedStart = this.selectedEnd = -1;
      },

      computeAniTarget(targets){
        var target = "";
        if (!targets) return null;
        targets.forEach(item=>{
          target += "#"+item.id +", "
        })
        target = target.slice(0, -2);
        return target;
      },


      clickIcon(iconObj) {
        if (this.audioRunning) return;
        if (this.selectionTool !== SelectionToolType.NORMAL) {
          if (this.isTargetButtonClicked && !this.isEditingIcon) { // editing other words
            this.shortNotify();
            return;
          }
          if (!this.isTargetButtonClicked && !this.isPreviewingIcon) { // editing something else
            this.shortNotify();
            return;
          }
        }

        const clear = () => {
          iconObj.clicked = false;
          iconObj.hovered = false;
          this.isEditingIcon = false;
          this.linkedStart = -1;
          this.linkedEnd = -1;
          this.$store.commit('editor/changeMode', 'normal');
          // this.$store.commit('editor/changeAnimationName', {
          //   aniName: '',
          //   aniType: '',
          //   aniItem: null,
          // });
          this.$store.commit('target/changeSelectedTargets', []);

          this.$store.commit('editor/changeHighlightItem', null);
          this.$store.commit('editor/changeItemForSettings', null);
        }

        const work = (fromOther, save) => {
          if (fromOther) {
            if (save)
              this.$store.commit('editor/clearSelectionConfirm', true);
            else
              this.$store.commit('editor/clearSelectionLeave', true);

            this.$store.commit('editor/changeSelectionTool', SelectionToolType.CLICK);
            this.$nextTick(() => {
              this.$store.commit('target/changeSelectedTargetsByIcon', iconObj);
              this.$store.commit('target/changeSvgSelectionChangeSign');
            });
          }

          this.$store.commit('editor/showToolBox');

          this.$store.commit('editor/changeHighlightItem', iconObj.originItem);
          this.$store.commit('editor/changeItemForSettings', iconObj.originItem);

          this.$store.commit('editor/registerSelectionConfirm', () => {
            // first, delete the icon
            // if (isAniIcon) {
            //   this.$store.commit('editor/deleteItemByIcon', iconObj);
            //   this.$emit('aniChange');
            // }
            // else
            //   this.$store.commit('editor/deleteWordTarget', iconObj);

            this.$store.commit('editor/updateItemByTextView', {
              item: iconObj.originItem,
              target: this.computeAniTarget(this.selectedTargets),
            })
            this.$emit('aniChange');

            this.selectedStart = this.selectedEnd = -1;
            clear();
          });

          this.$store.commit('editor/registerSelectionLeave', () => {
            clear();
          });

          // this.$store.commit('editor/changeAnimationName', {
          //   aniName: isAniIcon? iconObj.use: '',
          //   aniType: isAniIcon? iconObj.ani_type: '',
          //   aniItem: isAniIcon? iconObj.originItem: null,
          // });

          this.isTargetButtonClicked = true;
          this.isEditingIcon = true;
          this.isPreviewingIcon = false;
          this.iconHasChanged = false;
          iconObj.clicked = true;
          iconObj.hovered = true;
          this.linkedStart = iconObj.startIdx;
          this.linkedEnd = Math.max(iconObj.startIdx, iconObj.endIdx - 1);
          // use max to fix the bug  start might larger than end
          this.$store.commit('editor/changeMode', 'animation');
        }

        if (this.isEditingIcon) {
          if (this.iconHasChanged) {
            this.$confirm('Do you want to save your edit before editing another icon? (Close to cancel)', 'Warning', {
              confirmButtonText: 'Yes',
              cancelButtonText: 'No',
              type: 'warning',
              distinguishCancelAndClose: true,
            })
            .then(() => work(true, true))
            .catch(action => {
              if (action === 'cancel')
                work(true, false);
            });
          }
          else work(true, false);
        }
        else if (this.isPreviewingIcon) work(false, false);
      },


      mouseenterIcon(iconObj){
        this.changeHighlightItemInTextPanel = true;
        this.mouseenterIconHandler(iconObj);
      },
      mouseenterIconHandler(iconObj) {
        if (this.audioRunning || this.isPreviewingIcon || this.isEditingIcon || this.aniPlaying) return;
        if (this.selectionTool !== SelectionToolType.NORMAL) return;
        if (this.isSelecting) return;

        this.$store.commit('editor/changeHighlightItem', iconObj.originItem)

        iconObj.hovered = true;
        this.isPreviewingIcon = true;

        this.linkedStart = iconObj.startIdx;
        this.linkedEnd = Math.max(iconObj.startIdx, iconObj.endIdx - 1);
        // use max to fix the bug  start might larger than end

        this.$nextTick(() => {
          this.$store.commit('target/changeSelectedTargetsByIcon', iconObj);
          this.$store.commit('target/changeSvgSelectionChangeSign');
        });
      },

      mouseleaveIcon(iconObj) {
        this.changeHighlightItemInTextPanel = true;
        this.mouseleaveIconHandler(iconObj);
      },
      mouseleaveIconHandler(iconObj) {
        if (this.isPreviewingIcon) {
          this.isPreviewingIcon = false;
          this.$store.commit('editor/changeHighlightItem', null)
          this.$store.commit('editor/changeSelectionTool', SelectionToolType.NORMAL);
          iconObj.hovered = false;
          this.linkedStart = -1;
          this.linkedEnd = -1;
        }
      },
      computeTopLeft(element, timeStampOffset){
        const textPanel = document.getElementsByClassName("text-panel")[0];
        const textpanelPosition = textPanel.getBoundingClientRect();
        const wordPosition = element.getBoundingClientRect();

        this.timeStampTop = (wordPosition.top-textpanelPosition.top+textPanel.scrollTop+timeStampOffset[0])+"px";
        this.timeStampLeft = (wordPosition.left-textpanelPosition.left+timeStampOffset[1])+"px";

        // this.animationButtonsTop = (wordPosition.top-textpanelPosition.top+textPanel.scrollTop+animationButtonsOffset[0])+"px";
        // this.animationButtonsLeft = (wordPosition.left-textpanelPiosition.left+animationButtonsOffset[1])+"px";
      },
      computeDuration(startIdx,endIdx){
        const duration = Math.max(1, this.wordToEndTime(endIdx)-this.wordToStartTime(startIdx));
        return parseFloat(duration.toFixed(3));
      },
      startTimeToWord(time){
        for (let wordIdx in this.words){
          if (time<this.words[wordIdx].startTime){
            return parseInt(wordIdx)-1;
          }
        }
      },
      endTimeToWord(startIdx, endTime){
        for (let wordIdx=startIdx;wordIdx<this.words.length;wordIdx++){
          if (this.words[wordIdx].startTime>endTime) return wordIdx-1;
        }
        return this.words.length-1;
      },
      wordToStartTime(wordIdx){
        return this.words[wordIdx].startTime;
      },
      wordToEndTime(wordIdx){
        return this.words[wordIdx].endTime;
      },
      updateIconTiming(iconObj){
        iconObj.timing.start = this.wordToStartTime(iconObj.startIdx);
        iconObj.timing.duration = this.computeDuration(iconObj.startIdx, iconObj.endIdx);
        this.$store.commit('editor/updateItemTimingByIcon', iconObj);
      },
      createAnimationAction(ani_type, target, startTime, offset, startIdx, duration, use, originItem, selected=false){
        this.animationActions.push(
          {
            ani_type: ani_type,
            target: target,
            timing:{
              start: startTime,
              offset: offset,
              duration: duration,
            },
            startIdx: startIdx,
            endIdx: this.endTimeToWord(startIdx,duration + startTime + offset),
            clicked: selected,
            hovered: selected,
            use: use,
            originItem: originItem,
          }
        );
      },
      createAniActionsBytimelineRows(item, timing, selected=false){
        const duration = timing?.duration ?? 1;
        const startTime = (timing?.start ?? 0);
        const offset = timing?.offset ?? 0;
        const startIdx = this.startTimeToWord(startTime + offset);
        this.createAnimationAction(
          item.ani_type,
          item.target,
          startTime,
          offset,
          startIdx,
          duration,
          item.use,
          item,
          selected,
        );
      },
      initAniActions(){
        // console.log('>>>', this.animationActions, this.timelineRows)

        this.animationActions = [];
        this.timelineRows.flat().forEach((items)=>{
          if (items.sequence) {
            const sequence = items.sequence?.sync ?? items.sequence?.concat ?? [];
            sequence.forEach(item=>{
              this.createAniActionsBytimelineRows(item, items.timing);
            })
          } else {
            this.createAniActionsBytimelineRows(items, items.timing);
          }
        });

        if (this.highlightItem) {
          const newIcon = this.animationActions.find(o => o.originItem === this.highlightItem);
          if (newIcon) {
            newIcon.clicked = true;
            newIcon.hovered = true;
          }
        }

      },
      renewAniIconByItem(aniIcon, item) {
        return; // deprecated
        const duration = item.timing?.duration ?? 1;
        const startTime = item.timing?.start ?? 0;
        const offset = item.timing?.offset ?? 0;
        const startIdx = this.startTimeToWord(startTime + offset);
        const endIdx = this.endTimeToWord(startIdx, duration + startTime + offset);

        const updateObj = {
          ani_type: item.ani_type,
          target: item.target,
          timing:{
            start: startTime,
            offset: offset,
            duration: duration,
          },
          startIdx: startIdx,
          endIdx: endIdx,
          use: item.use,
          originItem: item,
        };

        Object.assign(aniIcon, updateObj);
      },
      handleOutsideResize() {
        this.$store.commit('editor/changeTlContainerWidth', 80);
      },
      isFadeColor(iconObj) {
        return this.highlightItem !== null && this.highlightItem !== iconObj.originItem;
      }
    },
    mounted() {
      this.$store.commit('editor/mountTextPanel', this.$el);
    },
    watch: {
      windowWidth() {
        this.handleOutsideResize();
      },
      leftPanelWidth() {
        this.handleOutsideResize();
      },
      targetPanelWidth() {
        this.handleOutsideResize();
      },
      selectionTool() {
        if (this.selectionTool === SelectionToolType.NORMAL)
          this.isTargetButtonClicked = false;
      },
      time(newV) {
        this.audioWordIndex = this.startTimeToWord(newV/1000);
        if (this.audioRunning) this.selectedStart = this.selectedEnd = -1;
      },
      timelineRows() {
        this.initAniActions();
      },
      selectedStart(){
        this.animationTargets = null;
        this.isTargetButtonClicked = false;
      },
      transcriptSrc(){
        this.audioWordIndex = -1;
      },
      tableSelectionChangeSign() {
        if (this.isEditingIcon)
          this.iconHasChanged = true;
        if (this.isEditingText)
          this.textHasChanged = true;
      },
      inEditMode() {
        // leave the mode
        if (!this.inEditMode) {
          if (this.textChanged)
            this.$store.commit('editor/updateAudio', this.textPanelText);
        }
        else {
          this.textChanged = false;
        }
      },
      textPanelText() {
        if (this.inEditMode) {
          this.textChanged = true;
        }
      },
      projectId() {
        this.selectedStart = this.selectedEnd = -1;
      },
      clearTextHighlightSign() {
        this.linkedStart = -1;
        this.linkedEnd = -1;
      },
      itemUpdateTuple() {
        if (this.itemUpdateTuple == null) {
          return;
        }
        const {item} = this.itemUpdateTuple;
        const aniIcon = this.animationActions.find(act => act.originItem === item);

        this.renewAniIconByItem(aniIcon, item);

        if (aniIcon.originItem === this.highlightItem) {
          this.linkedStart = aniIcon.startIdx;
          this.linkedEnd = Math.max(aniIcon.startIdx, aniIcon.endIdx - 1);
        }
      },
      highlightItem(newVal, oldVal) {
        if (this.changeHighlightItemInTextPanel) {
          this.changeHighlightItemInTextPanel = false;
          return;
        }
        if (newVal !== null) {
          const iconObj = this.animationActions.find(o => o.originItem === newVal)
          this.mouseenterIconHandler(iconObj);

        } else if (oldVal !== null) {
          const iconObj = this.animationActions.find(o => o.originItem === oldVal)
          this.mouseleaveIconHandler(iconObj);
        }
      },
      clickIconSignal() {
        const iconObj = this.animationActions.find(o => o.originItem === this.clickIconSignal);
        this.clickIcon(iconObj);
      }
    },
    computed: {
      ...mapState('editor', {
        time: state => state.time,
        timelineRows: state => state.timelineRows,
        tlContainerWidth: state => state.tlContainerWidth,

        transcriptSrc: state => state.transcriptSrc,
        wordTargetIconPos: state => state.wordTargetIconPos,
        animationStyle: state => state.animationStyle,

        selectionTool: state => state.selectionTool,

        windowWidth: state => state.windowWidth,
        leftPanelWidth: state => state.leftPanelWidth,
        targetPanelWidth: state => state.targetPanelWidth,

        aniStatus: state => state.aniStatus,

        tempAnimationName: state => state.animationName,
        useToAniMap: state => state.useToAniMap,

        aniSettings: state => state.aniSettings,
        currentMode: state => state.currentMode,

        itemForSettings: state => state.itemForSettings,
        clearTextHighlightSign: state => state.clearTextHighlightSign,
        itemUpdateTuple: state => state.itemUpdateTuple,

        highlightItem: state => state.highlightItem,
        clickIconSignal: state => state.clickIconSignal,
      }),
      ...mapState('target', {
        selectedTargets: state => state.selectedTargets,
        tableSelectionChangeSign: state => state.tableSelectionChangeSign
      }),
      ...mapState('resource', {
        projectId: state => state.projectId,
      }),
      ...mapGetters('editor', [
          'aniPlaying',
      ]),
      animationName() {
        return this.itemForSettings?.use ?? '';
      },
      textPanelText: {
        set(value) {
          this.$store.commit('editor/changetextPanelText', value);
        },
        get() {
          return this.$store.state.editor.textPanelText;
        }
      },
      words() {
        return this.transcriptSrc.words;
      },
      wordIdxIcons() {
        return (wordIdx)=>{
          return this.animationActions.filter(_=>_.startIdx===wordIdx);
        }
      },
      keywordIcons() {
        return (wordIdx) => {
          //if (this.animationActions.some(d => d.startIdx === wordIdx))
          //  return [];
          return this.wordTargetIconPos.filter(d => d.startIdx === wordIdx);
        }
      },
      inEditMode() {
        return this.currentMode === 'text';
      }
    },
  }
</script>

