import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {WipePropSchema} from "@/preset/template/common/WipeTemplate"
import {getRelativeBox} from "@/core/utils/SvgUtils"


export class WipeImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof WipePropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    const tl = gsap.timeline()
    const stagger: number = this.stagger
    this.elements.forEach((el, i) => {
      const tw = this.createTw(el)
      const position = i === 0 ? 0 : ('<' + stagger)
      tl.add(tw, position)
    })

    return tl
  }

  createTw(el: SVGElement): GsapAnimation {
    const {type, from, ease} = this.props()

    const {left, top, right, bottom, totalWidth, totalHeight} = getRelativeBox(el as SVGGraphicsElement)
    const [[ltx, lty], [rtx, rty], [lbx, lby], [rbx, rby]] = this.getOffsetFactorMatrix(from)
    const vars = this.mergeVars({ease})

    let tw: GsapAnimation

    const hiddenClipPath = `polygon(`
        + `${ltx * totalWidth + left}px ${lty * totalHeight + top}px, `
        + `${rtx * totalWidth + right}px ${rty * totalHeight + top}px, `
        + `${rbx * totalWidth + right}px ${rby * totalHeight + bottom}px, `
        + `${lbx * totalWidth + left}px ${lby * totalHeight + bottom}px)`
    const shownClipPath = `polygon(${left}px ${top}px, ${right}px ${top}px, ${right}px ${bottom}px, ${left}px ${bottom}px)`

    switch (type) {
      case 'in':
        tw = gsap.fromTo(el, { clipPath: hiddenClipPath }, { clipPath: shownClipPath, ...vars })
        break
      case 'out':
        tw = gsap.fromTo(el, { clipPath: shownClipPath }, { clipPath: hiddenClipPath, ...vars })
        break
      default:
        // never goes here as type is checked by zod
        throw new Error(`Unknown type: ${type}`)
    }

    return tw
  }


  /**
   * @param from top, bottom, left, right
   * @return widthFactor, heightFactor for each point (-1, 0, 1)
   */
  getOffsetFactorMatrix(from: string): number[][] {
    switch (from) {
      case 'top':
        return [
          [0, 0], [0, 0],     // left top, right top
          [0, -1], [0, -1],   // left bottom, right bottom
        ]
      case 'bottom':
        return [
          [0, 1], [0, 1],
          [0, 0], [0, 0],
        ]
      case 'left':
        return [
          [0, 0], [-1, 0],
          [0, 0], [-1, 0],
        ]
      case 'right':
        return [
          [1, 0], [0, 0],
          [1, 0], [0, 0],
        ]
      default:
        throw new Error(`Wrong value of from: ${from}. Must be 'top', 'bottom', 'left' or 'right'. At ${this}`)
    }
  }
}

