import {SynthesisRule} from "@/core/exec/synthesis/SynthesisRule"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {PhysicalAnimation} from "@/core/ast/animation/physical/PhysicalAnimation"
import {BaseAniPropSchema, LogicalAnimation} from "@/core/ast/animation/logical/LogicalAnimation"

interface PhysicalAnimationConstructor<ObjType, LogicalAniType extends LogicalAnimation<BaseAniPropSchema>, AniType> {
  new(logical: LogicalAniType): PhysicalAnimation<ObjType, LogicalAniType, AniType>
}

/**
 * Rule that mapping logical animation to physical animation
 */
export class MappingRule<ObjType, LogicalAniType extends LogicalAnimation<BaseAniPropSchema>, AniType>
  extends SynthesisRule<ObjType, LogicalAniType, AniType> {

  readonly physicalAnimationClass: PhysicalAnimationConstructor<ObjType, LogicalAniType, AniType>

  constructor(targetName: string,
              physicalAnimationClass: PhysicalAnimationConstructor<ObjType, LogicalAniType, AniType>) {
    super("MappingRule", targetName)
    this.physicalAnimationClass = physicalAnimationClass
  }

  apply(cxt: SynthesisContext<ObjType, AniType>, input: LogicalAniType): PhysicalAnimation<ObjType, LogicalAniType, AniType> | null {
    return new this.physicalAnimationClass(input)
  }
}
