import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {CustomPropSchema} from "@/preset/template/common/CustomTemplate"


export class CustomImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof CustomPropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // target
    const elements = this.elements
    // timing
    const stagger = this.stagger

    const tl = gsap.timeline()
    elements.forEach((el, i) => {
      tl.add(this.createTw(el, cxt), i === 0 ? 0 : ('<' + stagger))
    })
    return tl
  }

  createTw(el: SVGElement, cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // effect
    const {attr, from, to, ease} = this.props()

    let type
    if (from != null && to != null) {
      type = 'fromTo'
    } else if (from != null) {
      type = 'from'
    } else if (to != null) {
      type = 'to'
    } else {
      throw new Error('CustomImpl: Attribute "from" or "to" must be specified at least one.')
    }

    let tw: GsapAnimation

    const vars = this.mergeVars({ease})
    switch (type) {
      case 'fromTo':
        tw = gsap.fromTo(el, {[attr]: from}, {...vars, [attr]: to})
        break
      case 'from':
        tw = gsap.from(el, {...vars, [attr]: from})
        break
      case 'to':
        tw = gsap.to(el, {...vars, [attr]: to})
        break
      default:
        // never goes here as type is checked
        throw new Error(`Unknown type: ${type}`)
    }

    return tw
  }
}

